<template>
    <b-popover :target="`${id}${item.name}`" triggers="click" :show.sync="show">
        <template slot="title">
            修改{{item.name}}: 
            <button type="button" aria-label="Close" class="close" style="line-height: 0.7" @click="show = false">×</button>
        </template>
        <form>
            <b-input-group>
                <b-form-select v-if="item.type === 'boolean'" :options="bolOptions" v-model="value"></b-form-select>
                <b-form-input v-if="item.type === 'number'" type="number" v-model="value" @keyup.native.enter="edit"></b-form-input>
                <b-form-input v-if="item.type === 'string'" type="text" v-model="value" @keyup.native.enter="edit"></b-form-input>
            </b-input-group>
            <b-row class="mt-2 text-right">
                <b-col>
                    <b-btn @click="edit" size="sm" variant="primary">更改</b-btn>
                </b-col>
            </b-row>
        </form>
    </b-popover>
</template>

<script>
export default {
    props: ['id', 'item', 'fresh'],
    created() {
        this.value = this.item.value;
    },
    data() {
        return {
            bolOptions: [ 'true', 'false' ],
            show: false,
            value: ''
        }
    },
    methods: {
        edit() {
            if (!this.value) {
                this.$api.showModalInfo('ERROR', `資料不得為空`);
                return;
            }
            let body = {
                group_id: this.id,
                property: this.item.name,
                value: this.value
            };
            this.$api.showModalInfo('SUCCESS', '更改成功');
            this.show = false;
            // this.$api.requestFetch(`/member_group/edit`, "POST", body)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '更改成功');
            //             this.fresh();
            //             this.show = false;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '更改失敗' + `(${res.msg || res.message})`);
            //         }
            //     });
        }
    }
}
</script>

<style>

</style>



