<template>
<div id="server_config">
    <b-row class="card-header m-0">
        <b-col md="2">
            <span class="font-weight-bold" style="font-size: 30px">伺服器設定</span>
        </b-col>
        <b-col md="3">
            <b-input-group class="mt-1">
                <b-input-group-prepend is-text>
                    <i class="material-icons">search</i>
                </b-input-group-prepend>
                <b-form-input v-model="filterWord" type="text"></b-form-input>
                <b-input-group-append>
                    <b-btn :disabled="!filterWord" @click="filterWord = ''">Clear</b-btn>
                </b-input-group-append>
            </b-input-group>
        </b-col>
        <b-col offset-md="4" md="3" class="text-right">
            <b-btn class="material-icons" style="font-size: 24px" @click="showAddModal('')">add_box</b-btn>
        </b-col>
    </b-row>
    <div v-for="(server, key) in items" :key="key" class="border" v-if="filter(server)">
        <b-row class="card-header m-0">
            <b-col md="9">
                <span class="font-weight-bold" style="font-size: 24px">
                    <b-badge pill :variant="typeVariant(server.type)" style="font-size: 100%">{{ server.type | serverType }}</b-badge> {{ server.server }}
                </span>
            </b-col>
            <b-col md="3" class="text-right">
                <b-btn class="material-icons" @click="showAddModal(server.server, server.type)">add_box</b-btn>
                <b-btn class="material-icons" @click="refreshOne(server.server)">autorenew</b-btn>
            </b-col>
        </b-row>
        <b-row class="card-body pt-3 m-0">
            <b-col md="4" v-for="(currency, key) in server.list" :key="key">
                <b-card-header
                    :header="currency.currency"
                    header-bg-variant="dark"
                    header-text-variant="white"
                ></b-card-header>
                <b-card-body class="border mb-2 p-2">
                    <input-filed
                        v-for="(config_key, key) in Object.keys(currency.config)" 
                        :key="key"
                        :server="server.server"
                        :name="config_key"
                        :currency="currency.currency"
                        :value="currency.config[config_key].value"
                        :update="currency.config[config_key].update"
                        v-on:refreshOne="refreshOne($event)"
                    ></input-filed>
                </b-card-body>
            </b-col>
        </b-row>
    </div>
    <modal-add
        :targetServer="targetServer"
        :targetType="targetType"
        v-on:refresh="refresh"
    ></modal-add>
</div>
</template>

<script>
import InputFiled from './input';
import ModalAdd from './modal_add';

export default {
    name: "server_config",
    components: {
        InputFiled,
        ModalAdd
    },
    data() {
        return {
            items: [],
            server: '',
            type: '',
            filterWord: '',
        }
    },
    created: function () {
        this.refresh();
    },
    computed: {
        targetServer() {
            return this.server;
        },
        targetType() {
            return this.type;
        },
    },
    methods: {
        filter(server) {
            if (server.server.indexOf(this.filterWord) > -1) {
                return true;
            }
            return false;
        },
        refresh() {
            this.items = [
                {
                    type: '1',
                    server: 'server',
                    list: [
                        {
                            currency: 'TWD',
                            config: {
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                },
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                }
                            }
                        },
                        {
                            currency: 'RMB',
                            config: {
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                },
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                }
                            }
                        },
                        {
                            currency: 'JPY',
                            config: {
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                },
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                }
                            }
                        },
                        {
                            currency: 'QQQ',
                            config: {
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                },
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                }
                            }
                        },
                    ]
                },
                {
                    type: '2',
                    server: 'server2',
                    list: [
                        {
                            currency: 'TWD',
                            config: {
                                aaa: {
                                    value: 'aaaaa',
                                    update: '2088.09.09'
                                }
                            }
                        }
                    ]
                },
            ];
            // this.$api.requestFetch(`/server_config`, "GET")   
            //     .catch(err => alert(err)) 
            //     .then(res => this.items = res.data);
        },
        refreshOne(server) {
            // this.$api.requestFetch(`/server_config?server=${server}`, "GET", "", false)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         for (let i = 0, len = this.items.length; i < len; i++) {
            //             if (this.items[i].server === server) {
            //                 this.items.splice(i, 1, res.data[0]);
            //             }
            //         }
            //     });
        },
        showAddModal(server, type) {
            this.server = server;
            this.type   = type;
            this.$modal.show('modalAdd');
        },
        typeVariant(val) {
            switch(val) {
                case '1':
                    return 'primary';
                case '2':
                    return 'success';
                default: 
                    return 'danger';
            }
        }
    }
}
</script>

<style lang="scss">

</style>



