import Vue from 'vue';
import VueRx from 'vue-rx';
import App from './App.vue';
import filters from './filters';
import router from './router';
import store from './store';
import { ws } from './lib/ws';
import { api } from './lib/api';
import VModal from 'vue-js-modal';
import Notifications from 'vue-notification';
import ToggleButton from 'vue-js-toggle-button';
import ViserVue from 'viser-vue';
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import DataSet from '@antv/data-set';

Vue.use(DataSet);
Vue.use(ViserVue);
Vue.use(VueRx);
Vue.use(BootstrapVue);
Vue.use(VModal, { dynamic: true });
Vue.use(Notifications);
Vue.use(ToggleButton);
ws.init(store, Vue.prototype.$notify);
api.init(store, router, Vue.prototype.$modal);
Object.defineProperty(Vue.prototype, '$ws', { value: ws });
Object.defineProperty(Vue.prototype, '$api', { value: api });

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => {
        return record.meta.requiresAuth;
    })) {
        let key = sessionStorage.getItem('sessionKey');
        api.getLevel()
            .then(e => {
                if(!api.checkLevel(to.path)) {
                    api.logOut();
                }
            }
        )
        if(store.state.isLogin) {
            next();
            return;
        }
        if (!key) {
            next({ path: '/login' });
            return;
        }
        if (sessionStorage.getItem('level') >= 900) {
            ws.login();
            api.getLog();
        }
        api.reConnect()
            .then(e => next());
    } else {
        if (to.path === '/login') {
            api.logOut();
        }
        next();
    }
})

new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
})



