<template>
    <div id="player-table">
        <b-card-header>
            <b-row align-v="center">
                <b-col md="4">
                    <h4>{{ name }}遊戲資訊</h4>
                    <span>({{ data.date }})</span>
                </b-col>
                <b-col md="6">
                    <date-time
                        :noQuick="true"
                        v-on:startDate="startDate=$event"
                        v-on:startTime="startTime=$event"
                        v-on:endDate="endDate=$event"
                        v-on:endTime="endTime=$event"
                    ></date-time>
                </b-col>
                <b-col md="2">
                    <b-btn variant="primary" @click="request">查詢</b-btn>
                    <b-btn variant="success" @click="requestToNow">至今查詢</b-btn>
                </b-col>
            </b-row>
        </b-card-header>
        <b-card-body>
            <b-table 
                show-empty 
                bordered 
                :items="data.data" 
                :fields="fields" 
                :sort-by.sync="sort" 
                :sort-desc.sync="sortDesc"
                empty-text="無資料"
            >
                <template slot="gid" slot-scope="row">{{ row.value | toGidName }}</template>
                <template slot="rounds" slot-scope="row">{{ row.value }}</template>
                <template slot="max_bet" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
                <template slot="max_win" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
                <template slot="avg_bet" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
                <template slot="avg_win" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
                <template slot="sum_bet" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
                <template slot="sum_win" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
                <template slot="total" slot-scope="row">
                    <span :class="totalColor(row.value)">
                        {{ row.value | formatMoney(currency) }}
                    </span>
                </template>
            </b-table>
        </b-card-body>
    </div>
</template>

<script>
import DateTime from './date_time'
import { createNamespacedHelpers } from 'vuex';
const { mapGetters, mapActions } = createNamespacedHelpers('member_report');

export default {
    props: [ 'name', 'currency' ],
    components: {
        DateTime
    },
    created() {

    },
    computed: {
        ...mapGetters({
            data: "getPlayerGameData",
        })
    },
    data() {
        return {
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            fields: [
                {
                    key: "gid",
                    label: "遊戲名稱",
                    sortable: true,
                },
                {
                    key: "rounds",
                    label: "局數",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "max_bet",
                    label: "最大注額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "max_win",
                    label: "最大贏得",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "avg_bet",
                    label: "平均注額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "avg_win",
                    label: "平均贏得",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "sum_bet",
                    label: "總注額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "sum_win",
                    label: "總贏得",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "total",
                    label: "總輸贏",
                    sortable: true,
                    class: 'text-right'
                }
            ],
            sort: 'total',
            sortDesc: true
        }
    },
    methods: {
        request() {
            // let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            // let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            // this.$api.getPlayerGame(this.data.mid, totalStart, totalend)
            //     .then()
            //     .catch(e => this.$api.showModalInfo('Error', '查詢失敗!'));
        },
        requestToNow() {
            // this.$api.getPlayerGame(this.data.mid, '', '')
            //     .then()
            //     .catch(e => this.$api.showModalInfo('Error', '查詢失敗!'));
        },
        totalColor(val) {
            if (val <= 0) {
                return 'text-danger';
            } else {
                return 'text-success';
            }
        }
    }
}
</script>

<style>
#player-table{
    min-height: 400px
}
</style>



