<template>
    <b-card no-body>
        <b-row align-v="center" class="card-header m-0">
            <b-col md="2">
                <span class="font-weight-bold" style="font-size: 30px">盈虧報表</span>
            </b-col>
            <b-col md="9">
                <date-time
                    v-on:startDate="startDate=$event"
                    v-on:startTime="startTime=$event"
                    v-on:endDate="endDate=$event"
                    v-on:endTime="endTime=$event"
                ></date-time>
            </b-col>
            <b-col md="1">
                <b-btn variant="primary" @click="request">查詢</b-btn>
            </b-col>
        </b-row>
        <b-breadcrumb>
            <b-breadcrumb-item 
                v-for="(item, key) in bread" :key="key" 
                v-show="item.show"
                :text="item.text"
                :active="item.active"
                @click="activeBread(key)"
            />
        </b-breadcrumb>
        <b-card-body>
            <list-total 
                :items="itemsProp"
                v-on:currency="requestCurrency($event)"
                v-if="bread.time.active"
            ></list-total>
            <list-game 
                v-if="bread.currency.active"
                v-on:showPlayersModal="showPlayersModal($event.title, $event.content)"
                v-on:showRoundsModal="showRoundsModal($event.title, $event.content, $event.sendData)"
            ></list-game>
            <list-game-players 
                :items="playersItems" 
                v-on:getPlayerInfo="showPlayerInfo" 
                v-if="bread.players.active"
            ></list-game-players>
            <list-game-rounds 
                :items="roundsItems" 
                v-on:getPlayerInfo="showPlayerInfo" 
                v-if="bread.rounds.active"
            ></list-game-rounds>
            <pagination-fast
                v-if="bread.rounds.active"
                :totalRows="totalRows"
                :rows="per_page"
                :currentPage="currentPage"
                v-on:changePage="requestPage"
            ></pagination-fast>
            <b-pagination 
                v-model="currentPage" 
                align="right" 
                :total-rows="totalRows" 
                :per-page="per_page" 
                :limit="10" 
                @change="requestPage" 
                v-if="bread.rounds.active"
            />
            <player-info v-if="bread.playerInfo.active"></player-info>
        </b-card-body>
    </b-card>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import DateTime from '../date_time'
import ListTotal from './list_total';
import ListGame from '../game_report/list';
import ListGamePlayers from '../game_report/players_table';
import ListGameRounds from '../game_report/rounds_table';
import playerInfo from '../player_info';
import paginationFast from '../pagination_fast';

const { mapGetters, mapActions } = createNamespacedHelpers('game_report');

export default {
    name: "gameReport",
    components: {
        DateTime,
        ListTotal,
        ListGame,
        ListGamePlayers,
        ListGameRounds,
        playerInfo,
        paginationFast
    },
    created() {

    },
    beforeDestroy() {
        this.clear();
    },
    data() {
        return {
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            sendStartTime: '',
            sendEndTime: '',
            bread: {
                time: {
                    text: '',
                    level: 0,
                    active: false,
                    show: true
                },
                currency: {
                    text: '',
                    level: 1,
                    active: false,
                    show: false
                },
                players: {
                    text: '',
                    level: 2,
                    active: false,
                    show: false
                },
                rounds: {
                    text: '',
                    level: 2,
                    active: false,
                    show: false
                },
                playerInfo: {
                    text: '',
                    level: 3,
                    active: false,
                    show: false
                }
            },
            modalInfo: {
                title: "",
                content: []
            },
            sendData: '',
            totalRows: 0,
            per_page: 25,
            currentPage: 1,
            items: [],
        }
    },
    computed: {
        itemsProp() {
            return this.items;
        },
        playersItems() {
            return this.modalInfo.content;
        },
        roundsItems() {
            return this.modalInfo.content;
        },
    },
    methods: {
        request() {
            this.sendStartTime = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            this.sendEndTime   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            let body = {
                start_time: this.sendStartTime,
                end_time: this.sendEndTime
            };
            this.bread.time.text = `總表(${body.start_time}~${body.end_time})`;
            this.activeBread('time');
            this.hideBread('time');
            let data = [
                {
                    currency: 'TWD',
                    bet: 100000,
                    win: 200000,
                },
                {
                    currency: 'RMB',
                    bet: 200000,
                    win: 100000,
                },
            ];
            this.addResult(data);
            this.items = data;
            // this.$api.requestFetch(`/report/profit_loss`, "POST", body)
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.addResult(res.data);
            //             this.items = res.data;
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        requestCurrency(currency) {
            let body = {
                gid: [],
                start_time: this.sendStartTime,
                end_time: this.sendEndTime,
                currency: currency
            };
            let data = [
                {
                    gid: 2202,
                    rounds: 50,
                    players: 3,
                    bet: 5000,
                    win: 1000,
                    result: -4000
                },
                {
                    gid: 2203,
                    rounds: 20,
                    players: 1,
                    bet: 2000,
                    win: 1000,
                    result: -1000
                },
                {
                    gid: 2204,
                    rounds: 3000,
                    players: 30,
                    bet: 1000,
                    win: 1000,
                    result: 0
                },
            ];
            if (data.length === 0) {
                this.$api.showModalInfo('Success', `無資料`);
            }
            this.bread.currency.text = currency;
            this.activeBread('currency');
            this.hideBread('currency');
            this.setItems(data);
            this.setCurrency(currency);
            this.setStartTime(this.sendStartTime);
            this.setEndTime(this.sendEndTime);
            // this.$api.requestFetch(`/report/game`, "POST", body)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             if (res.data.length === 0) {
            //                 this.$api.showModalInfo('Success', `無資料`);
            //             }
            //             this.bread.currency.text = currency;
            //             this.activeBread('currency');
            //             this.hideBread('currency');
            //             this.setItems(res.data);
            //             this.setCurrency(currency);
            //             this.setStartTime(this.sendStartTime);
            //             this.setEndTime(this.sendEndTime);
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        requestPage(page) {
            this.sendData.page = page - 1;
            this.currentPage = page;
            // this.$api.requestFetch(`/report/game/rounds`, "POST", this.sendData)
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.addResult(res.data.list);
            //             this.modalInfo.content = res.data.list;
            //             document.documentElement.scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        addResult(list) {
            for (let i = 0, len = list.length; i < len; i++) {
                list[i].result = list[i].win - list[i].bet;
            }
        },
        showPlayersModal(title, content) {
            this.modalInfo.title = title;
            this.modalInfo.content = content;
            this.bread.players.text = title;
            this.activeBread('players');
            this.hideBread('players');
        },
        showRoundsModal(title, content, sendData) {
            this.addResult(content.list)
            this.sendData  = sendData;
            this.currentPage = 1;
            this.totalRows = content.total;
            this.modalInfo.title = title;
            this.modalInfo.content = content.list;
            this.bread.rounds.text = title;
            this.activeBread('rounds');
            this.hideBread('rounds');
        },
        showPlayerInfo() {
            this.bread.playerInfo.text = '玩家資訊';
            this.activeBread('playerInfo');
            this.hideBread('playerInfo');
        },
        activeBread(key) {
            let breadKeys   = Object.keys(this.bread);
            for (let i = 0, len = breadKeys.length; i < len; i++) {
                this.bread[breadKeys[i]].active = false;
            }
            this.bread[key].active = true;
        },
        hideBread(key) {
            let breadKeys   = Object.keys(this.bread);
            let targetLevel = this.bread[key].level;
            for (let i = 0, len = breadKeys.length; i < len; i++) {
                let temp  = this.bread[breadKeys[i]];
                temp.show = temp.level >= targetLevel ? false : temp.show; //隱藏比目標level同等或大者，代表在目標之後的麵包屑
            }
            this.bread[key].show = true;
        },
        ...mapActions(["setItems", "setCurrency", "setStartTime", "setEndTime", "clear"])
    }
}
</script>


