<template>
<div id="server-log">
    <b-navbar id="dragger" toggleable="md" type="dark" variant="info" sticky>
        <b-nav-form class="filter">
            <i class="material-icons">&#xE8B6;</i>Filter: 
            <b-input-group prepend="agent" class="pr-1">
                <b-form-select :options="agent" v-model="filterAgent">
                    <template slot="first">
                        <option :value="null" disabled>-- 選擇篩選對象 --</option>
                    </template>
                </b-form-select>
            </b-input-group>
            <b-input-group prepend="proc" class="pr-1">
                <b-form-input v-model.trim="filterProc" placeholder="Type to Search" />
            </b-input-group>
            <b-input-group prepend="type">
                <b-form-select :options="filterTypeOptions" v-model="filterType">
                    <template slot="first">
                        <option :value="null" disabled>-- 選擇篩選對象 --</option>
                    </template>
                </b-form-select>
            </b-input-group>
            <b-btn :disabled="!filterType&&!filterProc&&!filterAgent" @click="clearFilter">Clear</b-btn>
        </b-nav-form>
        <b-navbar-nav class="ml-auto">
            <b-btn class="material-icons" @click="$emit('closeLog')">clear</b-btn>
        </b-navbar-nav>
    </b-navbar>
    <div class="sticky-top to-top-bar" v-if="updateLock" @click="$el.scrollTop = 0">Back To Top</div>
    <div class="h-100 text-center bg-white">
        <div v-for="(item, key) in localLogs.slice(0, logLimit)" :key="key" class="mb-0 border-bottom" :class="variantMap(item.type)">
            <b-row class="m-0">
                <b-col md="2" class="text-left">
                    [{{item.agent}} - {{item.name}}] :
                </b-col>
                <b-col md="9" class="text-left word-break">
                    {{item.msg}}
                </b-col>
                <b-col md="1" class="text-right">
                    <p class="mb-0">{{item.time | formatTime}}</p>
                </b-col>
            </b-row>
        </div>
        <h1 v-if="localLogs.length === 0">Search not found</h1>
    </div>
</div>
</template>

<script>
import { fromEvent, from } from 'rxjs'
import { filter, takeUntil, concatMap, toArray } from 'rxjs/operators'
import { mapGetters, mapActions } from "vuex";

export default {
    watch: {
        logs: function(val) {
            if (this.updateLock) {
                return;
            }
            this.setLogs();
        },
        filterAgent: function() {
            this.setLogs();
        },
        filterProc: function() {
            this.setLogs();
        },
        filterType: function() {
            this.setLogs();
        },
    },
    data() {
        return {
            localLogs: [],
            logLimit: 20,
            updateLock: false,
            filterAgent: null,
            filterProc: null,
            filterType: null,
            filterTarget: null,
            filterTypeOptions: [
                {
                    text: "process:msg",
                    value: "process:msg"
                },
                {
                    text: "process:alert",
                    value: "process:alert"
                },
                {
                    text: "log:out",
                    value: "log:out"
                },
                {
                    text: "log:err",
                    value: "log:err"
                }
            ],
        };
    },
    computed: {
        ...mapGetters({
            agent: "getAgent",
            logs: "getLogs"
        })
    },
    mounted () {
        this.drag();
        this.scroll();
        this.$api.getAgent();
        this.localLogs = this.logs.slice(0);
        let level = Number(sessionStorage.getItem('level'));
        if (level < 900) {
            this.$emit('closeLog');
        }
    },
    methods: {
        drag() {
            let dragger = document.getElementById('dragger');
            let mouseDown = fromEvent(dragger, 'mousedown');
            let mouseUp = fromEvent(document, 'mouseup');
            let mouseMove = fromEvent(document, 'mousemove');
            let innerHeight = window.innerHeight;
            this.$subscribeTo(
                mouseDown.pipe(
                    filter(e => e.target.id === 'dragger'),
                    concatMap(e => mouseMove.pipe(takeUntil(mouseUp)), (e, res) => res.clientY),
                    filter(e => e < innerHeight * 0.8 && e > innerHeight * 0.2)
                ),e => this.$el.style.height = `calc(${100 - e / innerHeight * 100}% + 20px)`);
        },
        scroll() {
            let scroll = fromEvent(this.$el, 'scroll');
            this.$subscribeTo(
                scroll.pipe(
                    filter(e => {
                        if (e.target.scrollTop !== 0) {
                            this.updateLock = true;
                        } else {
                            this.updateLock = false;
                            this.setLogs();
                        }
                        return e.target.scrollHeight - e.target.clientHeight === e.target.scrollTop;
                    })
                ), e => this.logLimit += 10);              
        },
        setLogs() {
            this.$subscribeTo(
                from(this.logs).pipe(
                    filter(e => e.agent === this.filterAgent || this.filterAgent === null),
                    filter(e => e.name.indexOf(this.filterProc, 0) !== -1 || this.filterProc === null),
                    filter(e => e.type === this.filterType || this.filterType === null),
                    toArray()
                ), e => this.localLogs = e.slice(0));
        },
        clearFilter() {
            this.filterAgent = null;
            this.filterProc = null;
            this.filterType = null;
        },
        variantMap(type) {
            switch (type) {
                case "process:msg":
                    return "alert-primary";
                case "process:alert":
                    return "alert-warning";
                case "log:out":
                    return "alert-light";
                case "log:err":
                    return "alert-danger";
                default:
                    return "alert-white";
            }
        }
    }
};
</script>

<style lang="scss">
#server-log {
    height: calc(50% - 20px);
    width: calc(100%);
    z-index: 2;
    overflow-y: auto;
    position: fixed;
    bottom: 0;
    #dragger {
        cursor: all-scroll;
    }
    .to-top-bar {
        top: 3.5rem;
        text-align: center;
        background-color: #00000024;
        cursor: pointer;
    }
    .filter {
        font-size: 25px;
    }
    .word-break {
        word-break: break-all;
    }
}
</style>



