<template>
    <div>
        <div class="card-header">
            <b-row align-v="center" class="m-0">
                <b-col md="2">
                    <span class="font-weight-bold" style="font-size: 30px">玩家管理</span>
                </b-col>
                <b-col md="4" class="text-right">
                    <b-input-group class="mt-1">
                        <b-input-group-prepend is-text>
                            玩家關鍵字
                        </b-input-group-prepend>
                        <b-form-input v-model="keyWord" @keyup.native.enter="keyWordSearch"></b-form-input>
                        <b-input-group-prepend is-text>
                            玩家分組
                        </b-input-group-prepend>
                        <b-form-input v-model="groupId" @keyup.native.enter="keyWordSearch"></b-form-input>
                        <b-input-group-append>
                            <b-btn variant="primary" @click="keyWordSearch">查詢</b-btn>
                        </b-input-group-append>
                    </b-input-group>
                </b-col>
            </b-row>
        </div>
        <div class="m-2">
            <b-table
                hover
                show-empty 
                :items="memberList" 
                :fields="fields" 
                empty-text="無資料"
            >
                <template slot="name" slot-scope="row">
                    <div @click="getPlayerInfo(row.item.id, row.value)">
                        {{ row.value }}
                    </div>
                </template>
                <template slot="group_id" slot-scope="row">
                    <div @click="getGroup(row.value)">{{ row.value }}</div>
                </template>
                <template slot="gid" slot-scope="row">{{ row.value | toGidName }}</template>
                <template slot="ip" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="start_time" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="addtime" slot-scope="row">{{ row.value }}</template>
                <template slot="action" slot-scope="row">
                    <b-btn @click="showDetailModal(row)">遊玩記錄</b-btn>
                </template>
            </b-table>
            <pagination-fast
                :totalRows="totalRows"
                :rows="rows"
                :currentPage="currentPage"
                v-on:changePage="getMemberList"
            ></pagination-fast>
            <b-pagination :total-rows="totalRows" :per-page="rows" v-model="currentPage" align="right" :limit="10" @change="getMemberList"/>       
        </div>
    </div>
</template>

<script>
import DetailModal from './detail_modal';
import groupModal from './group_modal'
import playerInfo from '../player_info';
import paginationFast from '../pagination_fast';

export default {
    created() {
        this.getMemberList(1);
    },
    components: {
        paginationFast
    },
    data() {
        return {
            memberList: [],
            totalRows: 0,
            rows: 25,
            currentPage: 1,
            keyWord: '',
            groupId: '',
            lastKeyWord: '',
            lastGroupId: '',
            fields: [
                {
                    key: "name",
                    label: "玩家名稱",
                    class: 'text-center clickable'
                },
                {
                    key: "group_id",
                    label: "分組ID",
                    class: 'text-center clickable'
                },
                {
                    key: "gid",
                    label: "最新遊玩",
                    class: 'text-center'
                },
                {
                    key: "ip",
                    label: "最新IP位置",
                    class: 'text-center'
                },
                {
                    key: "start_time",
                    label: "最新遊玩時間",
                    class: 'text-center'
                },
                {
                    key: "addtime",
                    label: "加入時間",
                    class: 'text-center'
                },
                {
                    key: "action",
                    label: "選項",
                    class: 'text-center'
                }
            ]
        }
    },
    methods: {
        getGroup(id) {
            let data = [
                {
                    property: 'rtp',
                    value: 0.877
                }
            ];
            this.$modal.show(
                groupModal, 
                { data: data }, 
                { scrollable: true, width: "30%", height: "auto" }
            );
            // this.$api.requestFetch(`/member_list/getGroup?id=${id}`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$modal.show(
            //                 groupModal, 
            //                 { data: res.data }, 
            //                 { scrollable: true, width: "30%", height: "auto" }
            //             );
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        keyWordSearch() {
            this.lastKeyWord = this.keyWord;
            this.lastGroupId = this.groupId;
            this.getMemberList(1);
        },
        getMemberList(page) {
            this.currentPage = page;
            let url = `/member_list?page=${this.currentPage - 1}&rows=${this.rows}`;
            url += this.lastKeyWord ? `&key_word=${this.lastKeyWord}` : ''; 
            url += this.lastGroupId ? `&group_id=${this.lastGroupId}` : ''; 
            let data = {
                lists: [
                    {
                        name: '人員1',
                        group_id: 0,
                        gid: 2222,
                        ip: '87.87.87.87',
                        start_time: '2018.09.09',
                        addtime: '2018.09.09'
                    },
                    {
                        name: '人員1',
                        group_id: 0,
                        gid: 2222,
                        ip: '87.87.87.87',
                        start_time: '2018.09.09',
                        addtime: '2018.09.09'
                    },
                    {
                        name: '人員1',
                        group_id: 0,
                        gid: 2222,
                        ip: '87.87.87.87',
                        start_time: '2018.09.09',
                        addtime: '2018.09.09'
                    },
                ],
                total: 3
            }
            this.memberList = data.lists;
            this.totalRows = data.total;
            document.documentElement.scrollTop = 0;
            // this.$api.requestFetch(url, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.memberList = res.data.lists;
            //             this.totalRows = res.data.total;
            //             document.documentElement.scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        showDetailModal(row) {
            this.$modal.show(DetailModal, {
                title: `${row.item.name}的遊玩記錄`,
                mid: row.item.id
            }, {
                width:"70%", 
                height:"auto", 
                scrollable: true
            });
        },
        getPlayerInfo(mid, name) {
            let promises = [];
            promises.push(this.$api.getPlayerGame(mid, '', ''));
            promises.push(this.$api.getPlayerInfo(name));
            Promise.all(promises)
                .then(e => this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' }))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!' + `(${e.msg})`));
        }
    }
}
</script>

<style>

</style>



