export const getIsLogin = state => { return state.isLogin }
export const getUsername = state => { return state.username }
export const getLogs = state => { return state.logs }
export const getLogsLength = state => { return state.logs.length }
export const getIsLoading = state => { return state.isLoading }
export const getApiLevel = state => { return state.apiLevel }
export const getWsStatus = state => { return state.wsStatus }


