<template>
<div class="bg-white border p-2">
    <b-form-group>
        <b-form-checkbox v-model="allSelected" @change="toggleAll">
            {{ allSelected ? '清除' : '全選' }}
        </b-form-checkbox>
    </b-form-group>
    <b-form-group v-for="(items, key) in gameGroups" :key="key">
        <b-form-checkbox v-model="groupSelected[key]" @change="e => toggleGroup(e, items.gid)">{{items.name}}: </b-form-checkbox>
        <b-form-checkbox-group v-model="gid" :options="gidOptions[key]"></b-form-checkbox-group>
    </b-form-group>
    <b-row class="m-0">
        <b-col>已選取:{{gid | toGidNameString}}</b-col>
    </b-row>
</div>
</template>

<script>
import { from } from 'rxjs'
import { filter, toArray } from 'rxjs/operators'
export default {
    created: function () {
        this.getGidOption();
    },
    data() {
        return {
            allSelected: false,
            gameGroups: [],
            groupSelected: [],
            gidOptions: [],
            gid: []
        };
    },
    watch: {
        gid(val) {
            this.$emit('gidChange', this.gid);
        }
    },
    methods: {
        getGidOption() {
            let gidName = config.gid_name;
            this.gameGroups = config.gameGroups;
            for (let i = 0, len = this.gameGroups.length; i < len; i++) {
                let gids = this.gameGroups[i].gid;
                let temp = [];
                for (let j = 0, len2 = gids.length; j < len2; j++) {
                    let gid = gids[j];
                    temp.push({
                        value: gid, 
                        text: gidName[ gid ] || ('未定義-' + gid)
                    });
                }
                this.gidOptions.push(temp);
                this.groupSelected.push(false);
            }
        },
        toggleAll(checked) {
            this.gid = [];
            for (let i = 0, len = this.groupSelected.length; i < len; i++) {
                this.groupSelected[i] = checked ? true : false;
            }
            if (checked) {
                for (let i = 0, len = this.gidOptions.length; i < len; i++) {
                    for (let j = 0, len2 = this.gidOptions[i].length; j < len2; j++) {
                        this.gid.push(this.gidOptions[i][j].value);
                    }
                }
            }
        },
        toggleGroup(checked, typeOption) {
            if (checked) {
                let temp = [];
                this.$subscribeTo(
                    from(typeOption).pipe(
                        filter(e => this.gid.indexOf(e) < 0),
                        toArray()
                    ), e => temp = e);
                this.gid = this.gid.concat(temp);
            } else {
                this.$subscribeTo(
                    from(typeOption), e => {
                        let idx = this.gid.indexOf(e);
                        if (idx >= 0) {
                            this.gid.splice(idx, 1);
                        }
                    });
            }
        }
    }
}
</script>


