<template>
    <b-row id="agentController">
        <b-col class="sideMenu scroll sidebar-color sticky-top">
            <b-row v-for="(item, key) in agent" :key="key">
                <b-btn @click="queryAgentData(item)">
                    {{item}}
                </b-btn>
            </b-row>
        </b-col>
        <b-col class="content">
            <process v-if="showContent"></process>
            <h1 v-if="!showContent" style="text-align: center">選擇一個代理吧</h1>            
        </b-col>
    </b-row>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import Process from "./process.vue";

export default {
    name: "AgentController",
    components: {
        Process
    },
    data() {
        return {
            showContent: false
        };
    },
    computed: {
        ...mapGetters({
            agent: "getAgent",
            agentData: "getAgentData",
            target: "getTarget"
        })
    },
    created: function () {
        this.showContent = false;
        this.$api.getAgent();
    },
    methods: {
        queryAgentData(name) {
            if (this.target === name && this.showContent === true) {
                return;
            }
            let data = {
                list:[
                    {
                        id: 0,
                        name: 'server',
                        status: "online",
                        exec_mode: "fork_mode",
                        up_time: 87878787,
                        cpu_use: 64,
                        mem_use: 50,
                        instances: 2,
                        restart_time: 500,
                    },
                    {
                        id: 1,
                        name: 'server2',
                        status: "stopped",
                        exec_mode: "cluster_mode",
                        up_time: 87878787,
                        cpu_use: 64,
                        mem_use: 50,
                        instances: 2,
                        restart_time: 500,
                    },
                    {
                        id: 2,
                        name: 'server3',
                        status: "errored",
                        exec_mode: "cluster_mode",
                        up_time: 87878787,
                        cpu_use: 64,
                        mem_use: 50,
                        instances: 2,
                        restart_time: 500,
                    },
                ],
                os:{
                    intro: '介紹',
                    info: {
                        hostname: 'Agent1',
                        uptime: '50000000',
                        type: 'linux',
                        platform: 'linux',
                        cpu_arch: 'intel',
                        node_ver: '8.7'
                    },
                    monit: {
                        cpu_count: 4,
                        cpu_use: 0.54,
                        mem_total: 10241024,
                        mem_free: 10241024,
                        mem_use: 10241024,
                        loadavg: [0.5, 0.4, 0.3]
                    }
                }
            }
            this.showContent = true;
            this.setTarget(name);
            this.setAgentData(data);
            // this.$api.requestFetch(`/agent/data?name=${name}`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => this.setAgentData(res.data));
        },
        ...mapActions(["setAgentData", "setTarget"])
    }
};
</script>

<style lang="scss">
#agentController {
    text-align: left;
    margin: 0;
    .sideMenu {
        flex: 0 0 12%;
        max-width: 12%;
        top: 3.5rem;
        height: calc(100vh - 3.5rem);
        z-index: 1;
        .btn {
            margin-bottom: 1%;
            width: 100%;
        }
    }
    .content {
        flex: 0 0 88%;
        max-width: 88%;
        padding: 0;
    }
    .table {
        margin: 0;
        td {
            vertical-align: baseline;
        }
    }
}


</style>



