import Vue from 'vue'
import types from './types'

export const setAgent = ({ commit }, data) => {
    commit(types.SETAGENT, data);
}
export const setAgentData = ({ commit }, data) => {
    commit(types.SETAGENTDATA, data);
}
export const setTarget = ({ commit }, data) => {
    commit(types.SETTARGET, data);
}
export const setSendId = ({ commit }, data) => {
    commit(types.SETSENDID, data);
}
export const refresh = ({ commit }, data) => {
    commit(types.REFRESH, data);
}
export const updateOne = ({ commit }, data) => {
    commit(types.UPDATEONE, data);
}
export const toggleDetails = ({ commit }, data) => {
    commit(types.TOOGLEDETAILS, data);
}


