<template>
<div id="player_table">
    <b-table 
        hover
        show-empty
        stacked="md"
        small
        :items="items" 
        :fields="fields" 
        :sort-by.sync="sort" 
        :sort-desc.sync="sortDesc"
        empty-text="無資料">
        <template slot="name" slot-scope="row">
            <div @click="getPlayerInfo(row.item)">{{ row.value }}</div>
        </template>
        <template slot="rounds" slot-scope="row">{{ row.value }}</template>
        <template slot="bet" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
        <template slot="win" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
        <template slot="result" slot-scope="row">
            <span :class="resultTextColor(row.value)">
                {{ row.value | formatMoney(currency) }}
            </span>
        </template>
        <template slot="HEAD_bet" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
        <template slot="HEAD_win" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
        <template slot="HEAD_result" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
    </b-table>
</div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
const { mapGetters, mapActions } = createNamespacedHelpers('game_report');

export default {
    name: "player_table",
    props: ['items'],
    data() {
        return {
            fields: [
                {
                    key: "name",
                    label: "玩家名稱",
                    sortable: true,
                    class: 'clickable'
                },
                {
                    key: "rounds",
                    label: "局數",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "bet",
                    label: "下注金額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "win",
                    label: "贏得金額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "result",
                    label: "總輸贏",
                    sortable: true,
                    class: 'text-right'
                },
            ],
            sort: 'result',
            sortDesc: true
        };
    },
    computed: {
        ...mapGetters({
            startTime: "getStartTime",
            endTime: "getEndTime",
            currency: "getCurrency",
        })
    },
    methods: {
        getPlayerInfo(item) {
            let promises = [];
            promises.push(this.$api.getPlayerGame(item.mid, this.startTime, this.endTime));
            promises.push(this.$api.getPlayerInfo(item.name));
            Promise.all(promises)
                .then(e => this.$emit('getPlayerInfo'))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!'));
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        }
    },
}
</script>


