import Vue from 'vue'
import types from './types'

export const setItems = ({ commit }, data) => {
    commit(types.SETITEMS, data);
}
export const setCurrency = ({ commit }, data) => {
    commit(types.SETCURRENCY, data);
}
export const setStartTime = ({ commit }, data) => {
    commit(types.SETSTARTTIME, data);
}
export const setEndTime = ({ commit }, data) => {
    commit(types.SETENDTIME, data);
}
export const setChooseDay = ({ commit }, data) => {
    commit(types.SETCHOOSEDAY, data);
}
export const setPlayers = ({ commit }, data) => {
    commit(types.SETPLAYERS, data);
}
export const setNewPlayer = ({ commit }, data) => {
    commit(types.SETNEWPLAYER, data);
}
export const setNewTarget = ({ commit }, data) => {
    commit(types.SETNEWTARGET, data);
}
export const setPlayerInfoData = ({ commit }, data) => {
    commit(types.SETPLAYERINFODATA, data);
}
export const setPlayerGameData = ({ commit }, data) => {
    commit(types.SETPLAYERGAMEDATA, data);
}
export const clear = ({ commit }, data) => {
    commit(types.CLEAR, data);
}


