<template>
<div id="list">
    <div v-for="(item, key) in items.slice().reverse()" :key="key" v-if="shouldShow(item.date)" class="p-2 border">
        <b-table
            show-empty 
            bordered 
            fixed 
            hover 
            foot-clone 
            caption-top
            :items="filterPlayer(item.details)" 
            :fields="fields" 
            :sort-by.sync="sort" 
            :sort-desc.sync="sortDesc"
            class="table-fixed" 
            empty-text="無資料"
        >
            <template slot="table-caption">
                <span style="font-size: 24px;">{{ item.date }}</span>
            </template>
            <template slot="mid" slot-scope="row">
                <div @click="getPlayerInfo(row.item, item.date)" :class="isNewPlayer(row.item.mid)">
                    {{ players[row.value] }}
                </div>
            </template>
            <template slot="rounds" slot-scope="row">
                <div @click="getRounds(row.item, item.date)">{{ row.value }}</div>
            </template>
            <template slot="bet" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
            <template slot="win" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
            <template slot="result" slot-scope="row">
                <span :class="resultTextColor(row.value)">{{ row.value | formatMoney(currency) }}</span>
            </template>
            <template slot="HEAD_bet" slot-scope="data">
                {{ data.label | headCurrencyText(currency) }}
            </template>
            <template slot="HEAD_win" slot-scope="data">
                {{ data.label | headCurrencyText(currency) }}
            </template>
            <template slot="HEAD_result" slot-scope="data">
                {{ data.label | headCurrencyText(currency) }}
            </template>
            <template slot="FOOT_name" slot-scope="data">Total:</template>
            <template slot="FOOT_rounds" slot-scope="data">
                {{ total(filterPlayer(item.details), data.column) }}
            </template>
            <template slot="FOOT_bet" slot-scope="data">
                {{ total(filterPlayer(item.details), data.column) | formatMoney(currency) | footCurrencyText(currency) }}
            </template>
            <template slot="FOOT_win" slot-scope="data">
                {{ total(filterPlayer(item.details), data.column) | formatMoney(currency) | footCurrencyText(currency) }}
            </template>
            <template slot="FOOT_result" slot-scope="data">
                <span :class="resultTextColor(total(filterPlayer(item.details), data.column))">
                    {{ total(filterPlayer(item.details), data.column) | formatMoney(currency) | footCurrencyText(currency) }}
                </span>
            </template>
        </b-table>
    </div>
</div>
</template>

<script>
import { from } from 'rxjs'
import { filter, toArray } from 'rxjs/operators'
import { createNamespacedHelpers } from 'vuex';
import dayjs from 'dayjs';
import playerInfo from '../player_info';
const { mapGetters, mapActions } = createNamespacedHelpers('member_report');

export default {
    name: "list",
    components: {
        playerInfo
    },
    props: ['targetPlayers'],
    data() {
        return {
            fields: [
                {
                    key: "mid",
                    label: "玩家",
                    sortable: true,
                    class: 'clickable'
                },
                {
                    key: "rounds",
                    label: "局數",
                    sortable: true,
                    class: 'text-center clickable'
                },
                {
                    key: "bet",
                    label: "下注金額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "win",
                    label: "贏得金額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "result",
                    label: "總輸贏",
                    sortable: true,
                    class: 'text-right'
                }
            ],
            sort: 'result',
            sortDesc: true
        }
    },
    computed: {
        ...mapGetters({
            items: "getItems",
            currency: "getCurrency",
            startTime: "getStartTime",
            endTime: "getEndTime",
            chooseDay: "getChooseDay",
            players: "getPlayers",
            newTarget: "getNewTarget",
            newPlayer: "getNewPlayer"
        })
    },
    methods: {
        getRounds(item, date) {
            let body = {
                mid: item.mid,
                currency: this.currency,
                start_time: dayjs(date).format('YYYY-MM-DD HH:mm:ss'),
                end_time: dayjs(date).add(1, 'days').format('YYYY-MM-DD HH:mm:ss'),
                page: 0,
                rows: 25
            };
            let data = {
                list: [
                    {
                    id: 0,
                    gid: 2222,
                    time: '2018-09-09',
                    bet: 10,
                    win: 10,
                    result: 0
                    },
                    {
                        id: 1,
                        gid: 2222,
                        time: '2018-09-09',
                        bet: 10,
                        win: 10,
                        result: 0
                    },
                    {
                        id: 2,
                        gid: 2222,
                        time: '2018-09-09',
                        bet: 10,
                        win: 10,
                        result: 0
                    },
                ],
                total: 3
            }
            this.showRoundsModal(`${this.players[item.mid]}局數`, data, body);
            // this.$api.requestFetch(`/report/member/rounds`, "POST", body)
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.showRoundsModal(`${this.players[item.mid]}局數`, res.data, body);
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        getPlayerInfo(item, startDate) {
            let promises = [];
            startDate = dayjs(startDate).format('YYYY-MM-DD HH:mm:ss');
            let endDate = dayjs(startDate).add(1, 'days').format('YYYY-MM-DD HH:mm:ss');
            promises.push(this.$api.getPlayerGame(item.mid, startDate, endDate));
            promises.push(this.$api.getPlayerInfo(this.players[item.mid]));
            Promise.all(promises)
                .then(e => this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' }))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!'));
        },
        shouldShow(date) {
            for (let i = 0, len = this.chooseDay.length; i < len; i++) {
                if (this.chooseDay[i] === date) {
                    return true;
                }
            }
            return false;
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        },
        total(item, key) {
            let total = 0;
            for (let i = 0, len = item.length; i < len; i++) {
                total += item[i][key];
            }
            return total;
        },
        showRoundsModal(title, content, sendData) {
            this.$emit('showRoundsModal', {
                title: title,
                content: content,
                sendData: sendData
            })
        },
        isNewPlayer(mid) {
            for (let i = 0, len = this.newPlayer.length; i < len; i++) {
                let temp = this.newPlayer[i];
                if (temp.mid === mid) {
                    return temp.days >= this.newTarget ? 'target-color' : '';
                }
            }
            return '';
        },
        filterPlayer(details) {
            if (this.targetPlayers.length === 0) {
                return details;
            }
            let temp = details.slice();
            this.$subscribeTo(
                from(temp).pipe(
                    filter(e => this.targetPlayers.indexOf(`${e.mid}`) > -1),
                    toArray()
                ), e => temp = e);
            return temp;
        }
    }
}
</script>

<style lang="scss">
.target-color {
    font-weight: bolder;
    color: #ff0000;
}
</style>



