import Vue from 'vue'
import types from './types'

// state
export const state = {
    agent: [],
    agentData: {},
    target: '',
    sendId: '',
    updatedType: true
}

// mutations
export const mutations = {
    [types.SETAGENT](state, data) {
        state.agent = data;
    },
    [types.SETAGENTDATA](state, data) {
        if (data) {
            state.detailId = [];
            state.agentData = data;
        }
    },
    [types.SETTARGET](state, data) {
        state.target = data;
    },
    [types.SETSENDID](state, data) {
        state.sendId = data;
    },
    [types.REFRESH](state, data) {
        if (data.type === 'os') {
            state.agentData.os = data.content.os;
        } else if (data.type === 'list') {
            let temp = state.updatedType ? 1 : 2;
            state.updatedType = !state.updatedType;
            for (let i = 0, len = data.content.list.length; i < len; i++) {
                for (let j = 0, len2 = data.content.list[i].monitors.length; j < len2; j++) {
                    let state_monitors = state.agentData.list[i].monitors;
                    let data_monitors  = data.content.list[i].monitors;
                    if (state_monitors[j] && data_monitors[j] && data_monitors[j].val !== state_monitors[j].val) {
                        let tempClass = state_monitors[j].isUpdated === 'table-updated1' ? 'table-updated2' : 'table-updated1';
                        data.content.list[i].monitors[j].isUpdated = tempClass;
                    }
                }
                data.content.list[i]._showDetails = state.agentData.list[i]._showDetails;
                data.content.list[i]._cellVariants = { 
                    mem_use: data.content.list[i].mem_use !== state.agentData.list[i].mem_use ? `updated${temp}` : '',
                    cpu_use: data.content.list[i].cpu_use !== state.agentData.list[i].cpu_use ? `updated${temp}` : '' 
                };
            }
            state.agentData.list = data.content.list;
        } else {
            state.agentData = data.content;
        }
    },
    [types.UPDATEONE](state, data) {
        if (data.act === 'delete') {
            for (let i = 0, len = state.agentData.list.length; i < len; i++) {
                if(state.agentData.list[i].id === data.data[0].id) {
                    state.agentData.list.splice(i, 1);
                }
            }
        } else if (data.act === 'add') {
            state.agentData.list.push(data.data[0]);
        } else {
            for (let i = 0, len = data.data.length; i < len; i++) {
                for (let j = 0, len2 = state.agentData.list.length; j < len2; j++) {
                    if (data.data[i].id === state.agentData.list[j].id) {
                        for (let key in state.agentData.list[j]) {
                            if (data.data[i][key]) {
                                state.agentData.list[j][key] = data.data[i][key];
                            }
                        }
                    }
                }
            }
        }
    },
    [types.TOOGLEDETAILS](state, data) {
        data.toggleDetails();
    }
}


