<template>
<modal name="modalAdd" width="30%" height="auto">
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">建立帳號</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('modalAdd')">×</button>
        </header>
        <div class="modal-body">
            <form>
                帳號:
                <b-form-input type="text" v-model="addData.account"></b-form-input>
                密碼:
                <b-form-input type="text" v-model="addData.password"></b-form-input>
                名稱:
                <b-form-input type="text" v-model="addData.name"></b-form-input>
                角色:
                <b-form-select v-model="addData.level" :options="levelOptions"/>
                狀態:
                <b-form-select v-model="addData.disabled" :options="disabledOptions"/>
            </form>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$modal.hide('modalAdd')">Cancel</b-btn>
            <b-btn @click="add">OK</b-btn>
        </footer>
    </div>
</modal>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
    name: "ModalAdd",
    created() {
        this.getLevelOptions();
    },
    data() {
        return {
            addData: {
                name: "",
                account: "",
                password: "",
                level: "",
                disabled: "",
            },
            disabledOptions: [
                {value: '0', text: '啟用'},
                {value: '1', text: '停用'}
            ],
            levelOptions: []
        };
    },
    methods: {
        getLevelOptions() {
            let level = config.level;
            let keys  = Object.keys(level);
            for (let i = 0, len = keys.length; i < len; i++) {
                this.levelOptions.push( { value: keys[i], text: level[keys[i]] } )
            }
        },
        clearAddData() {
            this.addData = {
                name: "",
                account: "",
                password: "",
                level: "",
                disabled: "",
            };
        },
        add() {
            // this.$api.requestFetch(`/account/create`, "POST", this.addData)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status === true) {
            //             this.$emit('refresh');
            //             this.$api.showModalInfo('修改資訊', '新增成功');
            //         } else {
            //             this.$api.showModalInfo('修改資訊', '新增失敗');
            //         }
            //     });
            this.$api.showModalInfo('修改資訊', '新增成功');
            this.clearAddData();
            this.$modal.hide('modalAdd');
        },
        ...mapActions(["updateItems"])
    }
};
</script>

<style lang="scss">

</style>



