<template>
    <div>
        <b-jumbotron class="m-0 p-4">
            <h1>{{title}}</h1>
            <p>本日遊玩人數: {{data.players}}人</p>
            本日加入玩家: {{ data.new_member ? '' : '無' }}
            <b-badge 
                pill 
                variant="primary" 
                v-for="(item, key) in data.new_member" 
                :key="key"
                class="mr-1"
                style="font-size: 14px"
            >
                {{item}}
            </b-badge>
        </b-jumbotron>
        <div>
            <div class="card-header m-0">
                <span class="font-weight-bold" style="font-size: 30px">今日盈虧</span>
            </div>
            <b-row class="card-body p-0 m-1">
                <b-col md="12" v-for="(item, key) in currencyData" :key="key" class="border p-0">
                    <b-card-header
                        :header="item.currency"
                    ></b-card-header>
                    <group-bar-chart
                        :data="winLossDataset(item.win_loss)"
                        :padding="['30%', '10%']"
                        :height="300"
                        :x="'gid'"
                        :y="'value'"
                        :color="'key'"
                    ></group-bar-chart>
                    <b-row class="card-body m-1">
                        <b-col md="3" v-if="item.game_lose" class="p-0">
                            <b-card-header header="前三輸錢遊戲"></b-card-header>
                            <b-list-group-item v-for="(item2, key) in item.game_lose" :key="key" class="d-flex justify-content-between">
                                {{item2.gid}} <span>{{item2.result | formatMoney(item.currency)}}</span>
                            </b-list-group-item>
                        </b-col>
                        <b-col md="3" v-if="item.game_round" class="p-0">
                            <b-card-header header="前三局數遊戲"></b-card-header>
                            <b-list-group-item v-for="(item2, key) in item.game_round" :key="key" class="d-flex justify-content-between">
                                {{item2.gid}} <span>{{item2.rounds}}</span>
                            </b-list-group-item>
                        </b-col>
                        <b-col md="3" v-if="item.player_lose" class="p-0">
                            <b-card-header header="前五賺錢玩家"></b-card-header>
                            <b-list-group-item v-for="(item2, key) in item.player_lose" :key="key" class="d-flex justify-content-between">
                                <div class="clickable" @click="getPlayerInfo(item2)">{{item2.name}}</div> 
                                <span>{{item2.result | formatMoney(item.currency)}}</span>
                            </b-list-group-item>
                        </b-col>
                        <b-col md="3" v-if="item.player_win" class="p-0">
                            <b-card-header header="前五輸錢玩家"></b-card-header>
                            <b-list-group-item v-for="(item2, key) in item.player_win" :key="key" class="d-flex justify-content-between">
                                <div class="clickable" @click="getPlayerInfo(item2)">{{item2.name}}</div> 
                                <span>{{item2.result | formatMoney(item.currency)}}</span>
                            </b-list-group-item>
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
        </div>
    </div>
</template>

<script>
import dayjs from 'dayjs';
import { from } from 'rxjs'
import { reduce, mergeMap, toArray, groupBy } from 'rxjs/operators'
import { mapGetters, mapActions } from "vuex";
import GroupBarChart from './chart/group_bar_chart';
import playerInfo from './player_info';

const currency = config.currency;

export default {
    components: {
        GroupBarChart,
        playerInfo
    },
    created() {
        this.title = `你好!${this.level()}-${this.username}`
        this.getHome();
        this.getHomeCurrency();
    },
    data() {
        return {
            title: '',
            data: {
                players: 5,
                new_member: ['玩家1', '玩家2', '玩家3', '玩家4', '玩家5']
            },
            currencyData: [
                {
                    currency: 'TWD',
                    win_loss: [
                        {
                            gid: '遊戲A',
                            bet: 200,
                            win: 200,
                            result: 0
                        },
                        {
                            gid: '遊戲B',
                            bet: 100,
                            win: 200,
                            result: 100
                        },
                        {
                            gid: '遊戲C',
                            bet: 300,
                            win: 200,
                            result: -100
                        },
                        {
                            gid: '遊戲D',
                            bet: 500,
                            win: 200,
                            result: -300
                        }
                    ],
                    game_lose: [
                        {
                            gid: '遊戲A',
                            result: 100
                        },
                        {
                            gid: '遊戲B',
                            result: -1000
                        }
                    ],
                    game_round: [
                        {
                            gid: '遊戲A',
                            rounds: 100
                        },
                        {
                            gid: '遊戲B',
                            rounds: 200
                        }
                    ],
                    player_lose: [
                        {
                            name: '玩家A',
                            result: 100
                        },
                        {
                            name: '玩家B',
                            result: 200
                        }
                    ],
                    player_win: [
                        {
                            name: '玩家A',
                            result: 100
                        },
                        {
                            name: '玩家B',
                            result: 200
                        }
                    ],
                },
                {
                    currency: 'RMB',
                    win_loss: [
                        {
                            gid: '遊戲A',
                            bet: 200,
                            win: 200,
                            result: 0
                        },
                        {
                            gid: '遊戲B',
                            bet: 100,
                            win: 200,
                            result: 100
                        },
                        {
                            gid: '遊戲C',
                            bet: 300,
                            win: 200,
                            result: -100
                        },
                        {
                            gid: '遊戲D',
                            bet: 500,
                            win: 200,
                            result: -300
                        }
                    ],
                    game_lose: [
                        {
                            gid: '遊戲A',
                            result: 100
                        },
                        {
                            gid: '遊戲B',
                            result: -1000
                        }
                    ],
                    game_round: [
                        {
                            gid: '遊戲A',
                            rounds: 100
                        },
                        {
                            gid: '遊戲B',
                            rounds: 200
                        }
                    ],
                    player_lose: [
                        {
                            name: '玩家A',
                            result: 100
                        },
                        {
                            name: '玩家B',
                            result: 200
                        }
                    ],
                    player_win: [
                        {
                            name: '玩家A',
                            result: 100
                        },
                        {
                            name: '玩家B',
                            result: 200
                        }
                    ],
                }
            ]
        }
    },
    computed: {
        ...mapGetters({
            username: "getUsername",
        })
    },
    methods: {
        level() {
            return config.level[sessionStorage.getItem('level')];
        },
        getHome() {
            // this.$api.requestFetch(`/home`, "GET", '', false)   
            // .catch(err => alert(err)) 
            // .then(res => {
            //     if (res.status) {
            //         this.data = res.data;
            //     }
            // });
        },
        getHomeCurrency() {
            // let keys = Object.keys(currency);
            // for (let i = 0, len = keys.length; i < len; i++) {
            //     let val = currency[keys[i]];
            //     this.$api.requestFetch(`/home/currency?currency=${val}`, "GET", '', false)   
            //         .catch(err => alert(err)) 
            //         .then(res => {
            //             if (res.status) {
            //                 if (Object.keys(res.data).length === 0) return;
            //                 res.data.currency = val;
            //                 res.data.win_loss = this.groupCurrency(res.data.win_loss, val);
            //                 this.currencyData.push(res.data);
            //             }
            //         });
            // }
        },
        groupCurrency(data, currency) {
            let gidName = config.gid_name;
            let temp = '';
            this.$subscribeTo(
                from(data).pipe(
                    reduce((acc, curr) => {
                        acc.data.push({
                            gid: gidName[curr.gid],
                            bet: curr.bet,
                            win: curr.win,
                            result: Number((curr.win - curr.bet).toFixed(2))
                        });
                        return acc;
                    }, {data: []})
                ), e => temp = e.data);
            return temp;
        },
        winLossDataset(data) {
            const dv = new DataSet.View().source(data);
            dv.transform({
                type: 'sort-by',
                fields: [ 'result' ]
            });
            dv.transform({
                type: 'fold',
                fields: ['bet', 'win', 'result'],
                key: 'key',
                value: 'value',
            });
            return dv.rows;
        },
        getPlayerInfo(item) {
            let promises = [];
            let today = dayjs().format('YYYY-MM-DD');
            promises.push(this.$api.getPlayerGame(item.mid, `${today} 00:00:00`, `${today} 23:59:59`));
            promises.push(this.$api.getPlayerInfo(item.name));
            Promise.all(promises)
                .then(e => this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' }))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!'));
        },
    }
}
</script>

<style>

</style>



