<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">JP發送</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-form-group>
                玩家:
                <b-form-input type="text" v-model="name" disabled></b-form-input>
                幣別:
                <b-form-input type="text" v-model="currency" disabled></b-form-input>
                JP等級:
                <b-form-select :options="levels" style="height:100%" v-model="level"></b-form-select>
                JP權重:
                <b-form-select :options="percentOptions" style="height:100%" v-model="percent"></b-form-select>
            </b-form-group>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">Cancel</button>
            <button type="button" class="btn btn-primary" @click="sendJp()">OK</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'name', 'mid', 'currency', 'percentOptions' ],
    data() {
        return {
            level: '',
            percent: '',
            levels: [1, 2, 3, 4]
        }
    },
    methods: {
        sendJp() {
            this.$emit('close');
            if (!this.level || !this.percent || !this.mid) {
                this.$api.showModalInfo('ERROR', '資料不得為空');
                return;
            } else if (!this.currency) {
                this.$api.showModalInfo('ERROR', '此玩家從未遊玩過');
                return;
            }
            let data = {
                jp: this.level,           
                currency: this.currency,  
                percent: this.percent,   
                player_id: this.mid 
            }
            this.$api.showModalInfo('SUCCESS', '發送成功');
            // this.$api.requestFetch(`/jackpot/give_jp `, "POST", data)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '發送成功');
            //             //todo...
            //         } else {
            //             this.$api.showModalInfo('ERROR', '發送失敗' + `(${res.msg})`);
            //         }
            //     });
        },
    }
}
</script>

<style>

</style>



