<template>
    <b-input-group>
        <b-btn v-if="!noQuick" @click="getDay" class="mr-2">本日</b-btn>
        <b-btn v-if="!noQuick" @click="getYesterday" class="mr-2">昨日</b-btn>
        <b-btn v-if="!noQuick" @click="getWeek" class="mr-2">本週</b-btn>
        <b-btn v-if="!noQuick" @click="getLastWeek" class="mr-2">上週</b-btn>
        <b-btn v-if="!noQuick" @click="getMonth" class="mr-2">本月</b-btn>
        <b-input-group-prepend is-text>開始時間</b-input-group-prepend>
        <datepicker :format="format" v-model="startDate" :disabledDates="startDisable" input-class="date-input" @input="emitStartDate"></datepicker>
        <vue-timepicker format="HH:mm:ss" v-model="startTime" hide-clear-button @change="$emit('startTime', startTime)"></vue-timepicker>
        <b-input-group-prepend is-text>結束時間</b-input-group-prepend>
        <datepicker :format="format" v-model="endDate" :disabledDates="endDisable" input-class="date-input" @input="emitEndDate"></datepicker>
        <vue-timepicker format="HH:mm:ss" v-model="endTime" hide-clear-button @change="$emit('endTime', endTime)"></vue-timepicker>
    </b-input-group>
</template>

<script>
import Datepicker from 'vuejs-datepicker';
import VueTimepicker from 'vue2-timepicker'
import dayjs from 'dayjs';

export default {
    props: {
        noQuick: { default: false },
        monthFirst : { default: false }
    },
    components: {
        Datepicker,
        VueTimepicker
    },
    created() {
        if (this.monthFirst) {
            this.getMonth();
        } else {
            this.getDay();
        }
    },
    data() {
        return {
            format: 'yyyy-MM-dd',
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: ''
        }
    },
    computed: {
        startDisable() {
            return {
                from: new Date(dayjs((this.endDate)))
            }
        },
        endDisable() {
            return {
                to: new Date(dayjs((this.startDate)))
            }   
        },
    },
    methods: {
        emitStartDate() {
            this.$emit('startDate', dayjs(this.startDate).format('YYYY-MM-DD'));
        },
        emitEndDate() {
            this.$emit('endDate', dayjs(this.endDate).format('YYYY-MM-DD'));
        },
        getDay() {
            this.startDate = new Date(dayjs());
            this.endDate   = this.startDate;
            this.setTimeAndSend();
        },
        getYesterday() {
            this.startDate = new Date(dayjs().subtract(1, 'days'));
            this.endDate   = this.startDate;
            this.setTimeAndSend();
        },
        getWeek() {
            this.startDate = new Date(dayjs().startOf('week'));
            this.endDate   = new Date(dayjs().endOf('week'));
            this.setTimeAndSend();
        },
        getLastWeek() {
            this.startDate = new Date(dayjs().startOf('week').add(-1, 'week'));
            this.endDate   = new Date(dayjs().endOf('week').add(-1, 'week'));
            this.setTimeAndSend();
        },
        getMonth() {
            this.startDate = new Date(dayjs().startOf('month'));
            this.endDate   = new Date(dayjs().endOf('month'));
            this.setTimeAndSend();
        },
        setTimeAndSend() {
            this.startTime = { HH: '00', mm: '00', ss: '00' };
            this.endTime   = { HH: '23', mm: '59', ss: '59' };
            this.emitStartDate();
            this.emitEndDate();
        }
    }
}
</script>

<style>

</style>



