<template>
<div id="input-filed">
    <b-row align-v="center" class="card-header m-0">
        <b-col md="2">
            <span class="font-weight-bold" style="font-size: 30px">遊戲報表</span>
        </b-col>
        <b-col md="2" class="text-right">
            <b-input-group prepend="幣別">
                <b-form-select v-model="currency" :options="currencyOptions"/>
                <b-btn v-b-toggle.collapse class="ml-2">遊戲列表</b-btn>
            </b-input-group>
        </b-col>
        <b-col md="7">
            <date-time
                v-on:startDate="startDate=$event"
                v-on:startTime="startTime=$event"
                v-on:endDate="endDate=$event"
                v-on:endTime="endTime=$event"
            ></date-time>
        </b-col>
        <b-col md="1">
            <b-btn variant="primary" @click="getGameReport()">查詢</b-btn>
        </b-col>
    </b-row>
    <b-collapse id="collapse" class="mt-2">
        <game-select v-on:gidChange="gid=$event"></game-select>
    </b-collapse>
</div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import GameSelect from './game_select';
import DateTime from '../date_time'

const { mapGetters, mapActions } = createNamespacedHelpers('game_report');

export default {
    name: "input-filed",
    components: {
        DateTime,
        GameSelect
    },
    data() {
        return {
            type: '',
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            currency: 'TWD',
            gid: [],
            modalInfo: {
                title: "",
                content: ""
            },
            currencyOptions: []
        };
    },
    created: function () {
        this.getCurrencyOption();
    },
    computed: {
        ...mapGetters({})
    },
    methods: {
        getGameReport() {
            this.items = [];
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            let body = {
                gid: this.gid,
                start_time: totalStart,
                end_time: totalend,
                currency: this.currency
            };
            let data = [
                {
                    gid: 2202,
                    rounds: 50,
                    players: 3,
                    bet: 5000,
                    win: 1000,
                    result: -4000
                },
                {
                    gid: 2203,
                    rounds: 20,
                    players: 1,
                    bet: 2000,
                    win: 1000,
                    result: -1000
                },
                {
                    gid: 2204,
                    rounds: 3000,
                    players: 30,
                    bet: 1000,
                    win: 1000,
                    result: 0
                },
            ]
            this.setItems(data);
            this.setCurrency(this.currency);
            this.setStartTime(totalStart);
            this.setEndTime(totalend);
            // this.$api.requestFetch(`/report/game`, "POST", body)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             if (res.data.length === 0) {
            //                 this.$api.showModalInfo('Success', `無資料`);
            //             }
            //             this.setItems(res.data);
            //             this.setCurrency(this.currency);
            //             this.setStartTime(totalStart);
            //             this.setEndTime(totalend);
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        getCurrencyOption() {
            let currency = config.currency;
            let keys    = Object.keys(currency);
            for (let i = 0, len = keys.length; i < len; i++) {
                this.currencyOptions.push({ value: keys[i], text: currency[keys[i]] });
            }
        },
        ...mapActions(["setItems", "setCurrency", "setStartTime", "setEndTime"])
    }
}
</script>



