<template>
    <b-input-group class="my-1">
        <b-input-group-prepend is-text v-b-tooltip.hover title="名稱">
            {{name}}
        </b-input-group-prepend>
        <b-form-input :value="value" type="text" readonly :id="toolTipId"></b-form-input>
        <b-input-group-text v-b-tooltip.hover title="最近更新">{{update | formatTime}}</b-input-group-text>
        <b-input-group-append>
            <b-btn class="material-icons" @click="copy">file_copy</b-btn>
        </b-input-group-append>
        <b-tooltip triggers.hover :target="toolTipId" no-fade>
            <strong>{{toolTipText}}</strong>
        </b-tooltip>
        <b-popover :target="toolTipId" triggers="click" :show.sync="popoverShow">
            <template slot="title">
                更改伺服器設定: <button type="button" aria-label="Close" class="close" @click="popoverShow=false" style="line-height: 0.7">×</button>
            </template>
            <form>
                <label>伺服器: {{server}}({{currency}})</label>
                <b-input-group :prepend="name">
                    <b-form-input type="text" v-model="editText" @keyup.native.enter="updateConfig"></b-form-input>
                </b-input-group>
                <b-row class="mt-2">
                    <b-col offset-md="9">
                        <b-btn @click="updateConfig" size="sm" variant="primary">更改</b-btn>
                    </b-col>
                </b-row>
            </form>
        </b-popover>
    </b-input-group>
</template>

<script>
export default {
    name: 'input-filed',
    props: ['server', 'name', 'value', 'currency', 'update'],
    created() {
        this.toolTipId = this.server + this.currency + this.name;
    },
    data() {
        return {
            disabled: false,
            toolTipId: '',
            toolTipText: '設定值',
            editText: this.value,
            popoverShow: false
        }
    },
    methods: {
        copy() {
            let copyTarget = this.$el.getElementsByTagName('input')[0];
            copyTarget.select();
            document.execCommand('copy');
            this.toolTipText = '複製成功';
            this.$root.$emit('bv::show::tooltip', this.toolTipId);
            setTimeout(() => {
                this.$root.$emit('bv::hide::tooltip', this.toolTipId);
                this.toolTipText = '設定值';
            } , 1000);
        },
        updateConfig() {
            this.popoverShow = false;
            let body = {
                value: this.editText,
                currency: this.currency,
                server: this.server,
                name: this.name
            };
            this.$api.showModalInfo('Success', `更改成功`);
            // this.$api.requestFetch(`/server_config/update`, "POST", body)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if(res.status) {
            //             this.$emit('refreshOne', body.server);
            //             this.$api.showModalInfo('Success', `更改成功`);
            //         } else {
            //             this.$api.showModalInfo('Error', `更改失敗`);
            //         }
            //     });
        }
    }
}
</script>

<style lang="scss">

</style>



