<template>
<modal name="modalAdd" width="30%" height="auto" scrollable>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">建立新服務</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('modalAdd')">×</button>
        </header>
        <div class="modal-body">
            <form>
                服務名稱:
                <b-form-input type="text" v-model="addData.name"></b-form-input>
                JS檔案路徑:
                <b-input-group :prepend="agentData.root_path">
                    <b-form-input type="text" v-model="addData.script"></b-form-input>
                </b-input-group>
                記憶體重啟最大限制:
                <b-input-group>
                    <b-form-input type="text" v-model="addData.max_memory"></b-form-input>
                    <b-input-group-append>
                        <b-form-select v-model="memUnit">
                            <option value="K">K</option>
                            <option value="M">M</option>
                            <option value="G">G</option>
                        </b-form-select>
                    </b-input-group-append>
                </b-input-group>
                <b-form-group>
                    <template slot="label">
                            設定:
                        </template>
                    <b-row>
                        <b-col>
                            <b-input-group>
                                <b-input-group-prepend is-text>
                                    <b-form-checkbox v-model="addData.autorestart">
                                        是否自動重啟
                                    </b-form-checkbox>
                                    <b-form-checkbox v-model="showInstances" @change="(e) => {this.addData.instances = e ? 1 : 0;}">
                                        是否群集模式
                                    </b-form-checkbox>
                                </b-input-group-prepend>
                                <b-form-input type="number" min="1" v-model.number="addData.instances" v-show="showInstances"></b-form-input>
                            </b-input-group>
                        </b-col>
                    </b-row>
                </b-form-group>
                <b-form-group>
                    <template slot="label">
                            環境變數:
                            <b-btn class="material-icons mr-1" @click="addEnv" style="height:100%">add_box</b-btn>
                        </template>
                    <b-row v-for="(item, key) in envKey" :key="key" align-v="center" class="mt-1">
                        <b-col md="6">
                            <b-input-group prepend="key">
                                <b-form-input type="text" v-model="envKey[key]"></b-form-input>
                            </b-input-group>                            
                        </b-col>
                        <b-col md="6">
                            <b-input-group prepend="value">
                                <b-form-input type="text" v-model="envVaule[key]"></b-form-input>
                                <b-btn class="material-icons ml-1" @click="removeEnv(key)">clear</b-btn> 
                            </b-input-group>                                 
                        </b-col>
                    </b-row>
                </b-form-group>
            </form>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$modal.hide('modalAdd')">Cancel</b-btn>
            <b-btn @click="addProc">OK</b-btn>
        </footer>
    </div>
</modal>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
    name: "ModalAdd",
    data() {
        return {
            showInstances: false,
            addData: {
                name: "",
                script: "",
                autorestart: true,
                instances: 0,
                max_memory: "",
                env: {}
            },
            memUnit: 'M',
            envKey: [],
            envVaule: []
        };
    },
    computed: {
        ...mapGetters({
            target: "getTarget",
            agentData: "getAgentData",
        })
    },
    created: function () {},
    methods: {
        ...mapActions(["updateOne"]),
        addEnv() {
            this.envKey.push("");
            this.envVaule.push("");
        },
        removeEnv(key) {
            this.envKey.splice(key, 1);
            this.envVaule.splice(key, 1);
        },
        clearAddData() {
            this.addData = {
                name: "",
                script: "",
                autorestart: true,
                instances: 0,
                max_memory: "",
                env: {}
            };
            this.showInstances = false;
            this.memUnit = 'M';
            this.envKey = [];
            this.envVaule = [];
        },
        addProc() {
            for (let i = 0, len = this.envKey.length; i < len; i++) {
                this.addData.env[this.envKey[i]] = this.envVaule[i];
            }
            this.addData.max_memory = this.addData.max_memory ? `${this.addData.max_memory}${this.memUnit}` : '';
            this.addData.name = this.target;
            // this.$api.requestFetch(`/agent/start`, "POST", this.addData)
            //     .catch(err => alert(err))
            //     .then(res => {
            //         if (res.status) {
            //             this.updateOne({
            //                 act: "add",
            //                 data: res.data
            //             });
            //         }
            //         let ans = res.status ? '成功' : '失敗';
            //         this.$api.showModalInfo('新增結果', `新增服務${ans}`);
            //     });
            this.$api.showModalInfo('新增結果', `新增服務xoxoxox`);
            this.clearAddData();
            this.$modal.hide('modalAdd');
        }
    }
};
</script>

<style lang="scss">

</style>



