<template>
<div id="memberReport">
    <input-filed
        v-on:showRoundsModal="showRoundsModal($event.title, $event.content, $event.sendData)"
    ></input-filed>
    <modal id="roundsTable" name="memberRoundsTable" @before-close="resetModal" width="50%" height="auto" scrollable>
        <div class="border h-100">
            <header class="modal-header">
                <h5 class="modal-title">{{ modalInfo.title }}</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('memberRoundsTable')">×</button>
            </header>
            <div class="modal-body">
                <b-table show-empty :items="modalInfo.content" :fields="fields" bordered empty-text="無資料">
                        <template slot="id" slot-scope="row">
                            <div @click="getTicketUrl(row.item)">{{ row.value }}</div>
                        </template>
                        <template slot="gid" slot-scope="row">{{ row.value | toGidName }}</template>
                        <template slot="time" slot-scope="row">{{ row.value | formatTime }}</template>
                        <template slot="bet" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
                        <template slot="win" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
                        <template slot="result" slot-scope="row">
                            <span :class="resultTextColor(row.value)">
                                {{ row.value | formatMoney(currency) }}
                            </span>
                        </template>
                        <template slot="HEAD_bet" slot-scope="data">
                            {{ data.label | headCurrencyText(currency) }}
                        </template>
                        <template slot="HEAD_win" slot-scope="data">
                            {{ data.label | headCurrencyText(currency) }}
                        </template>
                        <template slot="HEAD_result" slot-scope="data">
                            {{ data.label | headCurrencyText(currency) }}
                        </template>
                </b-table>
                <pagination-fast
                    :totalRows="totalRows"
                    :rows="per_page"
                    :currentPage="currentPage"
                    v-on:changePage="requestPage"
                ></pagination-fast>
                <b-pagination :total-rows="totalRows" :per-page="per_page" v-model="currentPage" align="right" :limit="10" @change="requestPage"/>
            </div>
            <footer class="modal-footer">
                <button type="button" class="btn btn-primary" @click="$modal.hide('memberRoundsTable')">OK</button>
            </footer>
        </div>
    </modal>
</div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import InputFiled from './input';
import ticketModal from '../ticket_modal';
import paginationFast from '../pagination_fast';

const { mapGetters, mapActions } = createNamespacedHelpers('member_report');

export default {
    components: {
        InputFiled,
        ticketModal,
        paginationFast
    },
    data() {
        return {
            modalInfo: {
                title: "",
                content: []
            },
            fields: [
                {
                    key: "id",
                    label: "注單編號",
                    class: 'clickable'
                },
                {
                    key: "gid",
                    label: "遊戲名稱",
                    class: 'text-center'
                },
                {
                    key: "time",
                    label: "下注時間",
                    class: 'text-center'
                },
                {
                    key: "bet",
                    label: "下注金額",
                    class: 'text-right'
                },
                                {
                    key: "win",
                    label: "贏得金額",
                    class: 'text-right'
                },
                {
                    key: "result",
                    label: "總輸贏",
                    class: 'text-right'
                }
            ],
            sendData: '',
            totalRows: 0,
            per_page: 25,
            currentPage: 1,
        };
    },
    computed: {
        ...mapGetters({
            currency: "getCurrency",
        })
    },
    beforeDestroy: function() {
        this.clear();
    },
    methods: {
        requestPage(page) {
            // this.sendData.page = page - 1;
            // this.currentPage = page;
            // this.$api.requestFetch(`/report/member/rounds`, "POST", this.sendData)
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.addResult(res.data.list);
            //             this.modalInfo.content = res.data.list;
            //             document.getElementById('roundsTable').scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        getTicketUrl(item) {
            // this.$api.getTicketUrl(item.id)
            //     .then(e => this.$modal.show(ticketModal, { url: e }, { draggable: true, width: "50%", height: "auto" }))
            //     .catch(e => alert(e));
        },
        showRoundsModal(title, content, sendData) {
            this.addResult(content.list)
            this.sendData  = sendData;
            this.totalRows = content.total;
            this.modalInfo.title = title;
            this.modalInfo.content = content.list;
            this.$modal.show('memberRoundsTable')
        },
        addResult(list) {
            for (let i = 0, len = list.length; i < len; i++) {
                list[i].result = list[i].win - list[i].bet;
            }
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        },
        resetModal() {
            this.modalInfo.title = "";
            this.modalInfo.content = [];
        },
        ...mapActions(["clear"])
    },
};
</script>

<style lang="scss">
.card-body {
    padding: 0.5rem;
}
</style>



