<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">更改名稱</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            名稱-繁體:
            <b-form-input type="text" v-model="sendData.name_tw"></b-form-input>
            名稱-簡體:
            <b-form-input type="text" v-model="sendData.name_cn"></b-form-input>
            名稱-英文:
            <b-form-input type="text" v-model="sendData.name_en"></b-form-input>
            名稱-日文:
            <b-form-input type="text" v-model="sendData.name_jp"></b-form-input>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">取消</button>
            <button type="button" class="btn btn-primary" @click="edit()">更改</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'gid', 'refresh', 'old_data' ],
    mounted() {
        this.sendData = this.old_data;
    },
    data() {
        return {
            sendData: {
                name_tw: '',
                name_cn: '',
                name_en: '',
                name_jp: ''
            }
        }
    },
    methods: {
        edit() {
            // this.sendData.gid = [this.gid];
            // this.$api.requestFetch(`/game_list/edit`, "POST", this.sendData)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '更改成功');
            //             this.refresh();
            //             this.$emit('close');
            //         } else {
            //             this.$api.showModalInfo('ERROR', '更改失敗' + `(${res.msg})`);
            //             this.$emit('close');
            //         }
            //     });
            this.$api.showModalInfo('SUCCESS', '更改成功');
            this.$emit('close');
        }
    }

}
</script>

