<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">得獎細單</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-list-group flush>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>注單編號</span>{{ rewardDetails.bet_id }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>玩家</span>{{ rewardDetails.player }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>類型</span>{{ rewardDetails.type | toJpType }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>狀態</span>{{ rewardDetails.status | jpStatus }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>幣別</span>{{ rewardDetails.currency }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>贏得</span>{{ rewardDetails.win | formatMoney(rewardDetails.currency) }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>派發者</span>{{ rewardDetails.operator || '系統派發' }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>取獎時間</span>{{ rewardDetails.received_at | formatTime }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>過期時間</span>{{ rewardDetails.expired_at | formatTime }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>更新時間</span>{{ rewardDetails.updated_at | formatTime }}
                </b-list-group-item>
                <b-list-group-item class="d-flex justify-content-between">
                    <span>發送時間</span>{{ rewardDetails.created_at | formatTime }}
                </b-list-group-item>
                <b-list-group-item>
                    <span>匯率表</span>
                    <b-list-group-item v-for="(item, key) in rewardDetails.exchange_rate" :key="key" class="d-flex justify-content-between">
                        <span>{{ key }}</span>{{ item | formatMoney(key) }}
                    </b-list-group-item>
                </b-list-group-item>
            </b-list-group>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">Close</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: ['rewardDetails']
}
</script>

<style>

</style>



