<template>
    <div id="member-modal" class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">分組玩家(共{{data.length}}位)</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-card-group>
                <b-card>
                    <b-list-group-item v-for="(item, key) in localData[0]" :key="key">
                        <span>{{item}}</span>
                    </b-list-group-item>
                </b-card>
                <b-card>
                    <b-list-group-item v-for="(item, key) in localData[1]" :key="key">
                        <span>{{item}}</span>
                    </b-list-group-item>
                </b-card>
                <b-card>
                    <b-list-group-item v-for="(item, key) in localData[2]" :key="key">
                        <span>{{item}}</span>
                    </b-list-group-item>
                </b-card>
            </b-card-group>
            <span v-if="!data.length" class="ml-4">該分組無玩家</span>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">OK</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'data' ],
    mounted() {
        for (let i = 0, len = this.data.length; i < len; i++) {
            let idx = i % 3;
            this.localData[idx].push(this.data[i]);
        }
    },
    data() {
        return {
            localData: [[],[],[]]
        }
    }
}
</script>

<style lang="scss">
    #member-modal {
        .card {
            border: 0;
        }
        .card-body {
            padding: 0;
            margin: 5px;
        }
    }
</style>



