<template>
    <div>
        <div class="card-header">
            <b-row align-v="center">
                <b-col md="6">
                    <b-input-group>
                        <b-input-group-prepend is-text>類型</b-input-group-prepend>
                        <b-form-select v-model="type" :options="typeOptions"/>
                        <b-input-group-prepend is-text>幣值</b-input-group-prepend>
                        <b-form-select v-model="currency" :options="currencyOptions"/>
                        <b-input-group-prepend is-text>狀態</b-input-group-prepend>
                        <b-form-select v-model="status" :options="statusOptions"/>
                        <b-input-group-append>
                            <b-btn @click="clearSelect">Clear</b-btn>
                        </b-input-group-append>
                    </b-input-group>
                </b-col>
                <b-col md="4">
                    <b-input-group>
                        <b-input-group-prepend is-text>注單編號</b-input-group-prepend>
                        <b-form-input v-model="id"></b-form-input>
                        <b-input-group-append>
                            <b-btn :disabled="!id" @click="id = ''">Clear</b-btn>
                        </b-input-group-append>
                    </b-input-group>
                </b-col>
            </b-row>
            <b-row align-v="center" class="mt-2">
                <b-col md="11">
                    <date-time
                        v-on:startDate="startDate=$event"
                        v-on:startTime="startTime=$event"
                        v-on:endDate="endDate=$event"
                        v-on:endTime="endTime=$event"
                    ></date-time>
                </b-col>
                <b-col md="1">
                    <b-btn variant="primary" @click="getRewardList()">查詢</b-btn>
                </b-col>
            </b-row>
        </div>
        <div class="m-2">
            <b-table
                show-empty 
                bordered 
                :current-page="currentPage"
                :per-page="perPage"
                :items="rewardList.lists" 
                :fields="fields" 
                empty-text="無資料"
            >
                <template slot="id" slot-scope="row">
                    <div @click="getRewardDetails(row.value)">{{ row.value }}</div>
                </template>
                <template slot="bet_id" slot-scope="row">
                    <div @click="getTicketUrl(row.value)">{{ row.value }}</div>
                </template>
                <template slot="operator" slot-scope="row">{{ row.value || '系統派發'}}</template>
                <template slot="mid" slot-scope="row">
                    <div @click="getPlayerInfo(row.value)">{{ rewardList.players[row.value] }}</div>
                </template>
                <template slot="type" slot-scope="row">{{ row.value | toJpType }}</template>
                <template slot="status" slot-scope="row">
                    <div :class="statusTextColor(row.value)">{{ row.value | jpStatus }}</div>
                </template>
                <template slot="currency" slot-scope="row">{{ row.value }}</template>
                <template slot="win" slot-scope="row">{{ row.value | formatMoney(row.item.currency) }}</template>
                <template slot="received_at" slot-scope="row">{{ row.value | formatTime }}</template>
            </b-table>
            <pagination-fast
                :totalRows="totalRows"
                :rows="perPage"
                :currentPage="currentPage"
                v-on:changePage="changePage($event)"
            ></pagination-fast>
            <b-pagination :total-rows="totalRows" :per-page="perPage" v-model="currentPage" align="right" :limit="10" @change="changePage"/>
        </div>
    </div>
</template>

<script>
import ticketModal from '../ticket_modal';
import rewardDetailsModal from './reward_details_modal';
import playerInfo from '../player_info';
import DateTime from '../date_time'
import paginationFast from '../pagination_fast';

export default {
    components: {
        DateTime,
        paginationFast
    },
    created() {
        this.getCurrencyOption();
    },
    data() {
        return {
            fields: [
                {
                    key: "id",
                    label: "ID",
                    sortable: true,
                    class: 'clickable'
                },
                {
                    key: "bet_id",
                    label: "注單編號",
                    sortable: true,
                    class: 'text-center clickable'
                },
                {
                    key: "operator",
                    label: "派發者",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "mid",
                    label: "玩家",
                    sortable: true,
                    class: 'text-center clickable'
                },
                {
                    key: "type",
                    label: "類型",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "status",
                    label: "狀態",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "currency",
                    label: "幣別",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "win",
                    label: "贏得金額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "received_at",
                    label: "取獎時間",
                    sortable: true,
                    class: 'text-right'
                },
            ],
            id: '',
            rewardList: '',
            type: '',
            typeOptions: [
                {text: '全部', value: ''},
                {text: 'JP', value: 'jp'},
                {text: '紅包', value: 'red'}
            ],
            currency: '',
            currencyOptions: [],
            status: '',
            statusOptions: [
                {text: '全部', value: ''},
                {text: '未領取', value: '0'},
                {text: '已領取', value: '1'},
                {text: '已取消', value: '2'},
                {text: '錯誤', value: '3'}
            ],
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            currentPage: 1,
            perPage: 25
        }
    },
    computed: {
        totalRows() {
            if (!this.rewardList) {
                return 0;
            }
            return this.rewardList.lists.length;
        }
    },
    methods: {
        changePage($event) {
            this.currentPage = $event ? $event : this.currentPage;
            setTimeout(() => document.documentElement.scrollTop = 0, 100);
        },
        getRewardList() {
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            let url = `/jackpot/award_list?start_time=${totalStart}&end_time=${totalend}`;
            url += this.type ? `&type=${this.type}` : '';
            url += this.currency ? `&currency=${this.currency}` : '';
            url += this.status ? `&status=${this.status}` : '';
            url += this.id ? `&id=${this.id}` : '';
            let data = {
                players: [87],
                lists: [
                    {
                        id: 0,
                        bet_id: 0,
                        operator: 22,
                        mid: 87,
                        type: 9,
                        status: 0,
                        currency: 'TWD',
                        win: 90,
                        received_at: '2018.09.01'
                    }
                ]
            }
            this.rewardList = data;
            this.currentPage = 1;
            // this.$api.requestFetch(url, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.rewardList = res.data;
            //             this.currentPage = 1;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getRewardDetails(id) {
            let data = [];
            this.$modal.show(rewardDetailsModal, { rewardDetails: data }, { scrollable: true, width: "30%", height: "auto" })
            // this.$api.requestFetch(`/jackpot/award_detail?id=${id}`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$modal.show(rewardDetailsModal, { rewardDetails: res.data }, { scrollable: true, width: "30%", height: "auto" })
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getTicketUrl(id) {
            if(!id || id === 0) {
                return;
            }
            // this.$api.getTicketUrl(id)
            //     .then(e => this.$modal.show(ticketModal, { url: e }, { draggable: true, width: "50%", height: "auto" }))
            //     .catch(e => alert(e));
        },
        getPlayerInfo(mid) {
            let promises = [];
            promises.push(this.$api.getPlayerGame(mid, '', ''));
            promises.push(this.$api.getPlayerInfo(this.rewardList.players[mid]));
            Promise.all(promises)
                .then(e => this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' }))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!' + `(${res.msg})`));
        },
        getCurrencyOption() {
            let currency = config.currency;
            let keys    = Object.keys(currency);
            this.currencyOptions.push({text: '全部', value: ''});
            for (let i = 0, len = keys.length; i < len; i++) {
                this.currencyOptions.push({ value: keys[i], text: currency[keys[i]] });
            }
        },
        clearSelect() {
            this.type = '';
            this.currency = '';
            this.status = '';
        },
        statusTextColor(val) {
            switch(val) {
                case 0:
                    return 'text-success';
                case 1:
                    return 'text-primary';
                case 2:
                    return 'text-danger';
                case 3:
                    return 'text-danger';
                default:
                    return 'text-secondary';
            }
        },
    }
}
</script>

<style>

</style>



