<template>
    <div id="info-modal" class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">{{ title }}</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <pre>{{ content }}</pre>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">OK</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'title', 'content' ]
}
</script>

<style lang="scss">
#info-modal {
    pre { 
        white-space: pre-wrap; /* css-3 */
        white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
        white-space: -pre-wrap; /* Opera 4-6 */
        white-space: -o-pre-wrap; /* Opera 7 */
        word-wrap: break-word; /* Internet Explorer 5.5+ */
    }
}
</style>



