<template>
<modal name="modalAdd" width="30%" height="auto" scrollable @opened="show">
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">新增伺服器設定</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('modalAdd')">×</button>
        </header>
        <div class="modal-body">
            <form>
                伺服器名稱:
                <b-form-input type="text" v-model="addData.server" :disabled="disabled"></b-form-input>
                類型:
                <b-form-select :options="typeOptions" style="height:100%" v-model="addData.type" :disabled="disabled"></b-form-select>
                幣值:
                <b-form-group>
                    <b-form-checkbox-group v-model="addData.currency" :options="currencyOptions"></b-form-checkbox-group>
                </b-form-group>
                <b-form-group>
                    <template slot="label">
                            <b-btn class="material-icons mr-1" @click="addConfig" style="height:100%">add_box</b-btn>
                            設定:           
                    </template>
                    <b-row v-for="(item, key) in configKey" :key="key" align-v="center" class="mt-1">
                        <b-col md="6">
                            <b-input-group prepend="key">
                                <b-form-input type="text" v-model="configKey[key]"></b-form-input>
                            </b-input-group>                            
                        </b-col>
                        <b-col md="6">
                            <b-input-group prepend="value">
                                <b-form-input type="text" v-model="configValue[key]"></b-form-input>
                                <b-btn class="material-icons ml-1" @click="removeConfig(key)">clear</b-btn> 
                            </b-input-group>                                 
                        </b-col>
                    </b-row>
                </b-form-group>
            </form>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$modal.hide('modalAdd')">Cancel</b-btn>
            <b-btn @click="add">OK</b-btn>
        </footer>
    </div>
</modal>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
    name: "ModalAdd",
    props: ['targetServer', 'targetType'],
    created() {
        this.getCurrencyOption();
    },
    data() {
        return {
            addData: {
                server: "",
                type: "",
                currency: [],
                config: {}
            },
            disabled: false,
            typeOptions: [{ value: '1', text: '遊戲' }, { value: '2', text: '服務' }],
            currencyOptions: [],
            configKey: [],
            configValue: []
        };
    },
    methods: {
        show() {
            this.clearAddData();
            if(this.targetServer) {
                this.addData.server = this.targetServer;
                this.addData.type   = `${this.targetType}`;
                this.disabled = true;
            }
        },
        clearAddData() {
            this.addData = {
                server: "",
                currency: [],
                config: {}
            };
            this.configKey = [];
            this.configValue = [];
            this.disabled = false;
        },
        addConfig() {
            this.configKey.push("");
            this.configValue.push("");
        },
        removeConfig(key) {
            this.configKey.splice(key, 1);
            this.configValue.splice(key, 1);
        },
        add() {
            // if (!this.addData.server || this.addData.currency.length < 1 || this.configKey.length === 0) {
            //     this.dataEmptyError();
            //     return;
            // }
            // if (this.configKey.length > 0) {
            //     for (let i = 0, len = this.configKey.length; i < len; i++) {
            //         if(!this.configKey[i]) {
            //             this.dataEmptyError();
            //             return;
            //         }
            //     }
            // }
            for (let i = 0, len = this.configKey.length; i < len; i++) {
                this.addData.config[this.configKey[i]] = this.configValue[i];
            }
            this.$api.showModalInfo('新增結果', `新增成功`);
            // this.$api.requestFetch(`/server_config/add`, "POST", this.addData)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status === true) {
            //             this.$emit('refresh');
            //             this.$api.showModalInfo('新增結果', `新增成功`);
            //         } else {
            //             this.$api.showModalInfo('新增結果', `新增失敗`);
            //         }
            //     });
            this.clearAddData();
            this.$modal.hide('modalAdd');
        },
        dataEmptyError() {
            this.$api.showModalInfo('Error', `資料不得為空`);
            this.clearAddData();
            this.$modal.hide('modalAdd');
        },
        getCurrencyOption() {
            let currency = config.currency;
            let keys = Object.keys(currency);
            for (let i = 0, len = keys.length; i < len; i++) {
                this.currencyOptions.push({ value: keys[i], text: currency[keys[i]] });
            }
        }
    }
};
</script>

<style lang="scss">

</style>



