<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">玩家資訊</h5>
        </header>
        <div class="modal-body">
            <b-row class="m-1 border-bottom">
                <b-col>
                    <b-list-group flush>
                        <b-list-group-item class="d-flex justify-content-between">玩家 / 幣別: <span>{{ data.name }} / {{ data.currency }}</span></b-list-group-item>
                    </b-list-group>
                </b-col>
                <b-col>
                    <b-list-group flush>
                        <b-list-group-item class="d-flex justify-content-between">加入時間: <span>{{ data.add_time | formatTime }}</span></b-list-group-item>
                    </b-list-group>
                </b-col>
            </b-row>
            <b-row class="m-1 border-bottom">
                <b-col>
                    <b-list-group flush>
                        <b-list-group-item class="d-flex justify-content-between">首次下注時間: <span>{{ data.first_bet | formatTime }}</span></b-list-group-item>
                        <b-list-group-item class="d-flex justify-content-between">總局數: <span>{{ data.rounds }}</span></b-list-group-item>
                        <b-list-group-item class="d-flex justify-content-between">最大注額: <span>{{ data.max_bet | formatMoney(data.currency) }}</span></b-list-group-item>
                        <b-list-group-item class="d-flex justify-content-between">平均注額: <span>{{ data.avg_bet | formatMoney(data.currency) }}</span></b-list-group-item>
                        <b-list-group-item class="d-flex justify-content-between">最近遊玩IP: <span>{{ data.ip || '無資料' }}</span></b-list-group-item>
                    </b-list-group>
                </b-col>
                <b-col>
                    <b-list-group flush>
                        <b-list-group-item class="d-flex justify-content-between">最近下注時間: <span>{{ data.last_bet | formatTime }}</span></b-list-group-item>
                        <b-list-group-item class="d-flex justify-content-between">遊玩天數: <span>{{ data.play_days }}</span></b-list-group-item>
                        <b-list-group-item class="d-flex justify-content-between">最大贏得: <span>{{ data.max_win | formatMoney(data.currency) }}</span></b-list-group-item>
                        <b-list-group-item class="d-flex justify-content-between">平均贏得: <span>{{ data.avg_win | formatMoney(data.currency) }}</span></b-list-group-item>
                        <b-list-group-item class="d-flex justify-content-between">最近遊玩裝置: <span>{{ data.os || '無資料' }} / {{ data.browser_type || '無資料' }}</span></b-list-group-item>
                    </b-list-group>
                </b-col>
            </b-row>
            <b-row class="m-1">
                <b-col>
                    <b-list-group flush>
                        <b-list-group-item class="text-center">總注額: {{ data.sum_bet | formatMoney(data.currency) }}</b-list-group-item>
                    </b-list-group>
                </b-col>
                <b-col>
                    <b-list-group flush>
                        <b-list-group-item class="text-center">總贏得: {{ data.sum_win | formatMoney(data.currency) }}</b-list-group-item>
                    </b-list-group>
                </b-col>
                <b-col>
                    <b-list-group flush>
                        <b-list-group-item class="text-center">
                            <span :class="totalColor(data.sum_win - data.sum_bet)">
                                總輸贏: {{ data.sum_win - data.sum_bet | formatMoney(data.currency) }}
                            </span>
                        </b-list-group-item>
                    </b-list-group>
                </b-col>
            </b-row>
        </div>
        <footer class="modal-footer">
            <span class="text-danger">備註: 玩家資訊為創號至今之統計資料</span>
        </footer>    
        <player-table :name="data.name" :currency="data.currency"></player-table>
    </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import playerTable from './player_table';
const { mapGetters, mapActions } = createNamespacedHelpers('member_report');

export default {
    components: {
        playerTable
    },
    computed: {
        ...mapGetters({
            data: "getPlayerInfoData",
        })
    },
    methods: {
        totalColor(val) {
            if (val <= 0) {
                return 'text-danger';
            } else {
                return 'text-success';
            }
        }
    }
}
</script>

<style>

</style>



