<template>
    <b-row id="jackpot">
        <b-col md="2" class="sideMenu scroll sidebar-color sticky-top p-0 border">
            <b-list-group flush>
                <b-list-group-item variant="secondary" class="d-flex justify-content-between">
                    <h2>遙控送禮</h2>
                    <b-btn class="material-icons" @click="getAllInfo()" style="font-size: 24px; width: 50px">autorenew</b-btn>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        紅包基礎幣別<span>{{allInfo.red_std_currency}}</span>
                    </span>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        紅包餘額 <span>{{allInfo.red_balance}}</span><i class="material-icons clickable" v-b-toggle="'redBalance'">expand_more</i>
                    </span>
                    <b-collapse id="redBalance">
                        <b-list-group-item>
                            <b-input-group>
                                <b-input v-model="redBalance" @keyup.native.enter="addRedBalance()"></b-input>
                                <b-input-group-append>
                                    <b-btn @click="addRedBalance()">充值</b-btn>
                                </b-input-group-append>
                            </b-input-group>
                        </b-list-group-item>
                    </b-collapse>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        JP模式<toggle-button v-model="mode" @change="switchMode" :labels="{checked: '隨機', unchecked: '操作'}" 
                            :width="80" :height="30" :color="{checked: '#82c7eb', unchecked: '#7dce94'}" :sync="true"/>
                    </span>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        偽造JP<span>{{ allInfo.fake_on ? '啟動' : '關閉' }}</span>
                    </span>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        JP過期時間 <span>{{allInfo.jp_expire_sec | timeDuration}}</span>
                    </span>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        JP數值<i class="material-icons clickable" v-b-toggle="'jp_vals'">expand_more</i>
                    </span>
                    <b-collapse id="jp_vals">
                        <b-list-group-item v-for="(item, key) in allInfo.jp_vals" :key="key">
                            {{key}}
                            <b-list-group-item v-for="(item2, key2) in item" :key="key2">
                                <span>{{key2}}</span>{{item2}}
                            </b-list-group-item>
                        </b-list-group-item>
                        <b-btn @click="getNewVals()">更新</b-btn>
                    </b-collapse>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        JP貢獻比率<i class="material-icons clickable" v-b-toggle="'jp_provides'">expand_more</i>
                    </span>
                    <b-collapse id="jp_provides">
                        <b-list-group-item v-for="(item, key) in allInfo.jp_provides" :key="key" class="d-flex justify-content-between">
                            <span>{{key}}</span>{{item}}
                        </b-list-group-item>
                    </b-collapse>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        JP送獎權重<i class="material-icons clickable" v-b-toggle="'jp_percents'">expand_more</i>
                    </span>
                    <b-collapse id="jp_percents">
                        <b-list-group-item v-for="(item, key) in allInfo.jp_percents" :key="key" class="d-flex justify-content-between">
                            <span>{{key + 1}}.</span>{{item * 100}}%
                        </b-list-group-item>
                    </b-collapse>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        支援幣別<i class="material-icons clickable" v-b-toggle="'currencys'">expand_more</i>
                    </span>
                    <b-collapse id="currencys">
                        <b-list-group-item>
                            <span v-for="(item, key) in allInfo.currencys" :key="key">
                                {{item}}{{ key === allInfo.currencys.length - 1 ? '' : ',' }}
                            </span>
                        </b-list-group-item>
                    </b-collapse>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        匯率表<i class="material-icons clickable" v-b-toggle="'exchange_rates'">expand_more</i>
                    </span>
                    <b-collapse id="exchange_rates">
                        <b-list-group-item v-for="(item, key) in allInfo.exchange_rates" :key="key" class="d-flex justify-content-between">
                            <span>{{key}}</span>{{item}}
                        </b-list-group-item>
                        <b-btn variant="primary" @click="showRateChange">更改</b-btn>
                    </b-collapse>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        連線遊戲列表<i class="material-icons clickable" v-b-toggle="'clients'">expand_more</i>
                    </span>
                    <b-collapse id="clients">
                        <b-list-group-item v-for="(item, key) in allInfo.clients" :key="key">
                            <div>{{ item.name }}</div>
                            <div>({{ item.addtime }})</div>
                        </b-list-group-item>
                    </b-collapse>
                </b-list-group-item>
                <b-list-group-item>
                    <span class="d-flex justify-content-between">
                        偽造JP遊戲權重<i class="material-icons clickable" v-b-toggle="'fake_gids_weight'">expand_more</i>
                    </span>
                    <b-collapse id="fake_gids_weight">
                        <b-list-group-item v-for="(item, key) in allInfo.fake_gids_weight" :key="key" class="d-flex justify-content-between">
                            <span>{{key | toGidName}}</span>{{item}}
                        </b-list-group-item>
                        <b-btn variant="primary" @click="showFakeChange">更改</b-btn>
                    </b-collapse>
                </b-list-group-item>
            </b-list-group>
        </b-col>
        <b-col md="10" class="content">
            <b-tabs card>
                <b-tab title="送獎列表" class="p-0" active>
                    <reward-send :percents="allInfo.jp_percents"></reward-send>
                </b-tab>
                <b-tab title="得獎報表" class="p-0">
                    <reward-report></reward-report>
                </b-tab>
            </b-tabs>
        </b-col>
    </b-row>
</template>

<script>
import rewardSend from './reward_send';
import rewardReport from './reward_report';
import fakeModal from './fake_weight_modal';
import rateModal from './rate_modal';

export default {
    name: "Jackpot",
    components: {
        rewardSend,
        rewardReport
    },
    data() {
        return {
            allInfo: {},
            redBalance: 0,
            mode: false
        };
    },
    created: function () {
        this.getAllInfo();
    },
    methods: {
        getAllInfo() {
            let data = {
                red_std_currency: 'TWD',
                red_balance: 1000,
                fake_on: true,
                jp_expire_sec: 20000,
                jp_vals: {
                    TWD: [0.87, 0.5],
                    RMB: [0.87, 0.5]
                },
                jp_provides: {
                    TWD: 50,
                    RMB: 50
                },
                jp_percents: [
                    0.96, 0.5, 0.4
                ],
                currencys: [
                    'TWD', 'RMB'
                ],
                exchange_rates: {
                    TWD: 50,
                    RMB: 20
                },
                clients: [
                    {
                        name: 'A',
                        addtime: '2019.09.01'
                    },
                    {
                        name: 'B',
                        addtime: '2019.09.01'
                    }
                ],
                fake_gids_weight: {
                    '遊戲A': 0.5,
                    '遊戲B': 0.5
                }
            };
            this.allInfo = data;
            this.mode = true;
            // this.$api.requestFetch(`/jackpot`, "GET", '', false)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.allInfo = res.data;
            //             this.mode = res.data.jp_mode === "random" ? true : false;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getNewVals() {
            // this.$api.requestFetch(`/jackpot/vals`, "GET", '', false)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.allInfo.jp_vals = res.data;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        switchMode() {
            this.mode = !this.mode;
            // this.$api.requestFetch(`/jackpot/switch_mode `, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.mode = res.data === "random" ? true : false;
            //         } else {
            //             this.mode = !this.mode;
            //             this.$api.showModalInfo('ERROR', '模式轉換失敗' + `(${res.msg})`);
            //         }
            //     });
        },
        showRateChange() {
            let data = Object.assign({}, this.allInfo.exchange_rates);
            this.$modal.show(
                rateModal, 
                { data: data, refresh: this.updateRate }, 
                { scrollable: true, width: "30%", height: "auto" }
            );
        },
        showFakeChange() {
            let data = Object.assign({}, this.allInfo.fake_gids_weight);
            this.$modal.show(
                fakeModal, 
                { data: data, refresh: this.updateFake }, 
                { width: "60%", height: "auto" }
            );
        },
        updateFake(data) {
            this.allInfo.fake_gids_weight = data;
        },
        updateRate(data) {
            this.allInfo.exchange_rates = data;
        },
        addRedBalance() {
            if (!this.redBalance) {
                return;
            }
            let send = {
                type: 'red',
                amount: Number(this.redBalance)
            }
            this.redBalance = 0;
            this.$api.showModalInfo('SUCCESS', '加值成功');
            // this.$api.requestFetch(`/jackpot/add_balance`, "POST", send)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '加值成功');
            //             this.getAllInfo();
            //         } else {
            //             this.$api.showModalInfo('ERROR', '加值失敗' + `(${res.msg})`);
            //         }
            //     });
        }
    }
};
</script>

<style lang="scss">
#jackpot {
    text-align: left;
    margin: 0;
    .sideMenu {
        top: 3.5rem;
        height: calc(100vh - 3.5rem);
        z-index: 1;
        .btn {
            margin-bottom: 1%;
            width: 100%;
        }
    }
    .content {
        padding: 0;
    }
    .table {
        margin: 0;
        td {
            vertical-align: baseline;
        }
    }
}
</style>



