<template>
<b-card no-body id="agent-list-info">
    <b-row class="card-header m-0">
        <b-col md="10" class="align-middle">
            <b-input-group style="width:700px; height:100%">
                <b-input-group-prepend is-text>
                    <i class="material-icons align-middle">search</i>
                </b-input-group-prepend>
                <b-input-group-prepend>
                    <b-form-select :options="filterOptions" style="width:100%; height:100%" v-model="filterTarget" @change="filterWord = ''">
                        <template slot="first">
                                <option :value="null" disabled>選擇篩選對象</option>
                        </template>
                    </b-form-select>
                </b-input-group-prepend>
                <b-form-input v-show="shouldShow(0)" style="height:100%" v-model="filterWord" placeholder="Type to Search" :disabled="filterTarget === null"/>
                <b-form-select v-show="shouldShow(1)" :options="wordOptions" style="height:100%" v-model="filterWord">
                    <template slot="first">
                        <option :value="''" disabled>選擇篩選對象</option>
                    </template>
                </b-form-select>
                <b-input-group-append>
                    <b-btn :disabled="!filterWord" @click="filterWord = ''">Clear</b-btn>
                </b-input-group-append>
            </b-input-group>
        </b-col>
        <b-col md="2" class="text-right">
            <b-btn @click="refresh()" style="font-size: 18px">{{ refreshCount }}秒後更新</b-btn>
            <b-btn class="material-icons" @click="$emit('showAddModal')" style="font-size: 18px">add_box</b-btn>
        </b-col>
    </b-row>
    <b-table 
        show-empty 
        hover
        stacked="md" 
        :items="agentData.list" 
        :fields="fields" 
        :filter-function="filter" 
    >
        <template slot="id" slot-scope="row">{{row.value}}</template>
        <template slot="name" slot-scope="row">
            <b-btn @click="action(row.item.name, 'describe')" variant="link">{{row.value}}</b-btn>
        </template>
        <template slot="status" slot-scope="row"><span :class="statusTextColor(row.item.status)">{{row.value | status}}</span></template>
        <template slot="exec_mode" slot-scope="row"><span :class="modeTextColor(row.item.exec_mode)">{{row.value | mode}}</span></template>
        <template slot="up_time" slot-scope="row">{{ (Date.now() - row.value) | timeDuration(1000) }}</template>
        <template slot="cpu_use" slot-scope="row">{{row.value}}%</template>
        <template slot="mem_use" slot-scope="row">
            {{row.value}}
        </template>
        <template slot="instances" slot-scope="row">{{row.value}}</template>
        <template slot="restart_time" slot-scope="row">{{row.value}}</template>
        <template slot="options" slot-scope="row">
            <b-btn @click.stop="toggleDetails(row)" class="toggle-btn-color">
                <i class="material-icons align-middle">{{ row.detailsShowing ? 'expand_less' : 'expand_more' }}</i>
            </b-btn>
            <b-btn @click="checkModal(row.item.name, 'restart')" class="restart-btn-color" v-b-tooltip.hover title="重啟服務">
                <i class="material-icons align-middle">redo</i>
            </b-btn>
            <b-btn @click="checkModal(row.item.name, 'stop')" class="stop-btn-color" v-b-tooltip.hover title="停止服務">
                <i class="material-icons align-middle">block</i>
            </b-btn>
            <b-dropdown right v-b-tooltip.hover title="其他選項">
                <b-dropdown-item @click="checkModal(row.item.name, 'reload')">重新讀取</b-dropdown-item>
                <b-dropdown-item @click="checkModal(row.item.name, 'delete')">刪除服務</b-dropdown-item>
                <b-dropdown-item @click="$emit('showSendMsg', row.item.id)">傳送訊息</b-dropdown-item>
            </b-dropdown>
        </template>
        <template slot="row-details" slot-scope="row">
            <b-list-group flush v-if="row.item._showDetails">
                <b-row class="m-0">
                    <b-col>
                        <h4 class="text-center">自定義指標</h4>
                        <b-list-group-item v-for="(value, key) in row.item.monitors" :key="key" :class="updatedCheck(value.isUpdated)">
                            <span>{{ value.name }}:</span>{{ value.val }}
                        </b-list-group-item>
                    </b-col>
                    <b-col>
                        <h4 class="text-center">自定義事件</h4>
                        <div>
                            <b-btn 
                                v-for="(value, key) in row.item.actions" 
                                :key="key" 
                                class="mr-1" 
                                variant="primary" 
                                @click="showTrigger(row.item.id, value.name)"
                            >{{ value.name }}</b-btn>
                        </div>
                    </b-col>
                </b-row>
            </b-list-group>
        </template>
    </b-table>
</b-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import ModalCheck from '../modal/check';
import triggerModal from './modal_trigger';

export default {
    name: "ListInfo",
    data() {
        return {
            fields: [{
                    key: "id",
                    label: "ID",
                    sortable: true,
                    filterable: true
                },
                {
                    key: "name",
                    label: "服務名稱",
                    sortable: true,
                    filterable: true
                },
                {
                    key: "status",
                    label: "服務狀態",
                    sortable: true,
                    filterable: true
                },
                {
                    key: "exec_mode",
                    label: "執行模式",
                    sortable: true,
                    filterable: true
                },
                {
                    key: "up_time",
                    label: "啟動時間",
                    sortable: true
                },
                {
                    key: "cpu_use",
                    label: "CPU使用",
                    sortable: true
                },
                {
                    key: "mem_use",
                    label: "記憶體用量",
                    sortable: true
                },
                {
                    key: "instances",
                    label: "服務個數",
                    sortable: true
                },
                {
                    key: "restart_time",
                    label: "重啟次數",
                    sortable: true
                },
                {
                    key: "options",
                    label: "選項"
                }
            ],
            filterWord: null,
            filterTarget: null,
            triggerParam: '',
            refreshCount: 0,
            refreshInterval: ''
        };
    },
    created() {
        this.refreshCount = 10;
        this.refreshInterval = setInterval(() => {
            this.refreshCount--;
            if (this.refreshCount === 0) {
                this.refresh();
                this.refreshCount = 10;
            } 
        }, 1000);
    },
    beforeDestroy() {
        clearInterval(this.refreshInterval);
    },
    computed: {
        ...mapGetters({
            agentData: "getAgentData",
            target: "getTarget"
        }),
        filterOptions() {
            return this.fields.filter(f => f.filterable).map(f => {
                return {
                    text: f.label,
                    value: f.key
                };
            });
        },
        wordOptions() {
            if (this.filterTarget === "status") {
                return [{
                        text: "執行中",
                        value: "online"
                    },
                    {
                        text: "已停止",
                        value: "stopped"
                    }
                ];
            } else if (this.filterTarget === "exec_mode") {
                return [{
                        text: "單緒模式",
                        value: "fork_mode"
                    },
                    {
                        text: "群集模式",
                        value: "cluster_mode"
                    }
                ];
            } else {
                return [];
            }
        },
    },
    methods: {
        ...mapActions(["updateOne", "toggleDetails"]),
        updatedCheck(isUpdated) {
            if (isUpdated) {
                return 'd-flex justify-content-between ' + isUpdated;
            }
            return 'd-flex justify-content-between';
        },
        refresh(){
            // this.$emit('fresh');
        },
        showDetailModal(title, content){
            this.$emit('showDetailModal', {
                title: title,
                content: content
            })
        },
        checkModal(proc, act) {
            this.$modal.show(
                ModalCheck, 
                { title: '提示', content: `你確定要對${proc}進行${act}嗎?`, action: () => this.action(proc, act)}, 
                { scrollable: true, width: "30%", height: "auto" }
            );
        },
        action(proc, act) {
            // this.$api.requestFetch(`/agent/action?name=${this.target}&proc=${proc}&action=${act}`, "GET")    
            //     .catch(err => this.showModal("Error", err))
            //     .then(res => {
            //         if (!res.status) {
            //             this.$api.showModalInfo('Error', '動作執行失敗');
            //             return;
            //         }
            //         if (act === "describe") {
            //             this.showDetailModal("服務資訊", 'xxxoooxxx');
            //         } else {
            //             this.$api.showModalInfo('Success', '動作執行成功');
            //         }
            //         this.updateOne({
            //             data: res.data,
            //             act: act
            //         });
            //     });
            if (act === "describe") {
                this.showDetailModal("服務資訊", 'xxxoooxxx');
            } else {
                this.$api.showModalInfo('Success', '動作執行成功');
            }
        },
        showTrigger(id, action) {
            this.$modal.show(triggerModal, { target: this.target, id: id, action: action }, { width: "30%", height: "auto" })
        },
        describeTrans(data) {
            let fields = this.fields;
            let word = [];
            let returnData = [];
            for (let i = 0, len = data.length; i < len; i++) {
                for (let j = 0, len2 = fields.length; j < len2 - 1; j++) {
                    let val = data[i][fields[j]["key"]];
                    let label = fields[j]["label"];
                    if (fields[j]["key"] === 'up_time') {
                        let date = this.$options.filters.timeDuration(Date.now() - val, 1000);
                        word.push( `${label}: ${date}`);
                    } else {
                        word.push( `${label}: ${val}`);
                    }
                }
                word.push( `詳細資訊:`);
                returnData.push({
                    word: word,
                    detail: data[i].detail,
                    show: false
                });
                word = [];
            }
            return returnData;
        },
        statusTextColor(val) {
            switch (val) {
                case "online":
                    return 'text-success';
                case "stopped":
                    return 'text-warning';
                case "errored":
                    return 'text-danger';
                default:
                    return '';
            };
        },
        modeTextColor(val) {
            switch (val) {
                case "fork_mode":
                    return 'text-secondary';
                case "cluster_mode":
                    return 'text-primary';
                default:
                    return '';
            }
        },
        shouldShow(type) {
            let isSelect = this.filterTarget === "status" || this.filterTarget === "exec_mode";
            return type === 0 ? !isSelect : isSelect;
        },
        filter(item) {
            if (
                this.filterWord   === null ||
                this.filterWord   === ""   ||
                this.filterTarget === ""
            ) {
                return true;
            } else {
                if (`${item[this.filterTarget]}`.indexOf(this.filterWord, 0) !== -1) {
                    return true;
                }
            }
            return false;
        },
    }
};
</script>

<style lang="scss">
#agent-list-info {
    .toggle-btn-color {
        color: black;
        background-color: #93c0f1;
        &:hover {
            background-color: #526b86;
        }
    }
    .restart-btn-color {
        color: black;
        background-color: #f5cd55b3;
        &:hover {
            background-color: #d2b14eb3;
        }
    }
    .stop-btn-color {
        color: black;
        background-color: #dc3545c4;
        &:hover {
            background-color: #a92a36c4;
        }
    }
    .b-table-details {
        &:hover {
            background-color: #FFF!important;
        }
    }
    .table-updated1 {
        animation-name: updated1;
        animation-duration: 1.5s;
    }
    .table-updated2 {
        animation-name: updated2;
        animation-duration: 1.5s;
    }
    @keyframes updated1 {
        50%  { background-color: #ffdc747d; }
        70%  { background-color: #ffdc747d; }
    }
    @keyframes updated2 {
        50%  { background-color: #ffdc747d; }
        70%  { background-color: #ffdc747d; }
    }
}
</style>



