<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">批次修改</h5>
            <button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-form-group>
                <b-form-checkbox @change="toggleAll" v-model="selectAll" class="mb-3">{{ selectAll ? '清除' : '全選' }}</b-form-checkbox>
                <b-list-group>
                    <b-list-group-item 
                        button
                        v-for="(item, key) in gameList" :key="key" 
                        @click.stop.prevent="pushGid(item.gid)"
                        class="d-flex justify-content-between"
                    >
                        <b-form-checkbox :checked="gid.indexOf(item.gid) > -1">{{item.name}}</b-form-checkbox>
                        <div>
                            <span :class="textColor(item.status)">狀態: {{ item.status | gameListStatus }},</span>
                            <span :class="textColor(item.fun)">試玩: {{ item.fun | haveFun }}</span>
                        </div>
                    </b-list-group-item>
                </b-list-group>
            </b-form-group>
        </div>
        <footer class="modal-footer">
            <b-dropdown variant="link" no-caret>
                <template slot="button-content">
                    <span>設定: {{ set | groupSet }}</span>
                </template>
                <b-dropdown-item @click="set = 0">狀態下架</b-dropdown-item>
                <b-dropdown-item @click="set = 1">狀態開放</b-dropdown-item>
                <b-dropdown-item @click="set = 2">無試玩</b-dropdown-item>
                <b-dropdown-item @click="set = 3">有試玩</b-dropdown-item>
            </b-dropdown>
            <button type="button" class="btn btn-primary" @click="$emit('close')">取消</button>
            <button type="button" class="btn btn-primary" @click="edit()">更改</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'gameList', 'refresh' ],
    mounted() {
        let list = this.gameList;
        for (let i = 0, len = list.length; i < len; i++) {
            this.allGids.push(list[i].gid);
        }
    },
    data() {
        return {
            set: 0,
            gid: [],
            allGids: [],
            selectAll: false
        }
    },
    methods: {
        toggleAll(checked) {
            this.gid = checked ? this.allGids.slice(0) : [];
        },
        pushGid(gid) {
            let idx = this.gid.indexOf(gid);
            if (idx > -1) {
                this.gid.splice(idx, 1);
            } else {
                this.gid.push(gid);
            }
        },
        edit() {
            if (!this.gid.length) {
                this.$api.showModalInfo('ERROR', '遊戲不得為空');
                return;
            }
            let body = { gid: this.gid }
            if (this.set === 0 || this.set === 1) {
                body.status = this.set;
            } else {
                body.fun = this.set - 2;
            }
            // this.$api.requestFetch(`/game_list/edit`, "POST", body)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '更改成功');
            //             this.refresh();
            //             this.$emit('close');
            //         } else {
            //             this.$api.showModalInfo('ERROR', '更改失敗' + `(${res.msg})`);
            //             this.$emit('close');
            //         }
            //     });
            this.$api.showModalInfo('SUCCESS', '更改成功');
            // this.refresh();
            this.$emit('close');
        },
        textColor(val) {
            switch(val) {
                case 0:
                    return 'text-danger';
                case 1:
                    return 'text-success';
                default:
                    return '';
            }
        }
    }
}
</script>

<style>

</style>



