<template>
<div class="p-2">
    <b-table 
        show-empty 
        foot-clone 
        bordered 
        hover
        stacked="md"
        small
        :items="items" 
        :fields="fields" 
        :sort-by.sync="sort" 
        :sort-desc.sync="sortDesc"
        empty-text="無資料" 
    >
        <template slot="gid" slot-scope="row">{{ row.value }}</template>
        <template slot="gid_name" slot-scope="row">{{ row.item.gid | toGidName }}</template>
        <template slot="rounds" slot-scope="row">
            <div @click="getRounds(row.item)">{{ row.value }}</div>
        </template>
        <template slot="players" slot-scope="row">
            <div @click="getPlayers(row.item)">{{ row.value }}</div>
        </template>
        <template slot="bet" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
        <template slot="win" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
        <template slot="result" slot-scope="row">
            <span :class="resultTextColor(row.value)">{{ row.value | formatMoney(currency) }}</span>
        </template>
        <template slot="expected" slot-scope="row">
            <span :style="expectedTextColor(toExpectdValue(row))">{{ toExpectdValue(row) }}%</span>
        </template>
        <template slot="profit" slot-scope="row">
            <span :class="profitTextColor(toProfit(row))">{{ toProfit(row) }}%</span>
        </template>
        <template slot="HEAD_bet" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
        <template slot="HEAD_win" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
        <template slot="HEAD_result" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
        <template slot="FOOT_gid" slot-scope="data">Total:</template>
        <template slot="FOOT_gid_name" slot-scope="data"></template>
        <template slot="FOOT_rounds" slot-scope="data">
            {{ total(data.column) }}
        </template>
        <template slot="FOOT_players" slot-scope="data">
            {{ total(data.column) }}
        </template>
        <template slot="FOOT_bet" slot-scope="data">
            {{ total(data.column) | formatMoney(currency) | footCurrencyText(currency) }}
        </template>
        <template slot="FOOT_win" slot-scope="data">
            {{ total(data.column) | formatMoney(currency) | footCurrencyText(currency) }}
        </template>
        <template slot="FOOT_result" slot-scope="data">
            <span :class="resultTextColor(total(data.column))">
                {{ total(data.column) | formatMoney(currency) | footCurrencyText(currency) }}
            </span>
        </template>
        <template slot="FOOT_expected" slot-scope="data">
            <span :style="expectedTextColor(averExpected())">
                {{ averExpected() | averWord }}
            </span>
        </template>
        <template slot="FOOT_profit" slot-scope="data">
            <span :class="profitTextColor(averProfit())">
                {{ averProfit() | averWord }}
            </span>
        </template>
    </b-table>  
</div>
</template>
    
<script>
import { createNamespacedHelpers } from 'vuex';

const { mapGetters, mapActions } = createNamespacedHelpers('game_report');

export default {
    name: "list",
    data() {
        return {
            fields: [
                {
                    key: "gid",
                    label: "Gid",
                    sortable: true,
                    class: 'text-center gid-width',
                },
                {
                    key: "gid_name",
                    label: "遊戲名稱",
                    sortable: true
                },
                {
                    key: "rounds",
                    label: "局數",
                    sortable: true,
                    class: 'text-center clickable'
                },
                {
                    key: "players",
                    label: "遊玩人次",
                    sortable: true,
                    class: 'text-center clickable'
                },
                {
                    key: "bet",
                    label: "下注金額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "win",
                    label: "贏得金額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "result",
                    label: "總輸贏",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "expected",
                    label: "期望值",
                    class: 'text-right'
                },
                {
                    key: "profit",
                    label: "獲利",
                    class: 'text-right'
                },
            ],
            sort: 'result',
            sortDesc: true
        }
    },
    computed: {
        ...mapGetters({
            items: "getItems",
            currency: "getCurrency",
            startTime: "getStartTime",
            endTime: "getEndTime"
        })
    },
    methods: {
        getPlayers(item) {
            let body = {
                gid: item.gid,
                currency: this.currency,
                start_time: this.startTime,
                end_time: this.endTime,
            };
            let data = [
                {
                    name: '人員1',
                    rounds: 10,
                    bet: 100,
                    win: 200,
                    result: 100
                },
                {
                    name: '人員2',
                    rounds: 10,
                    bet: 200,
                    win: 200,
                    result: 0
                },
                {
                    name: '人員3',
                    rounds: 10,
                    bet: 300,
                    win: 200,
                    result: -100
                }
            ]
            this.showPlayersModal(`${config.gid_name[item.gid]}遊玩人次`, data);
            // this.$api.requestFetch(`/report/game/players`, "POST", body)
            //     .catch(e => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.showPlayersModal(`${config.gid_name[item.gid]}遊玩人次`, res.data);
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        getRounds(item) {
            let body = {
                gid: item.gid,
                currency: this.currency,
                start_time: this.startTime,
                end_time: this.endTime,
                page: 0,
                rows: 25
            };
            let data = {
                list: [
                {
                    id: 0,
                    time: '2019.09.09',
                    name: '人員1',
                    bet: 10,
                    win: 10,
                    result: 0
                },
                {
                    id: 1,
                    time: '2019.09.10',
                    name: '人員2',
                    bet: 10,
                    win: 10,
                    result: 0
                }
            ]
            }
            this.showRoundsModal(`${config.gid_name[item.gid]}局數`, data, body);
            // this.$api.requestFetch(`/report/game/rounds`, "POST", body)
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.showRoundsModal(`${config.gid_name[item.gid]}局數`, res.data, body);
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        },
        expectedTextColor(val) {
            if (val >= 98) {
                return 'color: #dc3545';
            } else if (val <= 94){
                return 'color: #93c47d';
            } else {
                return 'color: #45818e'
            }
        },
        profitTextColor(val) {
            if (val >= 0) {
                return 'text-danger';
            } else {
                return 'text-success';
            }
        },
        total(key) {
            let total = 0;
            for (let i = 0, len = this.items.length; i < len; i++) {
                total += this.items[i][key];
            }
            return total;
        },
        toExpectdValue(val) {
            if (!val) {
                return val;
            }
            if (val.item.win === 0) {
                return 0;
            } else if (val.item.bet === 0) {
                return 100;
            } else {
                return (val.item.win / val.item.bet * 100).toFixed(2);
            }
        },
        averExpected() {
            if (this.items.length === 0) {
                return '';
            }
            let win = this.total('win');
            let bet = this.total('bet');
            let total = 0;
            if (win === 0) {
                total += 0;
            } else if (bet === 0) {
                total += 100;
            } else {
                total += win / bet * 100;
            }
            return total.toFixed(2);
        },
        toProfit(val) {
            if (!val) {
                return val;
            }
            return (100 - this.toExpectdValue(val)).toFixed(2);
        },
        averProfit() {
            if (this.items.length === 0) {
                return '';
            }
            return (100 - this.averExpected()).toFixed(2);
        },
        showPlayersModal(title, content) {
            this.$emit('showPlayersModal', {
                title: title,
                content: content
            })
        },
        showRoundsModal(title, content, sendData) {
            this.$emit('showRoundsModal', {
                title: title,
                content: content,
                sendData: sendData
            })
        }
    },
    filters: {
        averWord(val) {
            if (!val) {
                return '';
            }
            return `平均: ${val}%`;
        }
    }
}
</script>


