<template>
<div id="ticket-list">
    <b-card no-body>
        <b-card-header>
            <b-row align-v="center">
                <b-col md="2">
                    <span class="font-weight-bold" style="font-size: 30px">注單查詢</span>
                </b-col>
                <b-col md="7">
                    <date-time
                        v-on:startDate="startDate=$event"
                        v-on:startTime="startTime=$event"
                        v-on:endDate="endDate=$event"
                        v-on:endTime="endTime=$event"
                    ></date-time>
                </b-col>
                <b-col md="3" class="text-center">
                    <toggle-button v-model="isEngineerMode" :labels="{checked: '工程模式', unchecked: '一般模式'}" :width="100" :height="38" class="mb-0"/>
                    <b-btn v-b-toggle.collapse>進階選項</b-btn>
                    <b-btn variant="primary" @click="request">查詢</b-btn>
                </b-col>
            </b-row>
            <b-collapse id="collapse" class="mt-2">
                <b-card>
                    <b-row>
                        <b-col>
                            <b-form-group class="mt-2" label="幣別查詢">
                                <b-form-checkbox v-model="allSelected" @change="e => selectAll(e)">{{ allSelected ? '清除' : '全選' }}</b-form-checkbox>
                                <b-form-checkbox-group v-model="currency" :options="currencyOptions">
                                </b-form-checkbox-group>
                            </b-form-group>
                        </b-col>
                        <b-col>
                            <b-form-group class="mt-2">
                                <b-input-group prepend="注單編號" class="mt-2">
                                    <b-form-input v-model="id" type="number"></b-form-input>
                                    <b-input-group-append>
                                        <b-btn :disabled="!id" @click="id = ''">Clear</b-btn>
                                    </b-input-group-append>
                                </b-input-group>
                            </b-form-group>
                            <b-form-group class="mt-2">
                                <b-input-group prepend="玩家名稱" class="mt-2">
                                    <b-form-input v-model="name"></b-form-input>
                                    <b-input-group-append>
                                        <b-btn :disabled="!name" @click="name = ''">Clear</b-btn>
                                    </b-input-group-append>
                                </b-input-group>
                            </b-form-group>
                        </b-col>
                        <b-col>
                            <b-form-group class="mt-2">
                                <b-input-group class="mt-2">
                                    <b-input-group-prepend is-text>
                                        倍率介於
                                    </b-input-group-prepend>
                                    <b-form-input v-model="winRateGreater" type="number" class="text-center" min="0"></b-form-input>
                                    <b-input-group-prepend is-text>~</b-input-group-prepend>
                                    <b-form-input v-model="winRateLess" type="number" class="text-center" min="0"></b-form-input>
                                </b-input-group>
                            </b-form-group>
                            <b-form-group class="mt-2">
                                <b-input-group class="mt-2">
                                    <b-input-group-prepend is-text>
                                        贏得介於
                                    </b-input-group-prepend>
                                    <b-form-input v-model="winGreater" type="number" class="text-center"></b-form-input>
                                    <b-input-group-prepend is-text>~</b-input-group-prepend>
                                    <b-form-input v-model="winLess" type="number" class="text-center"></b-form-input>
                                </b-input-group>
                            </b-form-group>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <b-form-group class="mt-2" label="遊戲查詢">
                                <game-select v-on:gidChange="gid=$event"></game-select>
                            </b-form-group>
                        </b-col>
                    </b-row>
                </b-card>
            </b-collapse>
        </b-card-header>
        <b-card-body class="mt-2">
            <b-table show-empty :items="items" :fields="fields" bordered empty-text="無資料">
                <template slot="id" slot-scope="row">
                    <div @click="getTicketUrl(row.item)">{{ row.value }}</div>
                </template>
                <template slot="type" slot-scope="row">{{ row.value | betType }}</template>
                <template slot="time" slot-scope="row">{{ row.value | formatTime}}</template>
                <template slot="gid" slot-scope="row">{{ row.value | toGidName }}</template>
                <template slot="currency" slot-scope="row">{{ row.value }}</template>
                <template slot="name" slot-scope="row">
                    <div @click="getPlayerInfo(row.item)" id="test">{{ row.value }}</div>
                </template>
                <template slot="bet" slot-scope="row">{{ row.value | formatMoney(row.item.currency) | footCurrencyText(row.item.currency) }}</template>
                <template slot="win" slot-scope="row">{{ row.value | formatMoney(row.item.currency) | footCurrencyText(row.item.currency) }}</template>
                <template slot="result" slot-scope="row">
                    <span :class="resultTextColor(row.value)">
                        {{ row.value | formatMoney(row.item.currency) | footCurrencyText(row.item.currency) }}
                    </span>
                </template>
            </b-table>
            <pagination-fast
                :totalRows="totalRows"
                :rows="per_page"
                :currentPage="currentPage"
                v-on:changePage="requestPage"
            ></pagination-fast>
            <b-pagination :total-rows="totalRows" :per-page="per_page" v-model="currentPage" align="right" :limit="10" @change="requestPage"/>
        </b-card-body>
    </b-card>
</div>
</template>

<script>
import DateTime from '../date_time'
import GameSelect from '../game_report/game_select';
import playerInfo from '../player_info';
import ticketModal from '../ticket_modal';
import engineerTicket from './engineer_ticket';
import paginationFast from '../pagination_fast';

export default {
    name: "ticket-list",
    components: {
        DateTime,
        playerInfo,
        GameSelect,
        paginationFast
    },
    created() {
        this.getCurrencyOption();
    },
    data() {
        return {
            currency: [],
            currencyOptions: [],
            gid: [],
            allSelected: false,
            id: '',
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            totalRows: 0,
            per_page: 25,
            currentPage: 1,
            winRateGreater: '',
            winRateLess: '',
            winGreater: '',
            winLess: '',
            sendData: '',
            name: '',
            isEngineerMode: false,
            items: [],
            fields: [
                {
                    key: "id",
                    label: "注單編號",
                    class: 'clickable'
                },
                {
                    key: "type",
                    label: "注單類型",
                    class: 'text-center'
                },
                {
                    key: "time",
                    label: "下注時間",
                    class: 'text-center'
                },
                {
                    key: "currency",
                    label: "幣別",
                    class: 'text-center'
                },
                {
                    key: "gid",
                    label: "遊戲名稱",
                    class: 'text-center'
                },
                {
                    key: "name",
                    label: "玩家名稱",
                    class: 'text-center clickable'
                },
                {
                    key: "bet",
                    label: "下注金額",
                    class: 'text-right'
                },
                {
                    key: "win",
                    label: "贏得金額",
                    class: 'text-right'
                },
                {
                    key: "result",
                    label: "總輸贏",
                    class: 'text-right'
                }
            ],
            modalInfo: {
                title: "",
                content: ""
            },
        };
    },
    computed: {

    },
    methods: {
        request() {
            this.items = [];
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            let body = {
                start_time: totalStart,
                end_time: totalend,
                currency: this.currency,
                gid: this.gid,
                win_rate_greater : this.winRateGreater,
                win_rate_less : this.winRateLess,
                win_greater : this.winGreater,
                win_less : this.winLess,
                id: this.id,
                name: this.name,
                page: 0,
                rows: this.per_page
            };
            this.sendData = body;
            this.currentPage = 1;
            let data = {
                total: 3,
                list: [
                    {
                        id: 0,
                        type: 1,
                        time: '2018.09.09',
                        currency: 'TWD',
                        gid: 2202,
                        name: '人員1',
                        bet: 100,
                        win: 100
                    },
                    {
                        id: 0,
                        type: 1,
                        time: '2018.09.09',
                        currency: 'TWD',
                        gid: 2202,
                        name: '人員1',
                        bet: 100,
                        win: 100
                    },
                    {
                        id: 0,
                        type: 1,
                        time: '2018.09.09',
                        currency: 'TWD',
                        gid: 2202,
                        name: '人員1',
                        bet: 100,
                        win: 100
                    }
                ]
            }
            this.totalRows = data.total;
            if (data.list.length === 0) {
                this.$api.showModalInfo('Error', `無資料`);
            }
            this.addResult(data.list);
            this.items = data.list;
            // this.$api.requestFetch(`/report/ticket`, "POST", body)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.currentPage = 1;
            //             this.totalRows = res.data.total;
            //             if (res.data.list.length === 0) {
            //                 this.$api.showModalInfo('Error', `無資料`);
            //             }
            //             this.addResult(res.data.list);
            //             this.items = res.data.list;
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        requestPage(page) {
            // this.sendData.page = page - 1;
            // this.currentPage = page;
            // this.$api.requestFetch(`/report/ticket`, "POST", this.sendData)
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.addResult(res.data.list);
            //             this.items = res.data.list;
            //             document.documentElement.scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        getTicketUrl(item) {
            // if (this.isEngineerMode) {
            //     this.$api.requestFetch(`/report/ticket_detail?id=${item.id}`, "GET")
            //         .catch(err => alert(err)) 
            //         .then(res => {
            //             if (res.status) {
            //                 this.$modal.show(engineerTicket, { title: '工程模式注單', content: res.data}, 
            //                     { width: '90%', height: 'auto', scrollable: true});
            //             } else {
            //                 this.$api.showModalInfo('Error', `查詢失敗`);
            //             }
            //         });
            // } else {
            //     this.$api.getTicketUrl(item.id)
            //         .then(e => this.$modal.show(ticketModal, { url: e }, { draggable: true, width: "50%", height: "auto" }))
            //         .catch(e => alert(e));
            // }

        },
        getPlayerInfo(item) {
            let promises = [];
            promises.push(this.$api.getPlayerGame(item.mid, '', ''));
            promises.push(this.$api.getPlayerInfo(item.name));
            Promise.all(promises)
                .then(e => this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' }))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!'));
        },
        addResult(list) {
            for (let i = 0, len = list.length; i < len; i++) {
                list[i].result = list[i].win - list[i].bet;
            }
        },
        selectAll(checked) {
            this.allSelected = checked;
            this.currency = checked ? this.currencyOptions.slice(0) : [];
        },
        getCurrencyOption() {
            let currency = config.currency;
            let keys    = Object.keys(currency);
            for (let i = 0, len = keys.length; i < len; i++) {
                this.currencyOptions.push(currency[keys[i]]);
            }
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        },
        resetModal() {
            this.modalInfo.title = "";
            this.modalInfo.content = "";
        }
    }
}
</script>



