<template>
    <div>
        <b-row align-v="center" class="card-header">
            <b-col md="2">
                <span class="font-weight-bold" style="font-size: 30px">遊戲分析</span>
            </b-col>
            <b-col md="2">
                <b-input-group prepend="幣別">
                    <b-form-select v-model="currency" :options="currencyOptions"/>
                </b-input-group>
            </b-col>
            <b-col md="6">
                <date-time
                    :monthFirst="true"
                    v-on:startDate="startDate=$event"
                    v-on:startTime="startTime=$event"
                    v-on:endDate="endDate=$event"
                    v-on:endTime="endTime=$event"
                ></date-time>
            </b-col>
            <b-col md="2" class="text-right">
                <b-btn v-b-toggle.collapse class="ml-2">進階搜尋</b-btn>
                <b-btn variant="primary" @click="getData()">查詢</b-btn>
            </b-col>
            <b-col md="12">
                <div>{{titleDays}}</div>
                <div>{{titleCurrency}}</div>
                <div>{{titleGames}}</div>
                <div>{{titleTotals}}</div>
            </b-col>
            <b-col md="12">
                <b-collapse id="collapse">
                    <b-card>
                        <b-row>
                            <b-col>
                                <b-form-group label="注單類型">
                                    <b-form-checkbox-group v-model="betType" :options="betTypeOptions">
                                    </b-form-checkbox-group>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-col>
                                <b-form-group class="mt-2" label="遊戲查詢">
                                    <game-select v-on:gidChange="gid=$event"></game-select>
                                </b-form-group>
                            </b-col>
                        </b-row>
                    </b-card>
                </b-collapse>
            </b-col>
        </b-row>
        <div>
            <div v-if="data_win_bet.length" class="card-header m-0">
                <span class="font-weight-bold" style="font-size: 30px">下注贏得</span>
            </div>
            <dual-line-chart
                :data="data_win_bet"
                :x="'date'"
                :y="'value'"
                :y2="'rtp'"
                :height="600"
                :padding="['10%', '10%']"
            >
            </dual-line-chart>
            <div v-if="data_bet.length" class="card-header m-0">
                <span class="font-weight-bold" style="font-size: 30px">下注分佈</span>
            </div>
            <bar-chart
                :data="data_bet"
                :x="'bet'"
                :y="'total'"
                :height="600"
                :padding="['10%', '10%']"
            >
            </bar-chart>
        </div>
    </div>
</template>

<script>
import DateTime from '../date_time'
import GameSelect from '../game_report/game_select';
import BarChart from '../chart/bar_chart';
import DualLineChart from '../chart/dual_line_chart';

export default {
    components: {
        GameSelect,
        DateTime,
        BarChart,
        DualLineChart
    },
    created() {
        this.getCurrencyOption();
    },
    data() {
        return {
            titleDays: '',
            titleCurrency: '',
            titleGames: '',
            titleTotals: '',
            gid: [],
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            betType: [1],
            // betTypeOptions: [
            //     { text: 'Normal', value: 1 },
            //     { text: 'Bonus', value: 2 },
            //     { text: 'Precharge', value: 3 },
            //     { text: 'JP', value: 4 }
            // ],
            betTypeOptions: Object.keys(config.betTypes || { }).map( (val) => ({ text: config.betTypes[val], value: Number(val) }) ),
            currency: 'TWD',
            currencyOptions: [],
            data_win_bet: [],
            data_bet: [],
        }
    },
    methods: {
        getData() {
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            let send = { 
                currency: this.currency, 
                gid: this.gid ,
                start_time: totalStart,
                end_time: totalend,
                bet_type: this.betType
            };
            let data = [
                {
                    date: '2018-09-01',
                    bet: 200,
                    win: 200,
                    rtp: 1
                },
                {
                    date: '2018-09-02',
                    bet: 100,
                    win: 200,
                    rtp: 0.2
                },
                {
                    date: '2018-09-03',
                    bet: 0,
                    win: 200,
                    rtp: 0.95
                },
                {
                    date: '2018-09-04',
                    bet: 50,
                    win: 200,
                    rtp: 0.97
                },
            ]
            let dv = new DataSet.View().source(data);
            dv.transform({
                type: 'fold',
                fields: ['bet', 'win'],
                key: 'key',
                value: 'value',
            });
            this.data_win_bet = dv.rows;
            let total = 0;
            let bet = [
                {
                    bet: 10,
                    total: 100
                },
                {
                    bet: 30,
                    total: 300
                },
                {
                    bet: 50,
                    total: 500
                },
            ]
            for (let i = 0, len = bet.length; i < len; i++) {
                bet[i].bet = `${bet[i].bet}`;
                total += bet[i].total;
            }
            this.data_bet = bet;
            let games = this.$options.filters.toGidNameString(this.gid);
            games = games === '無' ? '全部' : games;
            this.titleDays = `時間:${totalStart}~${totalend}`;
            this.titleCurrency = `幣值: ${this.currency}`;
            this.titleGames = `遊戲: ${games}`;
            this.titleTotals = `資料量: ${total}筆`;
            // this.$api.requestFetch(`/analysis/win_bet`, "POST", send)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             let dv = new DataSet.View().source(res.data.win_bet);
            //             dv.transform({
            //                 type: 'fold',
            //                 fields: ['bet', 'win'],
            //                 key: 'key',
            //                 value: 'value',
            //             });
            //             this.data_win_bet = dv.rows;
            //             let total = 0;
            //             for (let i = 0, len = res.data.bet.length; i < len; i++) {
            //                 res.data.bet[i].bet = `${res.data.bet[i].bet}`;
            //                 total += res.data.bet[i].total;
            //             }
            //             this.data_bet = res.data.bet;
            //             let games = this.$options.filters.toGidNameString(this.gid);
            //             games = games === '無' ? '全部' : games;
            //             this.titleDays = `時間:${totalStart}~${totalend}`;
            //             this.titleCurrency = `幣值: ${this.currency}`;
            //             this.titleGames = `遊戲: ${games}`;
            //             this.titleTotals = `資料量: ${total}筆`;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getCurrencyOption() {
            let currency = config.currency;
            let keys    = Object.keys(currency);
            for (let i = 0, len = keys.length; i < len; i++) {
                this.currencyOptions.push({ value: keys[i], text: currency[keys[i]] });
            }
        },
    }
}
</script>

<style>

</style>



