<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">分組設定</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-list-group>
                <b-list-group-item 
                    v-for="(item, key) in data" :key="key" 
                    class="d-flex justify-content-between"
                >
                    <span>{{item.property}}:</span>
                    <span>{{item.value}}</span>
                </b-list-group-item>
            </b-list-group>
            <span v-if="!data.length" class="ml-4">該分組無設定</span>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">OK</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'data' ]
}
</script>

<style>

</style>



