<template>
    <div>
        <b-row align-v="center" class="card-header">
            <b-col md="2">
                <span class="font-weight-bold" style="font-size: 30px">玩家分析</span>
            </b-col>
            <b-col md="6">
                <date-time
                    :monthFirst="true"
                    v-on:startDate="startDate=$event"
                    v-on:startTime="startTime=$event"
                    v-on:endDate="endDate=$event"
                    v-on:endTime="endTime=$event"
                ></date-time>
            </b-col>
            <b-col offset="3" md="1">
                <b-btn variant="primary" @click="getOs()">查詢</b-btn>
            </b-col>
        </b-row>
        <div>
            <div class="card-header m-0">
                <span class="font-weight-bold" style="font-size: 30px">裝置分析</span>
                <div>資料量: {{total}}筆</div>
            </div>
            <b-row class="m-0">
                <b-col md="4" class="p-0">
                    <pie :title="'裝置資訊'" :data="data_device"></pie>
                </b-col>
                <b-col md="4" class="p-0">
                    <pie :title="'系統資訊'" :data="data_os"></pie>
                </b-col>
                <b-col md="4" class="p-0">
                    <pie :title="'系統瀏覽器資訊'" :data="data_os_browser"></pie>
                </b-col>
            </b-row>
        </div>
    </div>
</template>

<script>
import DateTime from '../date_time';
import pie from '../chart/pie_chart';

export default {
    components: {
        DateTime,
        pie
    },
    computed: {
        totalStart() {
            return `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
        },
        totalEnd() {
            return `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
        },
        total() {
            let total = 0;
            for (let i = 0, len = this.data_device.length; i < len; i++) {
                total += this.data_device[i].total;
            }
            return total;
        }
    },
    data() {
        return {
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            data_device: [],
            data_os: [],
            data_os_browser: []
        }
    },
    methods: {
        getOs() {
            let device = [
                {
                    total: 70,
                    name: 'desktop',
                },
                {
                    total: 30,
                    name: 'mobile',
                }
            ];
            let os = [
                {
                    total: 25,
                    name: 'android',
                },
                {
                    total: 25,
                    name: 'ios',
                },
                {
                    total: 50,
                    name: 'windows',
                }
            ];
            let os_browser = [
                {
                    total: 52,
                    name: 'chrome',
                },
                {
                    total: 20,
                    name: 'safari',
                },
                {
                    total: 28,
                    name: 'ios-chrome',
                }
            ];
            let dv1 = new DataSet.View().source(device);
            let dv2 = new DataSet.View().source(os);
            let dv3 = new DataSet.View().source(os_browser);
            dv1.transform({ type: 'percent', field: 'total', dimension: 'name', as: 'percent' });
            dv2.transform({ type: 'percent', field: 'total', dimension: 'name', as: 'percent' });
            dv3.transform({ type: 'percent', field: 'total', dimension: 'name', as: 'percent' });
            this.data_device = dv1.rows;
            this.data_os = dv2.rows;
            this.data_os_browser = dv3.rows;
            // this.$api.requestFetch(`/analysis/os?start_time=${this.totalStart}&end_time=${this.totalEnd}`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             let dv1 = new DataSet.View().source(res.data.device);
            //             let dv2 = new DataSet.View().source(res.data.os);
            //             let dv3 = new DataSet.View().source(res.data.os_browser);
            //             dv1.transform({ type: 'percent', field: 'total', dimension: 'name', as: 'percent' });
            //             dv2.transform({ type: 'percent', field: 'total', dimension: 'name', as: 'percent' });
            //             dv3.transform({ type: 'percent', field: 'total', dimension: 'name', as: 'percent' });
            //             this.data_device = dv1.rows;
            //             this.data_os = dv2.rows;
            //             this.data_os_browser = dv3.rows;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            // });
        }
    }
}
</script>

<style>

</style>



