import Vue from 'vue'
import types from './types'

export const setAccount = ({ commit }, data) => {
    commit(types.SETACCOUNT, data);
}

export const setItems = ({ commit }, data) => {
    commit(types.SETITEMS, data);
}

export const updateItems = ({ commit }, data) => {
    commit(types.UPDATEITEMS, data);
}


