<template>
    <b-navbar id="my-bar" toggleable="md" type="dark" v-show="isLogin" sticky variant="dark">
        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
        <b-collapse id="nav-collapse" is-nav>
            <b-navbar-nav>
                <b-nav-item :to="{path: './home' }">
                    <i class="material-icons">home</i>主頁
                </b-nav-item>
                <b-nav-item :to="{path: './game_list'}">
                    <i class="material-icons">videogame_asset</i>遊戲管理
                </b-nav-item>
                <b-nav-item-dropdown :class="isActive(reportPath)" style="text-align: center;">
                    <template slot="text"><i class="material-icons">attach_money</i>報表</template>
                    <b-dropdown-item :to="{path: './game_report'}">遊戲報表</b-dropdown-item>
                    <b-dropdown-item :to="{path: './member_report'}">玩家報表</b-dropdown-item>
                    <b-dropdown-item :to="{path: './member_day_report'}">玩家日報表</b-dropdown-item>
                    <b-dropdown-item :to="{path: './profit_loss_report'}">盈虧報表</b-dropdown-item>
                </b-nav-item-dropdown>
                <b-nav-item-dropdown :class="isActive(memberPath)">
                    <template slot="text"><i class="material-icons">people</i>玩家</template>
                    <b-dropdown-item :to="{path: './member_list'}">玩家管理</b-dropdown-item>
                    <b-dropdown-item :to="{path: './member_group'}">玩家分組</b-dropdown-item>
                    <b-dropdown-item :to="{path: './member_logs'}">遊玩記錄</b-dropdown-item>
                </b-nav-item-dropdown>
                <b-nav-item-dropdown :class="isActive(analysisPath)">
                    <template slot="text"><i class="material-icons">bar_chart</i>分析</template>
                    <b-dropdown-item :to="{path: './game_analysis'}">遊戲分析</b-dropdown-item>
                    <b-dropdown-item :to="{path: './member_analysis'}">玩家分析</b-dropdown-item>
                </b-nav-item-dropdown>
                <b-nav-item :to="{path: './ticket'}">
                    <i class="material-icons">search</i>注單查詢
                </b-nav-item>
                <b-nav-item :to="{path: './jackpot'}" v-if="showRouter('/jackpot')">
                    <i class="material-icons">card_giftcard</i>遙控送禮
                </b-nav-item>
                <b-nav-item-dropdown v-if="showRouter('/account')" :class="isActive(adminPath)">
                    <template slot="text"><i class="material-icons">person</i>管理員</template>
                    <b-dropdown-item :to="{path: './account'}">後台帳號管理</b-dropdown-item>
                    <b-dropdown-item :to="{path: './operator_log'}">後台操作紀錄</b-dropdown-item>
                </b-nav-item-dropdown>
                <b-nav-item-dropdown v-if="showRouter('/server_config')" :class="isActive(engineerPath)">
                    <template slot="text"><i class="material-icons">build</i>工程控制</template>
                    <b-dropdown-item :to="{path: './server_config'}" v-if="showRouter('/server_config')">伺服器設定</b-dropdown-item>
                    <b-dropdown-item :to="{path: './dbinfo'}" v-if="showRouter('/dbinfo')">資料庫資訊</b-dropdown-item>
                    <b-dropdown-item :to="{path: './agent'}" v-if="showRouter('/agent')">Node.js服務控制</b-dropdown-item>
                </b-nav-item-dropdown>
            </b-navbar-nav>
            <b-navbar-nav class="ml-auto">
                <b-btn variant="dark" @click="clickLog" v-if="showNotify()">
                    後台通知
                    <b-badge variant="light"> {{ logsLength }} <span class="sr-only">unread messages</span> </b-badge>
                </b-btn>
                <b-btn id="popoverButton-sync" variant="dark">{{ username }}</b-btn>
                <b-popover target="popoverButton-sync" :placement="'bottom'" triggers="click" :show.sync="popoverShow">
                    <template slot="title">
                        {{ username }} - {{ level() }}
                    </template>
                    <b-list-group flush class="text-center" style="width:210px">
                        <b-list-group-item class="d-flex justify-content-between" v-if="showNotify()">
                            後台提醒<toggle-button v-model="notiActive" :labels="{checked: 'on', unchecked: 'off'}"/>
                        </b-list-group-item>
                        <b-list-group-item button class="d-flex justify-content-between"  @click="reconnectWs" v-if="showNotify()">
                            後台重連<b-badge pill :variant="statusVariant(status)" style="font-size: 100%">{{ status | wsStatus }}</b-badge>
                        </b-list-group-item>
                        <b-list-group-item button @click="showEditModal">更改密碼</b-list-group-item>
                        <b-list-group-item button @click="logOut">登出</b-list-group-item>
                    </b-list-group>
                </b-popover>
            </b-navbar-nav>
        </b-collapse>
    </b-navbar>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import editSelfPassword from "./modal/edit_self_password";

export default {
    data() {
        return {
            popoverShow: false,
            reportPath: ['/game_report', '/member_report', '/member_day_report', '/profit_loss_report'],
            memberPath: ['/member_list', '/member_logs', '/member_group'],
            analysisPath: ['/game_analysis', '/member_analysis'],
            adminPath: ['/account'],
            engineerPath: ['/server_config', '/agent', '/dbinfo']
        };
    },
    computed: {
        notiActive: {
            get() {
                return this.$ws.shouldNotify;
            },
            set(val) {
                this.$ws.shouldNotify = val;
            }
        },
        ...mapGetters({
            isLogin: "getIsLogin",
            username: "getUsername",
            logsLength: "getLogsLength",
            apiLevel: "getApiLevel",
            status: "getWsStatus"
        })
    },
    methods: {
        isActive(data) {
            for (let i = 0, len = data.length; i < len; i++) {
                if (this.$route.path === data[i] ) {
                    return 'active'
                }
            }
            return '';
        },
        showNotify() {
            let level = Number(sessionStorage.getItem('level'));
            let ans = level >= 900 ? true : false;
            return ans;
        },
        showRouter(path) {
            if(!this.apiLevel) {
                return false;
            }
            return this.$api.checkLevel(path);
        },
        level() {
            return config.level[sessionStorage.getItem('level')];
        },
        reconnectWs() {
            this.$ws.reconnect();
        },
        clickLog() {
            this.$emit('clickLog');
        },
        logOut() {
            this.$emit('closeLog');
            this.popoverShow = false;
            this.$api.logOut();
        },
        showEditModal() {
            this.popoverShow = false;
            this.$modal.show(editSelfPassword, {}, { width: "30%", height: "auto" });
        },
        statusVariant(val) {
            switch(val) {
                case 0:
                    return 'success';
                case 1:
                    return 'danger';
                default:
                    return 'secondary';
            }
        }
    }
};
</script>

<style lang="scss">
#my-bar {
    .material-icons {
        vertical-align: bottom;
        width: 26px
    }
    ul {
        text-align: center;
    }
    text-align: center;
}
</style>



