<template>
    <div>
        <div class="card-header">
            <b-row align-v="center" class="m-0">
                <b-col md="2">
                    <span class="font-weight-bold" style="font-size: 30px">遊戲管理</span>
                </b-col>
                <b-col md="10" class="text-right">
                    <b-btn 
                        class="material-icons" 
                        @click="showEditGroupModal" 
                        style="font-size: 24px"
                        v-b-tooltip.hover
                        title="批次修改"
                    >done_all</b-btn>
                    <b-btn 
                        class="material-icons" 
                        @click="showAddModal" 
                        style="font-size: 24px"
                        v-b-tooltip.hover
                        title="新增遊戲"
                    >add_box</b-btn>
                </b-col>
            </b-row>
        </div>
        <div class="m-2">
            <b-table
                hover
                show-empty
                stacked="md"
                small
                :items="gameList" 
                :fields="fields" 
                empty-text="無資料"
            >
                <template slot="gid" slot-scope="row">{{ row.value }}</template>
                <template slot="name" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="name_cn" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="name_en" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="name_jp" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="status" slot-scope="row">
                    <b-dropdown variant="link" no-caret>
                        <template slot="button-content">
                            <span :class="textColor(row.value)">{{ row.value | gameListStatus }}</span>
                        </template>
                        <b-dropdown-item @click="edit([row.item.gid], { status: 1 })">開放</b-dropdown-item>
                        <b-dropdown-item @click="edit([row.item.gid], { status: 0 })">下架</b-dropdown-item>
                    </b-dropdown>
                </template>
                <template slot="fun" slot-scope="row">
                    <b-dropdown variant="link" no-caret>
                        <template slot="button-content">
                            <span :class="textColor(row.value)">{{ row.value | haveFun }}</span>
                        </template>
                        <b-dropdown-item @click="edit([row.item.gid], { fun: 1 })">有</b-dropdown-item>
                        <b-dropdown-item @click="edit([row.item.gid], { fun: 0 })">無</b-dropdown-item>
                    </b-dropdown>
                </template>
                <template slot="updated_at" slot-scope="row">{{ row.value }}</template>
                <template slot="actions" slot-scope="row">
                    <b-btn class="material-icons" v-b-tooltip.hover title="更改名稱" @click="showEditModal(row)">edit</b-btn>
                </template>
            </b-table>
        </div>
    </div>
</template>

<script>
import editGroupModal from './edit_group_modal';
import editModal from './edit_modal';
import addModal from './add_modal';

export default {
    created() {
        this.getGameList();
    },
    data() {
        return {
            gids: [],
            gameList: [],
            fields: [
                {
                    key: "gid",
                    label: "遊戲ID",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "name",
                    label: "名稱-繁體",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "name_cn",
                    label: "名稱-簡體",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "name_en",
                    label: "名稱-英文",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "name_jp",
                    label: "名稱-日文",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "status",
                    label: "狀態",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "fun",
                    label: "試玩模式",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "updated_at",
                    label: "資料更新時間",
                    sortable: true,
                    class: 'text-center'
                },
                {
                    key: "actions",
                    label: "選項",
                    class: 'text-center'
                }
            ]
        }
    },
    methods: {
        getGameList() {
            // this.$api.requestFetch(`/game_list`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.gameList = res.data;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
            this.gameList = [
                {
                    gid: 1234,
                    name: '遊戲A',
                    name_cn: '遊戲A',
                    name_en: '遊戲A',
                    name_jp: '遊戲A',
                    status: 0,
                    fun: 0,
                    updated_at: '2019.08.08'
                },
                {
                    gid: 4321,
                    name: '遊戲B',
                    name_cn: '遊戲B',
                    name_en: '遊戲B',
                    name_jp: '遊戲B',
                    status: 1,
                    fun: 1,
                    updated_at: '2019.08.08'
                },
                {
                    gid: 222,
                    name: '遊戲A',
                    name_cn: '遊戲A',
                    name_en: '遊戲A',
                    name_jp: '遊戲A',
                    status: 0,
                    fun: 0,
                    updated_at: '2019.08.08'
                },
                {
                    gid: 33,
                    name: '遊戲B',
                    name_cn: '遊戲B',
                    name_en: '遊戲B',
                    name_jp: '遊戲B',
                    status: 1,
                    fun: 1,
                    updated_at: '2019.08.08'
                },
                {
                    gid: 1,
                    name: '遊戲A',
                    name_cn: '遊戲A',
                    name_en: '遊戲A',
                    name_jp: '遊戲A',
                    status: 0,
                    fun: 0,
                    updated_at: '2019.08.08'
                },
                {
                    gid: 4,
                    name: '遊戲B',
                    name_cn: '遊戲B',
                    name_en: '遊戲B',
                    name_jp: '遊戲B',
                    status: 1,
                    fun: 1,
                    updated_at: '2019.08.08'
                },
                {
                    gid: 55,
                    name: '遊戲A',
                    name_cn: '遊戲A',
                    name_en: '遊戲A',
                    name_jp: '遊戲A',
                    status: 0,
                    fun: 0,
                    updated_at: '2019.08.08'
                },
                {
                    gid: 66,
                    name: '遊戲B',
                    name_cn: '遊戲B',
                    name_en: '遊戲B',
                    name_jp: '遊戲B',
                    status: 1,
                    fun: 1,
                    updated_at: '2019.08.08'
                },
            ]
        },
        edit(gid, body) {
            // body.gid = gid;
            // this.$api.requestFetch(`/game_list/edit`, "POST", body)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '更改成功');
            //             this.getGameList();
            //         } else {
            //             this.$api.showModalInfo('ERROR', '更改失敗' + `(${res.msg})`);
            //         }
            //     });
            this.$api.showModalInfo('SUCCESS', '更改成功');
        },
        showAddModal() {
            this.$modal.show(
                addModal, 
                { refresh: this.getGameList}, 
                { scrollable: true, width: "80%", height: "auto" }
            );
        },
        showEditModal(row) {
            this.$modal.show(
                editModal, 
                { 
                    gid: row.item.gid, 
                    refresh: this.getGameList, 
                    old_data: { 
                        name_tw: row.item.name,
                        name_cn: row.item.name_cn,
                        name_en: row.item.name_en,
                        name_jp: row.item.name_jp 
                    }
                }, 
                { scrollable: true, width: "80%", height: "auto" }
            );
        },
        showEditGroupModal() {
            this.$modal.show(
                editGroupModal, 
                { 
                    gameList: this.gameList, 
                    refresh: this.getGameList, 
                }, 
                { scrollable: true, width: "80%", height: "auto" }
            );
        },
        textColor(val) {
            switch(val) {
                case 0:
                    return 'text-danger';
                case 1:
                    return 'text-success';
                default:
                    return '';
            }
        }
    }
}
</script>

<style>

</style>



