<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">{{ title }}</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-table
                show-empty 
                bordered 
                :items="items" 
                :fields="fields" 
                empty-text="無資料"
            >
                <template slot="id" slot-scope="row">
                    <div @click="getRewardDetails(row.value)">{{ row.value || '無' }}</div>
                </template>
                <template slot="bet_id" slot-scope="row">
                    <div @click="getTicketUrl(row.value)">{{ row.value || '無' }}</div>
                </template>
                <template slot="currency" slot-scope="row">{{ row.value || '無' }}</template>
                <template slot="win" slot-scope="row">{{ (row.value || '無') | formatMoney(row.item.currency) }}</template>
                <template slot="received_at" slot-scope="row">{{ row.value | formatTime }}</template>
            </b-table>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">Close</button>
        </footer>
    </div>
</template>

<script>
import ticketModal from '../ticket_modal';
import rewardDetailsModal from './reward_details_modal';

export default {
    props: [ 'title', 'items' ],
    data() {
        return {
            fields: [
                {
                    key: "id",
                    label: "ID",
                    class: 'clickable'
                },
                {
                    key: "bet_id",
                    label: "注單編號",
                    class: 'text-center clickable'
                },
                {
                    key: "currency",
                    label: "幣別",
                    class: 'text-center'
                },
                {
                    key: "win",
                    label: "贏得金額",
                    class: 'text-right'
                },
                {
                    key: "received_at",
                    label: "取獎時間",
                    class: 'text-right'
                },
            ]
        }
    },
    methods: {
        getRewardDetails(id) {
            // this.$api.requestFetch(`/jackpot/award_detail?id=${id}`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$modal.show(rewardDetailsModal, { rewardDetails: res.data }, { scrollable: true, width: "30%", height: "auto" })
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getTicketUrl(id) {
            if(!id || id === 0) {
                return;
            }
            // this.$api.getTicketUrl(id)
            //     .then(e => this.$modal.show(ticketModal, { url: e }, { draggable: true, width: "50%", height: "auto" }))
            //     .catch(e => alert(e));
        }
    }
}
</script>

<style>

</style>



