import Vue from 'vue'
import types from './types'

// state
export const state = {
    items: [],
    currency: '',
    startTime: '',
    endTime: '',
    newTarget: 0,
    players: {},
    chooseDay: [],
    newPlayer: [],
    playerInfoData: [],
    playerGameData: []
}

// mutations
export const mutations = {
    [types.SETITEMS](state, data) {
        state.items = data;
    },
    [types.SETCURRENCY](state, data) {
        state.currency = data;
    },
    [types.SETSTARTTIME](state, data) {
        state.startTime = data;
    },
    [types.SETENDTIME](state, data) {
        state.endTime = data;
    },
    [types.SETCHOOSEDAY](state, data) {
        state.chooseDay = data;
    },
    [types.SETPLAYERS](state, data) {
        state.players = data;
    },
    [types.SETNEWPLAYER](state, data) {
        state.newPlayer = data;
    },
    [types.SETNEWTARGET](state, data) {
        state.newTarget = data;
    },
    [types.SETPLAYERINFODATA](state, data) {
        state.playerInfoData = data;
    },
    [types.SETPLAYERGAMEDATA](state, data) {
        state.playerGameData = data;
    },
    [types.CLEAR](state, data) {
        state.items = [];
        state.startTime = '';
        state.endTime = '';
        state.currency = '';
        state.chooseDay = [];
    }
}


