<template>
<modal name="modalSend" width="30%" height="auto">
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">傳送訊息</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('modalSend')">×</button>
        </header>
        <div class="modal-body">
            <form>
                動作:
                <b-form-input type="text" placeholder="Enter your action" v-model="sendData.action"></b-form-input>
                訊息:
                <b-form-input type="text" placeholder="Enter your detail" v-model="sendData.detail"></b-form-input>
            </form>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$modal.hide('modalSend')">Cancel</b-btn>
            <b-btn @click="sendMsg">OK</b-btn>
        </footer>
    </div>
</modal>
</template>

<script>
import { mapGetters } from "vuex";
export default {
    name: "ModalSend",
    data() {
        return {
            sendData: {
                action: "",
                detail: ""
            }
        };
    },
    computed: {
        ...mapGetters({
            id: "getSendId",
            target: "getTarget"
        }),
    },
    created: function () {},
    methods: {
        clearSendData() {
            this.sendData = {
                action: "",
                detail: ""
            };
        },
        sendMsg() {
            this.sendData.name = this.target;
            this.sendData.id   = this.id;
            // this.$api.requestFetch(`/agent/send`, "POST", this.sendData)
            //     .catch(err => alert(err))
            //     .then(res => {
            //         let ans = res.status ? '成功' : '失敗';
            //         this.$api.showModalInfo('傳送結果', `傳送資訊${ans}`);
            //     });
            this.$api.showModalInfo('傳送結果', `傳送資訊: xxxxxx`);
            this.clearSendData();
            this.$modal.hide('modalSend');
        }
    }
};
</script>

<style lang="scss">

</style>



