import { state, mutations } from './mutations.js'
import * as actions from './actions.js'

const getters = {
    getAgent: state => { return state.agent },
    getAgentData: state => { return state.agentData },
    getTarget: state => { return state.target },
    getSendId: state => { return state.sendId },
}

export default ({
    state,
    mutations,
    getters,
    actions,
});



