<template>
    <div>
        <b-row align-v="center" class="card-header m-0">
            <b-col md="2">
                <span class="font-weight-bold" style="font-size: 30px">遊玩記錄</span>
            </b-col>
            <b-col md="9">
                <date-time
                    v-on:startDate="startDate=$event"
                    v-on:startTime="startTime=$event"
                    v-on:endDate="endDate=$event"
                    v-on:endTime="endTime=$event"
                ></date-time>
            </b-col>
            <b-col md="1">
                <b-btn variant="primary" @click="getMemberLogs(1)">查詢</b-btn>
            </b-col>
        </b-row>
        <div class="p-2">
            <b-table 
                show-empty  
                hover 
                :items="items" 
                :fields="fields" 
                empty-text="無資料" 
            >
                <template slot="gid" slot-scope="row">{{ row.value | toGidName }}</template>
                <template slot="name" slot-scope="row">
                    <div @click="getPlayerInfo(row.item.mid, row.value)">{{ row.value || '試玩帳號' }}</div>
                </template>
                <template slot="currency" slot-scope="row">{{ row.value }}</template>
                <template slot="ip" slot-scope="row">{{ row.value }}</template>
                <template slot="device" slot-scope="row">{{ row.value }}/{{ row.item.os }}/{{ row.item.os_ver }}</template>
                <template slot="browser_type" slot-scope="row">{{ row.value }}/{{ row.item.browser_ver }}</template>
                <template slot="start_time" slot-scope="row">{{ row.value | formatTime }}</template>
                <template slot="end_time" slot-scope="row">{{ row.value | formatTime }}</template>
            </b-table>
            <pagination-fast
                :totalRows="totalRows"
                :rows="rows"
                :currentPage="currentPage"
                v-on:changePage="getMemberLogs"
            ></pagination-fast>
            <b-pagination :total-rows="totalRows" :per-page="rows" v-model="currentPage" align="right" :limit="10" @change="getMemberLogs"/>
        </div>
    </div>
</template>

<script>
import DateTime from '../date_time';
import paginationFast from '../pagination_fast';
import playerInfo from '../player_info';

export default {
    components: {
        DateTime,
        paginationFast
    },
    data() {
        return {
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            totalRows: 0,
            rows: 25,
            currentPage: 1,
            items: [],
            fields: [
                {
                    key: "gid",
                    label: "遊戲名稱",
                    class: 'text-center',
                },
                {
                    key: "name",
                    label: "玩家名稱",
                    class: 'text-center clickable',
                },
                {
                    key: "currency",
                    label: "幣別",
                    class: 'text-center',
                },
                {
                    key: "ip",
                    label: "IP位置",
                    class: 'text-center',
                },
                {
                    key: "device",
                    label: "裝置/系統/版本",
                    class: 'text-center',
                },
                {
                    key: "browser_type",
                    label: "瀏覽器/版本",
                    class: 'text-center',
                },
                {
                    key: "start_time",
                    label: "開始時間",
                    class: 'text-center',
                },
                {
                    key: "end_time",
                    label: "結束時間",
                    class: 'text-center',
                }
            ]
        }
    },
    methods: {
        getMemberLogs(page) {
            this.currentPage = page;
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            let url = `/member_list/all_logs?page=${this.currentPage - 1}&rows=${this.rows}&start_time=${totalStart}&end_time=${totalend}`;
            let data = {
                lists: [
                    {
                        gid: 2222,
                        name: '人員1',
                        currency: 'TWD',
                        ip: '87.87.87.87',
                        device: 'desktop',
                        browser_type: 'chrome',
                        start_time: '2018-09-09',
                        end_time: '2018-09-09',
                        os: 'windows',
                        os_ver: '87',
                        browser_ver: '87'
                    },
                    {
                        gid: 2222,
                        name: '人員1',
                        currency: 'TWD',
                        ip: '87.87.87.87',
                        device: 'desktop',
                        browser_type: 'chrome',
                        start_time: '2018-09-09',
                        end_time: '2018-09-09',
                        os: 'windows',
                        os_ver: '87',
                        browser_ver: '87'
                    },
                    {
                        gid: 2222,
                        name: '人員1',
                        currency: 'TWD',
                        ip: '87.87.87.87',
                        device: 'desktop',
                        browser_type: 'chrome',
                        start_time: '2018-09-09',
                        end_time: '2018-09-09',
                        os: 'windows',
                        os_ver: '87',
                        browser_ver: '87'
                    }
                ],
                total: 3
            }
            this.items = data.lists;
            this.totalRows = data.total;
            document.documentElement.scrollTop = 0;
            // this.$api.requestFetch(url, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.items = res.data.lists;
            //             this.totalRows = res.data.total;
            //             document.documentElement.scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getPlayerInfo(mid, name) {
            if (!name) return;
            let promises = [];
            promises.push(this.$api.getPlayerGame(mid, '', ''));
            promises.push(this.$api.getPlayerInfo(name));
            Promise.all(promises)
                .then(e => this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' }))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!' + `(${e.msg})`));
        }
    }
}
</script>

<style>

</style>



