<template>
<div id="rounds_table">
    <b-table show-empty :items="items" :fields="fields" bordered empty-text="無資料">
        <template slot="id" slot-scope="row">
            <div @click="getTicketUrl(row.item)">{{ row.value }}</div>
        </template>
        <template slot="time" slot-scope="row">{{ row.value | formatTime}}</template>
        <template slot="name" slot-scope="row">
            <div @click="getPlayerInfo(row.item)">{{ row.value }}</div>
        </template>
        <template slot="bet" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
        <template slot="win" slot-scope="row">{{ row.value | formatMoney(currency) }}</template>
        <template slot="result" slot-scope="row">
            <span :class="resultTextColor(row.value)">
                {{ row.value | formatMoney(currency) }}
            </span>
        </template>
        <template slot="HEAD_bet" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
        <template slot="HEAD_win" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
        <template slot="HEAD_result" slot-scope="data">
            {{ data.label | headCurrencyText(currency) }}
        </template>
    </b-table>
</div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import ticketModal from '../ticket_modal';
const { mapGetters, mapActions } = createNamespacedHelpers('game_report');

export default {
    name: "rounds_table",
    props: ['items'],
    data() {
        return {
            fields: [
                {
                    key: "id",
                    label: "注單編號",
                    class: 'clickable'
                },
                {
                    key: "time",
                    label: "下注時間",
                    class: 'text-center'
                },
                {
                    key: "name",
                    label: "玩家名稱",
                    class: 'text-center clickable'
                },
                {
                    key: "bet",
                    label: "下注金額",
                    class: 'text-right'
                },
                                {
                    key: "win",
                    label: "贏得金額",
                    class: 'text-right'
                },
                {
                    key: "result",
                    label: "總輸贏",
                    class: 'text-right'
                }
            ]
        };
    },
    computed: {
        ...mapGetters({
            startTime: "getStartTime",
            endTime: "getEndTime",
            currency: "getCurrency",
        })
    },
    methods: {
        getPlayerInfo(item) {
            let promises = [];
            promises.push(this.$api.getPlayerGame(item.mid, this.startTime, this.endTime));
            promises.push(this.$api.getPlayerInfo(item.name));
            Promise.all(promises)
                .then(e => this.$emit('getPlayerInfo'))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!'));
        },
        getTicketUrl(item) {
            // this.$api.getTicketUrl(item.id)
            //     .then(e => this.$modal.show(ticketModal, { url: e }, { draggable: true, width: "50%", height: "auto" }))
            //     .catch(e => alert(e));
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        }
    },
}
</script>


