import { state, mutations } from './mutations.js'
import * as actions from './actions.js'

const getters = {
    getItems: state => { return state.items },
    getCurrency: state => { return state.currency },
    getStartTime: state => { return state.startTime },
    getEndTime: state => { return state.endTime },
    getChooseDay: state => { return state.chooseDay },
    getPlayers: state => { return state.players },
    getPlayerInfoData: state => { return state.playerInfoData },
    getPlayerGameData: state => { return state.playerGameData },
    getNewPlayer: state => { return state.newPlayer },
    getNewTarget: state => { return state.newTarget }
}

export default ({
    namespaced: true,
    state,
    mutations,
    getters,
    actions,
});



