<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">新增遊戲</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            遊戲ID:
            <b-form-input type="text" v-model="sendData.gid"></b-form-input>
            名稱-繁體:
            <b-form-input type="text" v-model="sendData.name_tw"></b-form-input>
            名稱-簡體:
            <b-form-input type="text" v-model="sendData.name_cn"></b-form-input>
            名稱-英文:
            <b-form-input type="text" v-model="sendData.name_en"></b-form-input>
            名稱-日文:
            <b-form-input type="text" v-model="sendData.name_jp"></b-form-input>
            狀態:
            <b-form-select :options="statusOptions" style="height:100%" v-model="sendData.status"></b-form-select>
            試玩模式:
            <b-form-select :options="funOptions" style="height:100%" v-model="sendData.fun"></b-form-select>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">取消</button>
            <button type="button" class="btn btn-primary" @click="edit()">新增</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'refresh' ],
    data() {
        return {
            statusOptions: [{ value: '1', text: '開放' }, { value: '0', text: '下架' }],
            funOptions: [{ value: '1', text: '有' }, { value: '0', text: '無' }],
            sendData: {
                gid: '',
                name_tw: '',
                name_cn: '',
                name_en: '',
                name_jp: '',
                status: '1',
                fun: '1'
            }
        }
    },
    methods: {
        edit() {
            let keys = Object.keys(this.sendData);
            for (let i = 0, len = keys.length; i < len; i++) {
                if (!this.sendData[keys[i]]) {
                    this.$api.showModalInfo('ERROR', '欄位不得為空');
                    return;
                }
            }
            this.$api.showModalInfo('SUCCESS', '更改成功');
            this.$emit('close');
            // this.$api.requestFetch(`/game_list/add`, "POST", this.sendData)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '更改成功');
            //             this.refresh();
            //             this.$emit('close');
            //         } else {
            //             this.$api.showModalInfo('ERROR', '更改失敗' + `(${res.msg})`);
            //             this.$emit('close');
            //         }
            //     });
        }
    }

}
</script>

<style>

</style>



