<template>
<div id="app">
    <template>
        <loading :show="isLoading"></loading>
    </template>
    <nav-bar
        v-on:clickLog="show = !show"
        v-on:closeLog="show = false"
    ></nav-bar>
    <router-view></router-view>
    <server-log 
        v-if="show && isLogin" 
        v-on:closeLog="show = false"
    ></server-log>
    <modals-container/>
    <notifications group="server-noti" position="bottom right">
        <template slot="body" slot-scope="props">
            <div class="vue-notification" :class="props.item.type">
                <div class="title">
                    <span class="clickable" @click="showNotiDetails(props)">{{ props.item.title }}</span>
                    <button type="button" aria-label="Close" class="close" @click="props.close">×</button>
                </div>
                <div class="clickable" @click="showNotiDetails(props)">{{ props.item.text.slice(0, 20) }}...</div>
            </div>
        </template>
    </notifications>
</div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import NavBar from "./components/nav_bar"
import ServerLog from "./components/server_log";
import Loading from 'vue-custom-loading';

export default {
    data() {
        return {
            show: false
        };
    },
    components: {
        NavBar,
        ServerLog,
        Loading
    },
    computed: {
        ...mapGetters({
            isLogin: "getIsLogin",
            isLoading: "getIsLoading"
        })
    },
    methods: {
        showNotiDetails(props) {
            props.close();
            this.$api.showModalInfo(props.item.title,  props.item.text);
        }
    }
};
</script>

<style lang="scss">
html {
    min-height: 100%;
    overflow-x: hidden;
    background-color: #d8dee3;
}

#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.ml-auto {
    .btn {
        margin-left: 1%;
        background-color: #6c757d;
    }
}

.scroll {
    overflow-y: auto;
}

.sidebar-color {
    background-color: #424242
}

.clickable { 
    cursor: pointer; 
    :hover{
        text-decoration: underline
    }
}

.date-input {
    height: calc(2.25rem + 2px);
    width: calc(7rem + 2px);
    text-align: center;
}

input.display-time {
    text-align: center;
}

.v--modal-overlay {
    z-index: 1021 !important;
    padding-top: 10px;
}

.v--modal {
    border-radius: 5px !important;
}

.time-picker {
    width: 86px !important;
    input.display-time {
        width: 100% !important;
        height: 100% !important;
    }
}

td {
    vertical-align: middle !important;
}

</style>



