<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">{{ title }}</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-table show-empty :items="bet" bordered empty-text="無資料" responsive caption-top>
                <template slot="table-caption">bet</template>
                <template slot="time" slot-scope="row"> {{ row.value | formatTime }} </template>
            </b-table>
            <b-table show-empty :items="betDtl01" bordered empty-text="無資料" responsive caption-top>
                <template slot="table-caption">betDet</template>
            </b-table>
            <b-table show-empty :items="betTran" bordered empty-text="無資料" responsive caption-top>
                <template slot="table-caption">betTran</template>
                <template slot="time" slot-scope="row"> {{ row.value | formatTime }} </template>
            </b-table>
            <b-table show-empty :items="betContribute" bordered empty-text="無資料" responsive caption-top>
                <template slot="table-caption">betContribute</template>
                <template slot="created_at" slot-scope="row"> {{ row.value | formatTime }} </template>
            </b-table>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">OK</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'title', 'content' ],
    computed: {
        bet() {
            return (this.content.bet || '') ? [this.content.bet] : [];
        },
        betDtl01() {
            return (this.content.betDtl01 || '') ? this.content.betDtl01 : [];
        },
        betTran() {
            return (this.content.betTran || '') ? [this.content.betTran] : [];
        },
        betContribute() {
            return (this.content.betContribute || '') ? [this.content.betContribute] : [];
        }
    }
}
</script>

<style>

</style>



