<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">新增分組</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            分組ID:
            <b-form-input type="number" v-model="id"></b-form-input>
            名稱:
            <b-form-input type="text" v-model="property"></b-form-input>
            類別:
            <b-form-select :options="typeOptions" v-model="type"></b-form-select>
            設定值:
            <b-form-select v-if="type === 0" :options="bolOptions" v-model="value[0]"></b-form-select>
            <b-form-input v-if="type === 1" type="number" v-model="value[1]"></b-form-input>
            <b-form-input v-if="type === 2" type="text" v-model="value[2]"></b-form-input>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">取消</button>
            <button type="button" class="btn btn-primary" @click="edit()">新增</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'refresh' ],
    data() {
        return {
            typeOptions: [{ value: 0, text: 'boolean' }, { value: 1, text: 'number' }, { value: 2, text: 'string' }],
            bolOptions: [ 'true', 'false' ],
            id: '',
            property: '',
            type: 0,
            value: ['true', '', ''],
            typeEnum: ['boolean', 'number', 'string']
        }
    },
    methods: {
        edit() {
            let type = this.type;
            let value = this.value[type];
            if (!this.id || !this.property || !value) {
                this.$api.showModalInfo('ERROR', `資料不得為空`);
                return;
            }
            let body = {
                group_id: this.id,
                property: this.property,
                type: this.typeEnum[type],
                value: value
            };
            this.$api.showModalInfo('SUCCESS', '新增成功');
            this.$emit('close');
            // this.$api.requestFetch(`/member_group/add`, "POST", body)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '新增成功');
            //             this.refresh();
            //             this.$emit('close');
            //         } else {
            //             this.$api.showModalInfo('ERROR', '新增失敗' + `(${res.msg || res.message})`);
            //             this.$emit('close');
            //         }
            //     });
        }
    }

}
</script>

<style>

</style>



