<template>
<div id="Process">
    <os-info v-on:fresh="fresh('os')" v-if="agentData.os"></os-info>
    <list-info 
        v-if="agentData.list"
        v-on:fresh="fresh('list')" 
        v-on:showSendMsg="showSendMsg($event)"
        v-on:showAddModal="showAddModal()"
        v-on:showDetailModal="showDetailModal($event)"
    ></list-info>
    <modal name="modalDetail" width="50%" height="auto">
        <header class="modal-header">
            <h5 class="modal-title">{{ detailInfo.title }}</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('modalDetail')">×</button>
        </header>
        <div class="modal-body">
            <div v-for="(item, key) in detailInfo.content" :key="key">
                <b-row v-for="(item, key) in item.word" :key="key">
                    {{item}}
                </b-row>
                <b-row  class="border-top border-bottom">
                    <pre v-if="item.show">{{item.detail}}</pre>
                    <b-btn @click="item.show=!item.show" class="mx-auto w-100 bg-white border-0">
                        <i class="material-icons text-secondary" v-if="!item.show">expand_more</i>
                        <i class="material-icons text-secondary" v-else>expand_less</i>
                    </b-btn>
                </b-row>
            </div>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$modal.hide('modalDetail')">OK</b-btn>
        </footer>
    </modal>
    <modal-add></modal-add>
    <modal-send></modal-send>
</div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import OsInfo from "./os_info.vue";
import ListInfo from "./list_info.vue";
import ModalAdd from "./modal_add.vue";
import ModalSend from "./modal_send.vue";

export default {
    name: "Process",
    components: {
        OsInfo,
        ListInfo,
        ModalAdd,
        ModalSend
    },
    data() {
        return {
            detailInfo: {
                title: "",
                content: [],
            }
        };
    },
    created() {
        // this.fresh("info");
    },
    computed: {
        ...mapGetters({
            agentData: "getAgentData",
            target: "getTarget"
        })
    },
    methods: {
        fresh(type) {
            if (!this.target) {
                return;
            }
            // this.$api.requestFetch(`/agent/data?name=${this.target}&type=${type}`, "GET", "", false)
            //     .catch(err => alert(err))
            //     .then(res => {
            //         this.refresh({
            //             type: type,
            //             content: res.data
            //         });
            //     });
        },
        showDetailModal(event) {
            this.detailInfo.title   = event.title;
            this.detailInfo.content = event.content;
            this.$modal.show('modalDetail');
        },
        showSendMsg(id) {
            this.setSendId(id);
            this.$modal.show('modalSend');
        },
        showAddModal() {
            this.$modal.show('modalAdd');
        },
        ...mapActions(["refresh", "setSendId"])
    },
};
</script>

<style lang="scss">
#Process {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
}
</style>



