<template>
    <div>
        <b-row align-v="center" class="card-header m-0">
            <b-col md="2">
                <span class="font-weight-bold" style="font-size: 30px">玩家報表</span>
            </b-col>
            <b-col md="7">
                <date-time
                    v-on:startDate="startDate=$event"
                    v-on:startTime="startTime=$event"
                    v-on:endDate="endDate=$event"
                    v-on:endTime="endTime=$event"
                ></date-time>
            </b-col>
            <b-col md="2">
                <b-input-group>
                    <b-input-group-prepend is-text>
                        玩家關鍵字
                    </b-input-group-prepend>
                    <b-form-input v-model="keyWord" @keyup.native.enter="keyWordSearch()"></b-form-input>
                </b-input-group>
            </b-col>
            <b-col md="1">
                <b-btn variant="primary" @click="keyWordSearch">查詢</b-btn>
            </b-col>
        </b-row>
        <div class="p-2">
            <b-table 
                show-empty 
                bordered 
                hover 
                :current-page="currentPage"
                :per-page="per_page"
                :items="items" 
                :fields="fields" 
                :sort-by.sync="sort" 
                :sort-desc.sync="sortDesc"
                empty-text="無資料" 
            >
                <template slot="name" slot-scope="row">
                    <div @click="getPlayerInfo(row.item)">{{ row.value }}</div>
                </template>
                <template slot="currency" slot-scope="row">{{ row.value }}</template>
                <template slot="bet" slot-scope="row">{{ row.value | formatMoney(row.item.currency) }}</template>
                <template slot="win" slot-scope="row">{{ row.value | formatMoney(row.item.currency) }}</template>
                <template slot="result" slot-scope="row">
                    <span :class="resultTextColor(row.value)">{{ row.value | formatMoney(row.item.currency) }}</span>
                </template>
                <template slot="rtp" slot-scope="row">
                    <span :style="expectedTextColor(row.value)">{{ row.value }}%</span>
                </template>
            </b-table>
            <pagination-fast
                :totalRows="totalRows"
                :rows="per_page"
                :currentPage="currentPage"
                v-on:changePage="changePage($event)"
            ></pagination-fast>
            <b-pagination :total-rows="totalRows" :per-page="per_page" v-model="currentPage" align="right" :limit="10" @change="changePage"/>
            <!-- <pagination-fast
                :totalRows="totalRows"
                :rows="per_page"
                :currentPage="currentPage"
                v-on:changePage="getMemberReport"
            ></pagination-fast> -->
            <!-- <b-pagination :total-rows="totalRows" :per-page="per_page" v-model="currentPage" align="right" :limit="10" @change="getMemberReport"/> -->
        </div>
    </div>
</template>

<script>
import DateTime from '../date_time'
import playerInfo from '../player_info';
import paginationFast from '../pagination_fast';

export default {
    components: {
        DateTime,
        playerInfo,
        paginationFast
    },
    data() {
        return {
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            keyWord: '',
            lastSearch: '',
            totalRows: 0,
            per_page: 25,
            currentPage: 1,
            items: [],
            sort: 'result',
            sortDesc: true,
            fields: [
                {
                    key: "name",
                    label: "玩家名稱",
                    sortable: true,
                    class: 'text-left clickable',
                },
                {
                    key: "currency",
                    label: "幣別",
                    sortable: true,
                    class: 'text-center',
                },
                {
                    key: "bet",
                    label: "下注金額",
                    sortable: true,
                    class: 'text-right',
                },
                {
                    key: "win",
                    label: "贏得金額",
                    sortable: true,
                    class: 'text-right',
                },
                {
                    key: "result",
                    label: "總輸贏",
                    sortable: true,
                    class: 'text-right',
                },
                {
                    key: "rtp",
                    label: "期望值",
                    sortable: true,
                    class: 'text-right',
                },
            ]
        }
    },
    methods: {
        keyWordSearch() {
            this.lastSearch = this.keyWord;
            this.getMemberReport(1);
        },
        changePage($event) {
            this.currentPage = $event ? $event : this.currentPage;
            setTimeout(() => document.documentElement.scrollTop = 0, 100);
        },
        getMemberReport(page) {
            // this.currentPage = page;
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            // let url = `/report/member?start_time=${totalStart}&end_time=${totalend}&page=${this.currentPage - 1}&rows=${this.per_page}`;
            let url = `/report/member?start_time=${totalStart}&end_time=${totalend}`;
            url += this.lastSearch ? `&key_word=${this.lastSearch}` : '';
            let data = [
                {
                    name: '人員1',
                    currency: 'TWD',
                    bet: 200,
                    win: 200
                },
                {
                    name: '人員1',
                    currency: 'TWD',
                    bet: 0,
                    win: 200
                },
                {
                    name: '人員1',
                    currency: 'TWD',
                    bet: 200,
                    win: 0
                },
            ];
            if (data.length === 0) {
                this.$api.showModalInfo('Success', `無資料`);
            }
            for (let i = 0, len = data.length; i < len; i++) {
                data[i].result = data[i].win - data[i].bet;
                data[i].rtp = this.toExpectdValue(data[i])
            }
            // this.totalRows = res.data.total;
            this.totalRows = data.length;
            this.items = data;
            this.currentPage = 1;
            document.documentElement.scrollTop = 0;
            // this.$api.requestFetch(url, "GET")   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             // let data = res.data.lists;
            //             let data = res.data;
            //             if (data.length === 0) {
            //                 this.$api.showModalInfo('Success', `無資料`);
            //             }
            //             for (let i = 0, len = data.length; i < len; i++) {
            //                 data[i].result = data[i].win - data[i].bet;
            //                 data[i].rtp = this.toExpectdValue(data[i])
            //             }
            //             // this.totalRows = res.data.total;
            //             this.totalRows = data.length;
            //             this.items = data;
            //             this.currentPage = 1;
            //             document.documentElement.scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        getPlayerInfo(item) {
            let promises = [];
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            promises.push(this.$api.getPlayerGame(item.mid, totalStart, totalend));
            promises.push(this.$api.getPlayerInfo(item.name));
            Promise.all(promises)
                .then(e => this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' }))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!'));
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        },
        expectedTextColor(val) {
            if (val >= 98) {
                return 'color: #dc3545';
            } else if (val <= 94){
                return 'color: #93c47d';
            } else {
                return 'color: #45818e'
            }
        },
        toExpectdValue(val) {
            if (!val) {
                return val;
            }
            if (val.win === 0) {
                return 0;
            } else if (val.bet === 0) {
                return 100;
            } else {
                return (val.win / val.bet * 100).toFixed(2);
            }
        },
    }
}
</script>

<style>

</style>



