<template>
    <div id="reward-send">
        <div class="card-header">
            <b-row align-v="center">
                <b-col>
                    <span style="color: red">備註: 除了幣別為三個月前統計至今以外，其餘資料為當月統計</span>、
                    <span style="color: blue">玩家在線最後更新: {{ onlinePlayers.last_time }}</span>
                    <b-btn @click="getOnlinePlayers">{{this.refreshCount}}秒後更新玩家在線</b-btn>
                </b-col>
            </b-row>
            <b-row align-v="center">
                <b-col md="4">
                    <b-input-group>
                        <b-input-group-prepend is-text>玩家關鍵字搜索</b-input-group-prepend>
                        <b-input v-model="keyWord" @keyup.native.enter="keyWordSearch()"></b-input>
                        <b-input-group-append>
                            <b-btn variant="primary" @click="keyWordSearch()">查詢</b-btn>
                        </b-input-group-append>
                    </b-input-group>
                </b-col>
                <b-col offset-md="7" md="1">
                    <toggle-button 
                        v-model="mode" 
                        @change="changeMode"
                        :labels="{checked: 'JP', unchecked: '紅包'}" 
                        :width="80" 
                        :height="38" 
                        :color="{checked: '#82c7eb', unchecked: '#7dce94'}"
                    />
                </b-col>
            </b-row>
            <b-row align-v="center" v-if="!mode">
                <b-col md="2">
                    <b-input-group>
                        <b-input-group-prepend is-text>預設金額</b-input-group-prepend>
                        <b-input v-model="defaultMoney"></b-input>
                    </b-input-group>
                </b-col>
                <b-col md="10">
                    <b-btn @click="clearRedData">清除選取</b-btn>
                    <b-btn @click="sendRed" class="red-btn-color">送出紅包</b-btn>
                </b-col>
            </b-row>
            <b-row align-v="center" v-if="!mode">
                <b-col>
                    <span>已選取:
                        <b-badge class="mr-1" pill variant="primary" v-for="(item, key) in redData" :key="key" v-if="item.active">
                            {{ `${item.name}(${item.money})` }}
                            <button type="button" aria-label="Close" class="close ml-1" @click="findRedData(item.mid).active = false">×</button>
                        </b-badge>
                    </span>
                </b-col>
            </b-row>
        </div>
        <b-table
                show-empty 
                bordered 
                hover 
                responsive
                :items="playersList.lists" 
                :fields="fields" 
                empty-text="無資料"
            >
            <template slot="name" slot-scope="row">
                <div @click="getPlayerInfo(row.item.mid, row.value, row.item.currency)">
                    {{ row.value || '無' }}
                    <b-badge
                        variant="success"
                        v-b-tooltip.hover
                        :title="getOnlineTooltip(row.item.mid)"
                    >{{ onlinePlayers.players[row.item.mid] ? 'online' : '' }}</b-badge>
                </div>
            </template>
            <template slot="currency" slot-scope="row">{{ row.value || '無' }}</template>
            <template slot="days" slot-scope="row">{{ row.value || 0 }}</template>
            <template slot="jp1" slot-scope="row">
                <div @click="getJpInfo(row, 1)">{{ row.value || 0 }}</div>
            </template>
            <template slot="jp2" slot-scope="row">
                <div @click="getJpInfo(row, 2)">{{ row.value || 0 }}</div>
            </template>
            <template slot="jp3" slot-scope="row">
                <div @click="getJpInfo(row, 3)">{{ row.value || 0 }}</div>
            </template>
            <template slot="jp4" slot-scope="row">
                <div @click="getJpInfo(row, 4)">{{ row.value || 0 }}</div>
            </template>
            <template slot="red" slot-scope="row">
                <div @click="getJpInfo(row, 9)">{{ row.value || 0 }}</div>
            </template>
            <template slot="bet" slot-scope="row">{{( row.value || 0 ) | formatMoney(row.item.currency) }}</template>
            <template slot="win" slot-scope="row">{{( row.value || 0 ) | formatMoney(row.item.currency) }}</template>
            <template slot="result" slot-scope="row">
                <span :class="resultTextColor(row.value)">
                    {{ ( row.value || 0 ) | formatMoney(row.item.currency) }}
                </span>
            </template>
            <template slot="send" slot-scope="row">
                <b-form-checkbox v-model="findRedData(row.item.mid).active" class="m-0 align-middle" v-if="!mode" @change="findRedData(row.item.mid).money=defaultMoney">
                    <b-input v-if="findRedData(row.item.mid).active" v-model="findRedData(row.item.mid).money" style="width: 100px; height: 25px;"></b-input>
                </b-form-checkbox>
                <b-btn v-if="mode" @click="showJpModal(row)">發送</b-btn>
            </template>
        </b-table>
        <pagination-fast
            :totalRows="totalRows"
            :rows="rows"
            :currentPage="page"
            v-on:changePage="changePage"
        ></pagination-fast>
        <b-pagination :total-rows="totalRows" :per-page="rows" v-model="page" align="right" :limit="10" @change="changePage"/>
    </div>
</template>

<script>
import jp_modal from './jp_modal';
import player_reward_modal from './player_reward_modal';
import playerInfo from '../player_info';
import paginationFast from '../pagination_fast';

export default {
    props: ['percents'],
    components: {
        paginationFast
    },
    created() {
        this.getPlayers(1);
        this.getOnlinePlayers();
        this.refreshCount = 10;
        this.refreshInterval = setInterval(() => {
            this.refreshCount--;
            if (this.refreshCount === 0) {
                this.getOnlinePlayers();
                this.refreshCount = 10;
            } 
        }, 1000);
    },
    beforeDestroy() {
        clearInterval(this.refreshInterval);
    },
    data() {
        return {
            playersList: '',
            mode: true,
            keyWord: '',
            tempKeyWord: '',
            page: 1,
            rows: 25,
            defaultMoney: 10,
            redData: [],
            onlinePlayers: [],
            refreshCount: 0,
            refreshInterval: '',
            fields: [
                {
                    key: "name",
                    label: "玩家",
                    class: 'clickable'
                },
                {
                    key: "currency",
                    label: "幣別",
                    class: 'text-center'
                },
                {
                    key: "days",
                    label: "遊玩天數",
                    class: 'text-center'
                },
                {
                    key: "jp1",
                    label: "JP1",
                    class: 'text-center clickable'
                },
                {
                    key: "jp2",
                    label: "JP2",
                    class: 'text-center clickable'
                },
                {
                    key: "jp3",
                    label: "JP3",
                    class: 'text-center clickable'
                },
                {
                    key: "jp4",
                    label: "JP4",
                    class: 'text-center clickable'
                },
                {
                    key: "red",
                    label: "紅包",
                    class: 'text-center clickable'
                },
                {
                    key: "bet",
                    label: "下注金額",
                    class: 'text-right'
                },
                {
                    key: "win",
                    label: "贏得金額",
                    class: 'text-right'
                },
                {
                    key: "result",
                    label: "總輸贏",
                    class: 'text-right'
                },
                {
                    key: "send",
                    label: "選項",
                    class: 'text-center',
                    thStyle: "width: 150px"
                }
            ],
        }
    },
    computed: {
        totalRows() {
            if (!this.playersList) {
                return 0;
            }
            return this.playersList.total;
        }
    },
    methods: {
        nameTrans(mid) {
            if (!mid) return;
            let idx = this.playersList.lists.findIndex(e => `${e.mid}` === `${mid}`);
            return idx < 0 ? '' : this.playersList.lists[idx].name;
        },
        keyWordSearch() {
            this.clearRedData();
            this.getPlayers(1, this.keyWord);
        },
        getPlayers(e, keyWord = '') {
            let data = {
                total: 10,
                lists: [
                    {
                        mid: 87,
                        name: '人員1',
                        currency: 'TWD',
                        days: 120,
                        jp1: 0,
                        jp2: 0,
                        jp3: 0,
                        jp4: 0,
                        red: 0,
                        bet: 100,
                        win: 100,
                        result: 0
                    },
                    {
                        mid: 5,
                        name: '人員2',
                        currency: 'RMB',
                        days: 111,
                        jp1: 0,
                        jp2: 0,
                        jp3: 0,
                        jp4: 0,
                        red: 0,
                        bet: 200,
                        win: 100,
                        result: -100
                    },
                    {
                        mid: 1,
                        name: '人員3',
                        currency: 'TWD',
                        days: 555,
                        jp1: 0,
                        jp2: 0,
                        jp3: 0,
                        jp4: 0,
                        red: 0,
                        bet: 100,
                        win: 0,
                        result: 100
                    },
                ]
            };
            this.page = e;
            this.getRedData(data.lists)
            this.playersList = data;
            this.tempKeyWord = keyWord;
            document.documentElement.scrollTop = 0;
            // this.$api.requestFetch(`/jackpot/players_list?page=${e - 1}&rows=${this.rows}&key_word=${keyWord || ''}`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.getRedData(res.data.lists)
            //             this.playersList = res.data;
            //             this.tempKeyWord = keyWord;
            //             document.documentElement.scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getOnlinePlayers() {
            let data = {
                players: [87]
            };
            this.onlinePlayers = data;
            // this.$api.requestFetch(`/jackpot/players`, "GET", '', false)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.onlinePlayers = res.data;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getRedData(lists) {
            for (let i = 0, len = lists.length; i < len; i++) {
                if (this.redData.findIndex(e => e.mid === lists[i].mid) >= 0) {
                    continue;
                }
                let temp = {
                    mid: lists[i].mid,
                    name: lists[i].name,
                    active: false,
                    money: 0
                }
                this.redData.push(temp);
            }
        },
        findRedData(mid) {
            let idx = this.redData.findIndex(e => e.mid === mid);
            return this.redData[idx];
        },
        sendRed() {
            let data = this.redData;
            let list = [];
            for (let i = 0, len = data.length; i < len; i++) {
                if (data[i].active) {
                    list.push(
                        {
                            mid: data[i].mid,
                            amount: Number(data[i].money)
                        }
                    )
                }
            }
            this.$api.showModalInfo('SUCCESS', '發送成功');
            this.clearRedData();
            // this.$api.requestFetch(`/jackpot/give_red`, "POST", { list: list })   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '發送成功');
            //             this.clearRedData();
            //         } else {
            //             this.$api.showModalInfo('ERROR', '發送失敗' + `(${res.msg})`);
            //         }
            //     });
        },
        showJpModal(row) {
            if (!row.item.currency) {
                this.$api.showModalInfo('Error', '該玩家從未遊玩過!');
                return;
            }
            let percentOptions = [];
            for (let i = 0, len = this.percents.length; i < len; i++) {
                percentOptions.push(
                    {
                        text: `${this.percents[i] * 100}%`,
                        value: this.percents[i]
                    }
                )
            }
            this.$modal.show(jp_modal, {
                mid: row.item.mid,
                name: row.item.name,
                currency: row.item.currency,
                percentOptions: percentOptions
            }, {
                width:"30%", 
                height:"auto", 
                scrollable: true
            });
        },
        changePage(e) {
            if (this.page === e) {
                return;
            }
            this.getPlayers(e, this.tempKeyWord);
        },
        changeMode(e) {
            if (e.value) {
                this.clearRedData();
            }
            let idx = this.fields.findIndex(e => e.key === 'send');
            this.fields[idx].class = e.value ? 'text-center' : '';
        },
        clearRedData() {
            for (let i = 0, len = this.redData.length; i < len; i++) {
                this.redData[i].active = false;
            }
        },
        getPlayerInfo(mid, name, currency) {
            if (!currency) {
                this.$api.showModalInfo('Error', '該玩家從未遊玩過!');
                return;
            } 
            let promises = [];
            promises.push(this.$api.getPlayerGame(mid, '', ''));
            promises.push(this.$api.getPlayerInfo(name));
            Promise.all(promises)
                .then(e => this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' }))
                .catch(e => this.$api.showModalInfo('Error', '查詢失敗!' + `(${res.msg})`));
        },
        getJpInfo(row, type) {
            if (!row.value) {
                return;
            }
            let data = [];
            this.$modal.show(player_reward_modal, {
                items: data,
                title: `${row.item.name}的${this.$options.filters.toJpType(type)}領取資訊`,
            }, {
                width:"50%", 
                height:"auto", 
                scrollable: true
            });
            // this.$api.requestFetch(`/jackpot/player_award_list?mid=${row.item.mid}&type=${type}`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$modal.show(player_reward_modal, {
            //                 items: res.data,
            //                 title: `${row.item.name}的${this.$options.filters.toJpType(type)}領取資訊`,
            //             }, {
            //                 width:"50%", 
            //                 height:"auto", 
            //                 scrollable: true
            //             });
            //         } else {
            //             this.$api.showModalInfo('ERROR', '查詢失敗' + `(${res.msg})`);
            //         }
            //     });
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        },
        getOnlineTooltip(mid) {
            let player = this.onlinePlayers.players[mid];
            if (!player) return;
            let keys = Object.keys(player);
            let title = '';
            for (let i = 0, len = keys.length; i < len; i++) {
                let key = player[keys[i]];
                title += config.gid_name[key];
                title += i === keys.length - 1 ? `` : `, `;
            }
            return title;
        }
    }
}
</script>

<style lang="scss">
#reward-send {
    .close {
        font-size: .8rem;
    }
    .red-btn-color {
        background-color: #ff7482;
        &:hover {
            background-color: #cc5b66;
        }
    }
}
</style>



