<template>
    <div>
        <b-row align-v="center" class="card-header m-0">
            <b-col md="2">
                <span class="font-weight-bold" style="font-size: 30px">後台操作紀錄</span>
            </b-col>
            <b-col md="2">
                <b-input-group>
                    <b-input-group-prepend is-text>
                        類型
                    </b-input-group-prepend>
                    <b-form-select :options="typeOptions" v-model="keyWord.type"></b-form-select>
                </b-input-group>
            </b-col>
            <b-col md="2">
                <b-input-group>
                    <b-input-group-prepend is-text>
                        操作者
                    </b-input-group-prepend>
                    <b-form-input v-model="keyWord.operator" @keyup.native.enter="keyWordSearch()"></b-form-input>
                </b-input-group>
            </b-col>
            <b-col md="5">
                <date-time
                    :noQuick="true"
                    v-on:startDate="startDate=$event"
                    v-on:startTime="startTime=$event"
                    v-on:endDate="endDate=$event"
                    v-on:endTime="endTime=$event"
                ></date-time>
            </b-col>
            <b-col md="1">
                <b-btn variant="primary" @click="keyWordSearch">查詢</b-btn>
            </b-col>
        </b-row>
        <div class="p-2">
            <b-table 
                show-empty 
                bordered 
                hover 
                :current-page="currentPage"
                :per-page="per_page"
                :items="items" 
                :fields="fields" 
                empty-text="無資料" 
            >
                <template slot="name" slot-scope="row">{{ row.value || '未知' }}</template>
                <template slot="type" slot-scope="row">{{ row.value }}</template>
                <template slot="url" slot-scope="row">{{ row.value }}</template>
                <template slot="content" slot-scope="row">{{ row.value }}</template>
                <template slot="time" slot-scope="row">{{ row.value | formatTime }}</template>
            </b-table>
            <pagination-fast
                :totalRows="totalRows"
                :rows="per_page"
                :currentPage="currentPage"
                v-on:changePage="changePage($event)"
            ></pagination-fast>
            <b-pagination :total-rows="totalRows" :per-page="per_page" v-model="currentPage" align="right" :limit="10" @change="changePage"/>
        </div>
    </div>
</template>

<script>
import DateTime from '../date_time';
import PaginationFast from '../pagination_fast';

export default {
    created() {
        this.getType();
    },
    components: {
        DateTime,
        PaginationFast
    },
    data() {
        return {
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            typeOptions: [{ text: '全部', value: '' }],
            keyWord: { type: '' },
            lastSearch: {},
            totalRows: 0,
            per_page: 25,
            currentPage: 1,
            items: [],
            fields: [
                {
                    key: "name",
                    label: "操作者",
                    sortable: true,
                    class: 'text-left',
                },
                {
                    key: "type",
                    label: "類型",
                    sortable: true,
                    class: 'text-left',
                },
                {
                    key: "url",
                    label: "路徑",
                    sortable: true,
                    class: 'text-left',
                },
                {
                    key: "content",
                    label: "內容",
                    sortable: true,
                    class: 'text-left',
                },
                {
                    key: "time",
                    label: "時間",
                    sortable: true,
                    class: 'text-center',
                }
            ]
        }
    },
    methods: {
        keyWordSearch() {
            this.lastSearch = this.keyWord;
            this.getLogs(1);
        },
        changePage($event) {
            this.currentPage = $event ? $event : this.currentPage;
            setTimeout(() => document.documentElement.scrollTop = 0, 100);
        },
        getType() {
            let data = {
                操作: '操作'
            };
            let keys = Object.keys(data);
            for (let i = 0, len = keys.length; i < len; i++) {
                let value = data[keys[i]];
                this.typeOptions.push({ text: value, value: value });
            }
            // this.$api.requestFetch( `/account/type`, "GET")   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             let data = res.data;
            //             let keys = Object.keys(data);
            //             for (let i = 0, len = keys.length; i < len; i++) {
            //                 let value = data[keys[i]];
            //                 this.typeOptions.push({ text: value, value: value });
            //             }
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        getLogs(page) {
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            let url = `/account/logs?start_time=${totalStart}&end_time=${totalend}`;
            let type = this.lastSearch.type || '';
            let operator = this.lastSearch.operator || '';
            url += this.lastSearch ? `&type=${type}&operator=${operator}` : '';
            let data = [
                {
                    name: '人員1',
                    type: '操作1',
                    url: 'a/b',
                    content: '進行了操作1',
                    time: '2018.09.09'
                },
                {
                    name: '人員1',
                    type: '操作1',
                    url: 'a/b',
                    content: '進行了操作1',
                    time: '2018.09.09'
                },
                {
                    name: '人員1',
                    type: '操作1',
                    url: 'a/b',
                    content: '進行了操作1',
                    time: '2018.09.09'
                }
            ];
            if (data.length === 0) {
                this.$api.showModalInfo('Success', `無資料`);
            }
            this.totalRows = data.length;
            this.items = data;
            this.currentPage = 1;
            document.documentElement.scrollTop = 0;
            // this.$api.requestFetch(url, "GET")   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             let data = res.data;
            //             if (data.length === 0) {
            //                 this.$api.showModalInfo('Success', `無資料`);
            //             }
            //             this.totalRows = data.length;
            //             this.items = data;
            //             this.currentPage = 1;
            //             document.documentElement.scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
    }
}
</script>

<style>

</style>



