export let ws =  {
    ws: null,
    store: null,
    notify: null,
    shouldNotify: true,
    init(store, notify){
        this.store  = store;
        this.notify = notify;
    },
    login() {
        // this.ws = new WebSocket(config.ws);
        // this.ws.onopen = evt => {
        //     let loginData = {
        //         protocol: 100,
        //         data: {
        //             session: sessionStorage.getItem('sessionKey')
        //         }
        //     };
        //     this.store.commit('SETWSSTATUS', 0);
        //     this.ws.send(JSON.stringify(loginData));
        // };
        // this.ws.onmessage = evt => {
        //     let data = JSON.parse(evt.data);
        //     if (data.status) {
        //         switch (data.protocol) {
        //             case 100:
        //                 break;
        //             case 300:
        //                 let details = data.data;
        //                 this.popNotify(details);
        //                 this.store.commit('PUSHLOGS', details);
        //                 break;
        //             default:
        //                 console.log("protocal error");
        //         }
        //     } else {
        //         console.log("status false");
        //     }
        // };
        // this.ws.onclose = evt => {
        //     this.store.commit('SETWSSTATUS', 1);
        // };
        this.store.commit('SETWSSTATUS', 0);
    },
    reconnect() {
        if (this.ws.readyState === 2 || this.ws.readyState === 3) {
            this.login();
        }
    },
    popNotify(details) {
        if (!this.shouldNotify) {
            return;
        }
        if (details.type === 'process:alert' || details.type === 'process:msg') {
            this.notify({
                group: 'server-noti',
                type: details.type === 'process:alert' ? 'warn' : '',
                title: `${details.agent}-${details.name}`,
                text: details.msg,
                duration: 10000
            });
        }
    },
    close() {
        if (this.ws) {
            this.ws.close();
        }
    }
}


