<template>
    <div class="h-100">
        <b-navbar toggleable="md" type="dark" variant="dark" sticky>
            <b-navbar-nav class="ml-auto">
                <b-btn class="material-icons mr-1" @click="open">add_to_photos</b-btn>
                <b-btn class="material-icons" @click="$emit('close')">clear</b-btn>
            </b-navbar-nav>
        </b-navbar>
        <b-embed type="iframe" :src="url"></b-embed>
    </div>
</template>

<script>
export default {
    props: ['url'],
    methods: {
        open() {
            window.open(this.url);
        }
    }
}
</script>


