<template>
<modal name="modalEdit" width="30%" height="auto">
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">修改密碼</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('modalEdit')">×</button>
        </header>
        <div class="modal-body">
            <form>
                帳號:
                <b-form-input type="text" :placeholder="account" disabled></b-form-input>
                舊密碼:
                <b-form-input type="text" v-model="addData.old_password"></b-form-input>
                新密碼:
                <b-form-input type="text" v-model="addData.new_password"></b-form-input>
            </form>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$modal.hide('modalEdit')">Cancel</b-btn>
            <b-btn @click="add">OK</b-btn>
        </footer>
    </div>
</modal>        
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
    name: "ModalEdit",
    data() {
        return {
            addData: {
                old_password: "",
                new_password: ""
            }
        };
    },
    computed: {
        ...mapGetters({
            account: "getAccount"
        })
    },
    methods: {
        clearAddData() {
            this.addData = {
                old_password: "",
                new_password: "",
            };
        },
        add() {
            this.addData.account = this.account;
            // this.$api.requestFetch(`/account/edit`, "POST", this.addData)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status === true) {
            //             this.$emit('refresh');
            //             this.$api.showModalInfo('修改資訊', '修改成功');
            //         } else {
            //             this.$api.showModalInfo('修改資訊', '修改失敗');
            //         }
            // });
            this.$api.showModalInfo('修改資訊', '修改成功');
            this.clearAddData();
            this.$modal.hide('modalEdit');
        }
    }
};
</script>

<style lang="scss">

</style>



