<template>
    <v-chart :forceFit="true" :height="height" :data="data" :padding="padding">
        <v-tooltip />
        <v-legend />
        <v-axis :dataKey="x" />
        <v-axis :dataKey="y" position="right" :label="label"/>
        <v-bar :position="`${x}*${y}`" :color="color" :adjust="adjust"/>
    </v-chart>
</template>

<script>
const label = { offset: 12 };
const adjust = [{ type: 'dodge', marginRatio: 1 / 32 }];

export default {
    props: ['data', 'padding', 'height', 'x', 'y', 'color'],
    data() {
        return {
            adjust,
            label
        }
    },
}
</script>

<style>

</style>



