<template>
    <div id="fake-modal" class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">更改偽造JP權重</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-row>
                <b-col class="sub-view">
                    <b-list-group-item 
                        button
                        v-for="(item, key) in games" 
                        :key="key" 
                        class="d-flex justify-content-between"
                        v-if="!data.hasOwnProperty(key)"
                        @click="addData(key)"
                    >
                        {{item}}
                    </b-list-group-item>
                </b-col>
                <b-col class="sub-view">
                    <b-list-group-item v-for="(item, key) in data" :key="key">
                        <b-row>
                            <b-col>
                                <span>{{key | toGidName}}</span>
                            </b-col>
                            <b-col>
                                <b-input-group>
                                    <b-form-input type="number" min="1" class="text-right" style="border-radius: 0;" v-model="data[key]"></b-form-input>
                                    <b-btn class="material-icons ml-1" @click="delData(key)">clear</b-btn> 
                                </b-input-group>
                            </b-col>
                        </b-row>
                    </b-list-group-item>
                </b-col>
            </b-row>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$emit('close')">Cancel</b-btn>
            <b-btn @click="edit">OK</b-btn>
        </footer>
    </div>
</template>

<script>

export default {
    props: [ 'data', 'refresh' ],
    data() {
        return {
            games: config.gid_name
        }
    },
    methods: {
        addData(key) {
            this.$set(this.data, key, 1);
        },
        delData(key) {
            this.$delete(this.data, key);
        },
        edit() {
            let keys = Object.keys(this.data);
            if (!keys.length) {
                this.$api.showModalInfo('ERROR', '資料不得為空');
                return;
            }
            for (let i = 0, len = keys.length; i < len; i++) {
                let data = Number(this.data[keys[i]]);
                if (data <= 0) {
                    this.$api.showModalInfo('ERROR', '權重不得小於0');
                    return;
                }
                this.data[keys[i]] = Math.floor(data);
            }
            this.$api.showModalInfo('SUCCESS', '更改權重成功');
            // this.$api.requestFetch(`/jackpot/fake_gids_weight`, "POST", this.data)   
            //     .catch(err => alert(err)) 
            //     .then(res => this.checkRes(res));
            this.$emit('close');
        },
        checkRes(res) {
            if (res.status) {
                this.$api.showModalInfo('SUCCESS', '更改權重成功');
                this.refresh(res.data);
            } else {
                this.$api.showModalInfo('ERROR', '更改權重失敗' + `(${res.msg})`);
            }
        }
    }
}
</script>

<style lang="scss">
#fake-modal {
    .sub-view {
        overflow: auto;
        height: 50vh;
    }
}
</style>



