<template>
<div id="gameReport">
    <input-filed></input-filed>
    <list 
        v-on:showPlayersModal="showPlayersModal($event.title, $event.content)"
        v-on:showRoundsModal="showRoundsModal($event.title, $event.content, $event.sendData)"
    ></list>
    <modal name="playersTable" @before-close="resetModal" width="80%" height="auto" scrollable>
        <div class="border h-100">
            <header class="modal-header">
                <h5 class="modal-title">{{ modalInfo.title }}</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('playersTable')">×</button>
            </header>
            <div class="modal-body">
                <players-table :items="playersItems" v-on:getPlayerInfo="showPlayerInfoModal"></players-table>
            </div>
            <footer class="modal-footer">
                <button type="button" class="btn btn-primary" @click="$modal.hide('playersTable')">OK</button>
            </footer>
        </div>
    </modal>
    <modal id="roundsTable" name="roundsTable" @before-close="resetModal" width="80%" height="auto" scrollable>
        <div class="border h-100">
            <header class="modal-header">
                <h5 class="modal-title">{{ modalInfo.title }}</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('roundsTable')">×</button>
            </header>
            <div class="modal-body">
                <rounds-table :items="roundsItems" v-on:getPlayerInfo="showPlayerInfoModal"></rounds-table>
                <pagination-fast
                    :totalRows="totalRows"
                    :rows="per_page"
                    :currentPage="currentPage"
                    v-on:changePage="requestPage"
                ></pagination-fast>
                <b-pagination :total-rows="totalRows" :per-page="per_page" v-model="currentPage" align="right" :limit="10" @change="requestPage"/>
            </div>
            <footer class="modal-footer">
                <button type="button" class="btn btn-primary" @click="$modal.hide('roundsTable')">OK</button>
            </footer>
        </div>
    </modal>
</div>
</template>

<script>
import List from './list.vue';
import PlayersTable from './players_table.vue';
import RoundsTable from './rounds_table.vue';
import InputFiled from './input.vue';
import playerInfo from '../player_info';
import paginationFast from '../pagination_fast';
import { createNamespacedHelpers } from 'vuex';

const { mapGetters, mapActions } = createNamespacedHelpers('game_report');

export default {
    name: "gameReport",
    components: {
        List,
        PlayersTable,
        RoundsTable,
        InputFiled,
        playerInfo,
        paginationFast
    },
    data() {
        return {
            sendData: '',
            totalRows: 0,
            per_page: 25,
            currentPage: 1,
            modalInfo: {
                title: "",
                content: []
            },
        };
    },
    beforeDestroy: function() {
        this.clear();
    },
    computed: {
        playersItems() {
            return this.modalInfo.content;
        },
        roundsItems() {
            return this.modalInfo.content;
        },
    },
    methods: {
        requestPage(page) {
            // this.sendData.page = page - 1;
            // this.currentPage = page;
            // this.$api.requestFetch(`/report/game/rounds`, "POST", this.sendData)
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.addResult(res.data.list);
            //             this.modalInfo.content = res.data.list;
            //             document.getElementById('roundsTable').scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        showPlayerInfoModal() {
            this.$modal.show(playerInfo, {}, { scrollable: true, width: '80%', height: 'auto' })
        },
        showPlayersModal(title, content) {
            this.modalInfo.title = title;
            this.modalInfo.content = content;
            this.$modal.show('playersTable');
        },
        showRoundsModal(title, content, sendData) {
            console.log(content)
            this.addResult(content.list)
            this.sendData  = sendData;
            this.totalRows = content.total;
            this.modalInfo.title = title;
            this.modalInfo.content = content.list;
            this.$modal.show('roundsTable');
        },
        addResult(list) {
            for (let i = 0, len = list.length; i < len; i++) {
                list[i].result = list[i].win - list[i].bet;
            }
        },
        resetModal() {
            this.modalInfo.title = "";
            this.modalInfo.content = [];
            this.currentPage = 1;
        },
        ...mapActions(["clear"])
    },
}
</script>

<style lang="scss">
.gid-width {
    width: 5%;
}
@media (min-width: 992px) {
    .modal-xl {
        max-width: 1200px;
    }
}
</style>



