<template>
    <b-table 
        show-empty 
        bordered 
        fixed 
        hover 
        :items="items" 
        :fields="fields"
        :sort-by.sync="sort" 
        :sort-desc.sync="sortDesc"
        empty-text="無資料"
    >
        <template slot="currency" slot-scope="row">
            <div @click="requestCurrency(row.value)">{{ row.value }}</div>
        </template>
        <template slot="bet" slot-scope="row">{{ row.value | formatMoney(row.item.currency) | footCurrencyText(row.item.currency) }}</template>
        <template slot="win" slot-scope="row">{{ row.value | formatMoney(row.item.currency) | footCurrencyText(row.item.currency) }}</template>
        <template slot="result" slot-scope="row">
            <span :class="resultTextColor(row.value)">{{ row.value | formatMoney(row.item.currency) | footCurrencyText(row.item.currency) }}</span>
        </template>
    </b-table>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';

const { mapGetters, mapActions } = createNamespacedHelpers('game_report');

export default {
    name: "list_total",
    props: ['items'],
    components: {
    },
    created() {
        
    },
    data() {
        return {
            fields: [
                {
                    key: "currency",
                    label: "幣別",
                    sortable: true,
                    class: 'clickable'
                },
                {
                    key: "bet",
                    label: "下注金額",
                    sortable: true,
                    class: 'text-right',
                },
                {
                    key: "win",
                    label: "贏得金額",
                    sortable: true,
                    class: 'text-right'
                },
                {
                    key: "result",
                    label: "總輸贏",
                    sortable: true,
                    class: 'text-right'
                }
            ],
            sort: 'result',
            sortDesc: true
        }
    },
    computed: {
    },
    methods: {
        requestCurrency(currency) {
            this.$emit('currency', currency)
        },
        resultTextColor(val) {
            if (val >= 0) {
                return 'text-success';
            } else {
                return 'text-danger';
            }
        },
        ...mapActions([])
    }
}
</script>



