import * as types from './mutations_type.js'
import Vue from 'vue'

export const state = {
    isLogin: false,
    username: '',
    logs: [],
    isLoading: false,
    apiLevel: '',
    wsStatus: 3
}

export const mutations = {
    [types.SETLOGIN](state, isLogin) {
        this.state.isLogin = isLogin;
    },
    [types.SETUSERNAME](state, name) {
        sessionStorage.setItem('username', name);
        this.state.username = name;
    },
    [types.SETLOGS](state, logs) {
        this.state.logs = logs;
    },
    [types.PUSHLOGS](state, log) {
        this.state.logs.unshift(log);
    },
    [types.SETISLOADING](state, data) {
        this.state.isLoading = data;
    },
    [types.SETAPILEVEL](state, data) {
        this.state.apiLevel = data;
    },
    [types.SETWSSTATUS](state, data) {
        this.state.wsStatus = data;
    }
}


