<template>
<div id="accountController">
    <b-row class="card-header m-0">
        <b-col md="6">
            <span class="font-weight-bold" style="font-size: 30px">後台帳號管理</span>
        </b-col>
        <b-col md="6" class="text-right">
            <b-btn class="material-icons" style="font-size: 24px" @click="showAddModal()">add_box</b-btn>
        </b-col>
    </b-row>
    <b-table show-empty stacked="md" :items="items" :fields="fields">
        <template slot="name" slot-scope="row">{{ row.value }}</template>
        <template slot="account" slot-scope="row">{{ row.value }}</template>
        <template slot="level" slot-scope="row">
            <b-dropdown variant="link" no-caret>
                <template slot="button-content">{{ row.value | level }}</template>
                <b-dropdown-item v-for="(item, key) in level" :key="key" @click="showCheckModal('level', row.item.account, key)"> {{ item }} </b-dropdown-item>
            </b-dropdown>
        </template>
        <template slot="disabled" slot-scope="row">
            <b-dropdown variant="link" no-caret>
                <template slot="button-content">
                    <span :class="disabledTextColor(row.value)">{{ row.value | disabled }}</span>
                </template>
                <b-dropdown-item @click="showCheckModal('disabled', row.item.account, 0)">啟用</b-dropdown-item>
                <b-dropdown-item @click="showCheckModal('disabled', row.item.account, 1)">停用</b-dropdown-item>
            </b-dropdown>
        </template>
        <template slot="last_login_ip" slot-scope="row">{{ row.value || '無' }}</template>
        <template slot="last_login_at" slot-scope="row">{{ row.value | formatTime }}</template>
        <template slot="actions" slot-scope="row">
            <b-btn class="material-icons" v-b-tooltip.hover title="修改密碼" @click="showEditModal(row.item.account)">edit</b-btn>
        </template>
    </b-table>
    <modal-add v-on:refresh="refresh()"></modal-add>
    <modal-edit v-on:refresh="refresh()"></modal-edit>
</div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import ModalAdd from "./modal_add";
import ModalEdit from "./modal_edit";
import ModalCheck from '../modal/check';

export default {
    name: "ListInfo",
    components: {
        ModalAdd,
        ModalEdit
    },
    data() {
        return {
            fields: [
                {
                    key: "name",
                    label: "名稱",
                    sortable: true
                },
                {
                    key: "account",
                    label: "帳號",
                    sortable: true
                },
                {
                    key: "level",
                    label: "角色",
                    sortable: true
                },
                {
                    key: "disabled",
                    label: "狀態",
                    sortable: true
                },
                {
                    key: "last_login_ip",
                    label: "最後登入IP",
                    sortable: true
                },
                {
                    key: "last_login_at",
                    label: "最後登入時間",
                    sortable: true
                },
                {
                    key: "actions",
                    label: "動作",
                    class: 'text-center'
                }
            ],
            level: config.level
        };
    },
    created: function () {
        let data = [
            {
                name: '人員1',
                account: 'man1',
                level: 999,
                disabled: 1,
                last_login_ip: '87.87.87.87',
                last_login_at: '2019.09.09'
            },
            {
                name: '人員2',
                account: 'man2',
                level: 100,
                disabled: 0,
                last_login_ip: '87.87.87.87',
                last_login_at: '2019.09.09'
            },
        ]
        this.refresh();
        this.setItems(data);
    },
    computed: {
        ...mapGetters({
            items: "getItems",
        })
    },
    methods: {
        refresh() {
            // this.$api.requestFetch(`/account`, "GET")   
            //     .catch(err => alert(err)) 
            //     .then(res => this.setItems(res.data));
        },
        edit(type, account, value) {
            let send = { account: account };
            if (type === 'level') {
                send.level = value;
            } else {
                send.disabled = value;
            }
            this.$api.showModalInfo('修改資訊', '修改成功');
            // this.$api.requestFetch(`/account/edit`, "POST", send)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status === true) {
            //             this.refresh();
            //             this.$api.showModalInfo('修改資訊', '修改成功');
            //         } else {
            //             this.$api.showModalInfo('修改資訊', '修改失敗');
            //         }
            // });
        },
        disabledTextColor(val) {
            if (val) {
                return 'text-danger';
            } else {
                return 'text-success';
            }
        },
        showAddModal() {
            this.$modal.show("modalAdd");
        },
        showEditModal(account) {
            this.setAccount(account);
            this.$modal.show("modalEdit");
        },
        showCheckModal(type, account, value) {
            this.$modal.show(
                ModalCheck, 
                { title: '提示', content: `你確定要對${account}進行修改嗎?`, action: () => this.edit(type, account, value)}, 
                { scrollable: true, width: "30%", height: "auto" }
            );
        },
        ...mapActions(["setAccount", "setItems"])
    }
}
</script>

<style lang="scss">
.table {
    margin: 0;
    td {
        vertical-align: baseline;
    }
}
</style>



