<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">{{ action }}參數輸入</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-input v-model="triggerParam"></b-input>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">Cancel</button>
            <button type="button" class="btn btn-primary" @click="send()">OK</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'target', 'id', 'action' ],
    data() {
        return {
            triggerParam: ''
        }
    },
    methods: {
        send() {
            let sendData = {
                action: this.action,
                param: this.triggerParam,
                name: this.target,
                id: this.id
            };
            // this.$api.requestFetch(`/agent/trigger`, "POST", sendData)
            //     .catch(err => alert(err))
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('Success', `執行成功(回傳訊息:${res.data[0].result})`);
            //         } else {
            //             this.$api.showModalInfo('Error', `執行失敗(${res.msg})`);
            //         }
            //     });
            this.$api.showModalInfo('Success', `執行成功`);
            this.$emit('close');
        }
    }
}
</script>

<style>

</style>



