<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">{{ title }}</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-table
                hover
                show-empty 
                :items="detail" 
                :fields="fields" 
                empty-text="無資料"
            >
                <template slot="gid" slot-scope="row">{{ row.value | toGidName }}</template>
                <template slot="ip" slot-scope="row">{{ row.value }}</template>
                <template slot="device" slot-scope="row">{{ row.value }}</template>
                <template slot="os" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="os_ver" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="browser_type" slot-scope="row">{{ row.value }}</template>
                <template slot="browser_ver" slot-scope="row">{{ row.value }}</template>
                <template slot="start_time" slot-scope="row">{{ row.value | formatTime }}</template>
                <template slot="end_time" slot-scope="row">{{ row.value | formatTime }}</template>
                <template slot="total_time" slot-scope="row">{{ totalTimeTrans(row) | timeDurationNoDay(1000, true)}}</template>
            </b-table>
            <pagination-fast
                :totalRows="totalRows"
                :rows="rows"
                :currentPage="currentPage"
                v-on:changePage="getDetail"
            ></pagination-fast>
            <b-pagination :total-rows="totalRows" :per-page="rows" v-model="currentPage" align="right" :limit="10" @change="getDetail"/>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">OK</button>
        </footer>
    </div>
</template>

<script>
import dayjs from 'dayjs';
import paginationFast from '../pagination_fast';

export default {
    props: [ 'title', 'mid' ],
    created() {
        this.getDetail(1);
    },
    components: {
        paginationFast
    },
    data() {
        return {
            detail: [],
            totalRows: 0,
            rows: 25,
            currentPage: 1,
            fields: [
                {
                    key: "gid",
                    label: "遊戲名稱",
                    class: 'text-center'
                },
                {
                    key: "ip",
                    label: "IP位置",
                    class: 'text-center'
                },
                {
                    key: "device",
                    label: "使用裝置",
                    class: 'text-center'
                },
                {
                    key: "os",
                    label: "作業系統",
                    class: 'text-center'
                },
                {
                    key: "os_ver",
                    label: "作業系統版本",
                    class: 'text-center'
                },
                {
                    key: "browser_type",
                    label: "瀏覽器",
                    class: 'text-center'
                },
                {
                    key: "browser_ver",
                    label: "瀏覽器版本",
                    class: 'text-center'
                },
                {
                    key: "start_time",
                    label: "開始遊玩時間",
                    class: 'text-center'
                },
                {
                    key: "end_time",
                    label: "結束遊玩時間",
                    class: 'text-center'
                },
                {
                    key: "total_time",
                    label: "總遊玩時間",
                    class: 'text-center'
                }
            ]
        }
    },
    methods: {
        getDetail(page) {
            this.currentPage = page - 1;
            let url = `/member_list/logs?page=${this.currentPage}&rows=${this.rows}&mid=${this.mid}`;
            let data = {
                lists: [
                    {
                        gid: 2202,
                        ip: '87.87.87.87',
                        device: 'desktop',
                        os: 'windows',
                        os_ver: '87',
                        browser_type: 'chrome',
                        browser_ver: '87',
                        start_time: '2018.09.09',
                        end_time: '2018.09.10'
                    },
                    {
                        gid: 2202,
                        ip: '87.87.87.87',
                        device: 'desktop',
                        os: 'windows',
                        os_ver: '87',
                        browser_type: 'chrome',
                        browser_ver: '87',
                        start_time: '2018.09.09',
                        end_time: '2018.09.10'
                    },
                    {
                        gid: 2202,
                        ip: '87.87.87.87',
                        device: 'desktop',
                        os: 'windows',
                        os_ver: '87',
                        browser_type: 'chrome',
                        browser_ver: '87',
                        start_time: '2018.09.09',
                        end_time: '2018.09.10'
                    },
                ],
                total: 3
            }
            this.detail = data.lists;
            this.totalRows = data.total;
            document.documentElement.scrollTop = 0;
            // this.$api.requestFetch(url, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.detail = res.data.lists;
            //             this.totalRows = res.data.total;
            //             document.documentElement.scrollTop = 0;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        totalTimeTrans(row) {
            let total = dayjs(row.item.end_time) - dayjs(row.item.start_time);
            return total;
        }
    }
}
</script>

<style>

</style>



