<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">{{ title }}</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <pre>{{ content }}</pre>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$emit('close')">Cancel</button>
            <button type="button" class="btn btn-primary" @click="ok()">OK</button>
        </footer>
    </div>
</template>

<script>
export default {
    props: [ 'title', 'content', 'action' ],
    methods: {
        ok() {
            this.action();
            this.$emit('close');
        }
    }
}
</script>

<style>

</style>



