<template>
<b-card no-body>
    <b-row class="card-header m-0">
        <b-col cols="11" style="text-align: left; font-size: 25px">{{target}} (簡介: {{agentData.os.intro}})</b-col>
        <b-col cols="1" class="text-right">
            <b-btn class="material-icons" @click="$emit('fresh')" style="font-size: 18px">autorenew</b-btn>
        </b-col>
    </b-row>
    <b-card-body>
        <b-row class="m-0">
            <b-col>
                <b-list-group flush>
                    <b-list-group-item>主機名稱: {{agentData.os.info.hostname}}</b-list-group-item>
                    <b-list-group-item>啟動時間: {{agentData.os.info.uptime | timeDuration}}</b-list-group-item>
                    <b-list-group-item>OS類型: {{agentData.os.info.type}}</b-list-group-item>
                    <b-list-group-item>OS平台: {{agentData.os.info.platform}}</b-list-group-item>
                </b-list-group>
            </b-col>
            <b-col>
                <b-list-group flush>
                    <b-list-group-item>CPU架構: {{agentData.os.info.cpu_arch}}</b-list-group-item>
                    <b-list-group-item>CPU數量: {{agentData.os.monit.cpu_count}}</b-list-group-item>
                    <b-list-group-item>CPU使用: {{ (agentData.os.monit.cpu_use * 100).toFixed(2) }}%</b-list-group-item>
                    <b-list-group-item>Node.js版本: {{agentData.os.info.node_ver}}</b-list-group-item>
                </b-list-group>
            </b-col>
            <b-col>
                <b-list-group flush>
                    <b-list-group-item>記憶體(總量): {{agentData.os.monit.mem_total | bitsToMB}}MB</b-list-group-item>
                    <b-list-group-item>記憶體(可用): {{agentData.os.monit.mem_free | bitsToMB}}MB</b-list-group-item>
                    <b-list-group-item>記憶體(已用): {{agentData.os.monit.mem_use | bitsToMB}}MB</b-list-group-item>
                </b-list-group>
            </b-col>
            <b-col>
                <b-list-group flush>
                    <b-list-group-item>前1分鐘平均附載: {{ (agentData.os.monit.loadavg[0] * 100).toFixed(2) }}%</b-list-group-item>
                    <b-list-group-item>前5分鐘平均附載: {{ (agentData.os.monit.loadavg[1] * 100).toFixed(2) }}%</b-list-group-item>
                    <b-list-group-item>前15分鐘平均附載: {{ (agentData.os.monit.loadavg[2] * 100).toFixed(2) }}%</b-list-group-item>
                </b-list-group>
            </b-col>
        </b-row>
    </b-card-body>
</b-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
    name: "OsInfo",
    data() {
        return {};
    },
    created: function () {

    },
    computed: {
        ...mapGetters({
            agentData: "getAgentData",
            target: "getTarget"
        })
    },
    methods: {
        
    }
};
</script>

<style lang="scss">

</style>



