<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">更改密碼</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <form>
                <b-form-group label="舊密碼:">
                    <b-form-input type="text" v-model="addData.old_password"></b-form-input>
                </b-form-group>
                <b-form-group label="新密碼:">
                    <b-form-input type="text" v-model="addData.new_password"></b-form-input>
                </b-form-group>
            </form>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$emit('close')">Cancel</b-btn>
            <b-btn @click="add">OK</b-btn>
        </footer>
    </div>
</template>

<script>
import info from './info';

export default {
    data() {
        return {
            addData: {
                old_password: "",
                new_password: ""
            }
        }
    },
    methods: {
        clearAddData() {
            this.addData = {
                old_password: "",
                new_password: ""
            };
        },
        add() {
            // this.$api.requestFetch(`/admin/edit_self`, "POST", this.addData)   
            //     .catch(err => alert(err)) 
            //     .then(res => this.checkRes(res));
            this.$api.showModalInfo('SUCCESS', '修改成功');
            this.clearAddData();
            this.$emit('close');
        },
        checkRes(res) {
            if (res.status) {
                this.$api.showModalInfo('SUCCESS', '修改成功');
                this.$api.logOut(false);
            } else {
                this.$api.showModalInfo('ERROR', '修改失敗');
            }
            
        }
    }
}
</script>

<style>

</style>



