<template>
    <div>
        <div class="card-header">
            <b-row align-v="center" class="m-0">
                <b-col md="2">
                    <span class="font-weight-bold" style="font-size: 30px">玩家分組</span>
                </b-col>
                <b-col md="10" class="text-right">
                    <b-btn class="material-icons" @click="add" style="font-size: 24px" v-b-tooltip.hover title="新增分組">add_box</b-btn>
                </b-col>
            </b-row>
        </div>
        <div v-for="(item, key) in data" :key="key" class="p-2 border">
            <b-table
                hover
                show-empty 
                bordered 
                caption-top
                :items="item.details" 
                :fields="fields" 
                empty-text="無資料"
            >
                <template slot="table-caption">
                    <span style="font-size: 24px; vertical-align: middle;">分組ID: {{ item.id }}</span>
                    <b-btn @click="getMember(item.id)" class="ml-2">檢視玩家</b-btn>
                </template>
                <template slot="name" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="type" slot-scope="row">{{ row.value || '無資料' }}</template>
                <template slot="value" slot-scope="row">
                    <div :id="`${item.id}${row.item.name}`">{{ row.value || '無資料' }}</div>
                    <edit-popover :id="item.id" :item="row.item" :fresh="getData"></edit-popover>
                </template>
                <template slot="updated_at" slot-scope="row">{{ row.value | formatTime }}</template>
                <template slot="action" slot-scope="row">
                    <b-btn 
                        class="material-icons align-middle" 
                        v-b-tooltip.hover 
                        title="刪除" 
                        @click="checkModal(item.id, row.item.name)"
                    >
                        delete
                    </b-btn>
                </template>
            </b-table>
        </div>
    </div>
</template>

<script>
import addModal from './add_modal'
import editPopover from './edit_popover'
import memberModal from './member_modal'
import ModalCheck from '../modal/check';

export default {
    created() {
        this.getData();
    },
    components: {
        editPopover
    },
    data() {
        return {
            data: [],
            fields: [
                {
                    key: "name",
                    label: "名稱",
                    class: 'text-center'
                },
                {
                    key: "type",
                    label: "類別",
                    class: 'text-center'
                },
                {
                    key: "value",
                    label: "設定值",
                    class: 'text-center clickable'
                },
                {
                    key: "updated_at",
                    label: "最近更新時間",
                    class: 'text-center'
                },
                {
                    key: "action",
                    label: "選項",
                    class: 'text-center'
                }
            ]
        }
    },
    methods: {
        checkModal(id, property) {
            this.$modal.show(
                ModalCheck, 
                { title: '提示', content: `你確定要刪除分組ID${id}的${property}嗎?`, action: () => this.deleteOne(id, property)}, 
                { scrollable: true, width: "30%", height: "auto" }
            );
        },
        deleteOne(id, property) {
            this.$api.showModalInfo('SUCCESS', '刪除成功'); 
            // let body = { group_id: id, property: property };
            // this.$api.requestFetch(`/member_group/delete`, "POST", body)   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$api.showModalInfo('SUCCESS', '刪除成功'); 
            //             this.getData();
            //         } else {
            //             this.$api.showModalInfo('ERROR', '刪除失敗' + `(${res.msg || res.message})`);
            //         }
            //     });
        },
        getMember(id) {
            let data = [
                '人員1', '人員1', '人員1', '人員1', '人員1', '人員1', '人員1'
            ]
            this.$modal.show(
                memberModal, 
                { data: data }, 
                { scrollable: true, width: "50%", height: "auto" }
            );
            // this.$api.requestFetch(`/member_group/getMember?id=${id}`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.$modal.show(
            //                 memberModal, 
            //                 { data: res.data }, 
            //                 { scrollable: true, width: "50%", height: "auto" }
            //             );
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        getData() {
            let data = [
                {
                    id: 0,
                    details: [
                        {
                            name: 'rtp',
                            type: 'number',
                            value: 0.87,
                            updated_at: '2018-09-09'
                        },
                        {
                            name: '神秘福利',
                            type: 'boolean',
                            value: true,
                            updated_at: '2018-09-09'
                        }
                    ]
                },
                {
                    id: 1,
                    details: [
                        {
                            name: 'rtp',
                            type: 'number',
                            value: 0.887,
                            updated_at: '2018-09-09'
                        }
                    ]
                },
            ]
            this.data = data;
            // this.$api.requestFetch(`/member_group`, "GET")   
            //     .catch(err => this.alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.data = res.data;
            //         } else {
            //             this.$api.showModalInfo('ERROR', '無法取得資料' + `(${res.msg})`);
            //         }
            //     });
        },
        add() {
            this.$modal.show(
                addModal, 
                { refresh: this.getData}, 
                { scrollable: true, width: "30%", height: "auto" }
            );
        }
    }
}
</script>

<style>

</style>



