<template>
    <h6 class="text-right" v-if="totalRows">
        {{`目前於${currentPage}/${Math.ceil(totalRows / rows)}頁`}}
        前往第<input v-model="targetPage" type="number" style="width: 72px" class="m-2 text-center" min="1" @keyup.enter="goPage"/>頁
        <b-btn variant="primary" @click="goPage">go</b-btn>
    </h6>
</template>

<script>
export default {
    props: ['totalRows', 'rows', 'currentPage'],
    data() {
        return {
            targetPage: 1
        }
    },
    methods: {
        goPage() {
            let tempPage = Math.floor(this.targetPage);
            if (tempPage > Math.ceil(this.totalRows / this.rows) || tempPage < 1) {
                return;
            }
            this.$emit('changePage', tempPage);
        }
    }
}
</script>

<style>

</style>



