import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/login'
import Home from '../components/home'
import AgentController from '../components/agent_controller/agent_controller'
import AccountController from '../components/account_controller/account_controller'
import GameReport from '../components/game_report/game_report'
import MemberReport from '../components/member_report/member_report'
import MemberDayReport from '../components/member_day_report/member_day_report'
import ServerConfig from '../components/server_config/server_config'
import ProfitLossReport from '../components/profit_loss_report/profit_loss_report'
import TicketList from '../components/ticket_list/ticket_list'
import Jackpot from '../components/jackpot/jackpot'
import GameList from '../components/game_list/game_list'
import MemberList from '../components/member_list/member_list'
import MemberLogs from '../components/member_logs/member_logs'
import GameAnalysis from '../components/game_analysis/game_analysis'
import MemberAnalysis from '../components/member_analysis/member_analysis'
import OperatorLog from '../components/operator_log/operator_log'
import DbInfo from '../components/dbinfo/dbinfo'
import MemberGroup from '../components/member_group/member_group'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  base: __dirname,
  routes: [
    {
      path: '/login',
      component: Login,
      meta: { requiresAuth: false },
    },
    {
      path: '/home',
      component: Home,
      meta: { requiresAuth: true },
    },
    {
      path: '/agent',
      component: AgentController,
      meta: { requiresAuth: true },
    },
    {
      path: '/account',
      component: AccountController,
      meta: { requiresAuth: true },
    },
    {
      path: '/game_report',
      component: GameReport,
      meta: { requiresAuth: true },
    },
    {
      path: '/member_report',
      component: MemberReport,
      meta: { requiresAuth: true },
    },
    {
      path: '/member_day_report',
      component: MemberDayReport,
      meta: { requiresAuth: true },
    },
    {
      path: '/profit_loss_report',
      component: ProfitLossReport,
      meta: { requiresAuth: true },
    },
    {
      path: '/ticket',
      component: TicketList,
      meta: { requiresAuth: true },
    },
    {
      path: '/server_config',
      component: ServerConfig,
      meta: { requiresAuth: true },
    },
    {
      path: '/jackpot',
      component: Jackpot,
      meta: { requiresAuth: true },
    },
    {
      path: '/game_list',
      component: GameList,
      meta: { requiresAuth: true },
    },
    {
      path: '/member_list',
      component: MemberList,
      meta: { requiresAuth: true },
    },
    {
      path: '/member_logs',
      component: MemberLogs,
      meta: { requiresAuth: true },
    },
    {
      path: '/game_analysis',
      component: GameAnalysis,
      meta: { requiresAuth: true },
    },
    {
      path: '/member_analysis',
      component: MemberAnalysis,
      meta: { requiresAuth: true },
    },
    {
      path: '/operator_log',
      component: OperatorLog,
      meta: { requiresAuth: true },
    },
    {
      path: '/dbinfo',
      component: DbInfo,
      meta: { requiresAuth: true },
    },
    {
      path: '/member_group',
      component: MemberGroup,
      meta: { requiresAuth: true },
    },
    { path: '/*', redirect: '/login' }
  ]
});
export default router


