import Vue from 'vue'
import types from './types'

// state
export const state = {
    items: [],
    currency: '',
    startTime: '',
    endTime: '',
}

// mutations
export const mutations = {
    [types.SETITEMS](state, data) {
        state.items = data;
    },
    [types.SETCURRENCY](state, data) {
        state.currency = data;
    },
    [types.SETSTARTTIME](state, data) {
        state.startTime = data;
    },
    [types.SETENDTIME](state, data) {
        state.endTime = data;
    },
    [types.CLEAR](state, data) {
        state.items = [];
        state.startTime = '';
        state.endTime = '';
        state.currency = '';
    }
}


