<template>
<div>
    <v-chart :forceFit="true" :height="height" :data="data" :scale="scale" :padding="padding">
        <v-tooltip />
        <v-legend/>
        <v-axis
            :data-key="y2"
            :grid="axisGrid"
            :label="axisLabel"
        />
        <v-bar :position="`${x}*${y}`" color="key" :adjust="[{ type: 'dodge', marginRatio: 1 / 32 }]" />
        <v-line :position="`${x}*${y2}`" color="#fdae6b" :label="label"/>
        <v-point shape="circle" :position="`${x}*${y2}`" color="#fdae6b"/>
    </v-chart>
</div>
</template>

<script>
export default {
    props: ['data', 'x', 'y', 'y2', 'height', 'padding'],
    computed: {
        label() {
            return [this.y2, { offset: -16, textStyle: { fill: '#fdae6b' } } ];
        },
        scale() {
            return [{ dataKey: this.y2, max: 2, min: 0}, {dataKey: this.x, type: 'timeCat'}];
        }
    },
    data() {
        return {
            adjust: [{ type: 'dodge', marginRatio: 1 / 32 }],
            axisLabel: { textStyle: { fill: '#fdae6b' } },
            axisGrid: null,
        }
    }
}
</script>

<style>

</style>



