import Vue from 'vue'
import types from './types'

// state
export const state = {
    account: '',
    items: []
}

// mutations
export const mutations = {
    [types.SETACCOUNT](state, data) {
        state.account = data;
    },
    [types.SETITEMS](state, data) {
        state.items = data;
    },
    [types.UPDATEITEMS](state, data) {
        let items = state.items;
        for (let i = 0, len = items.length; i < len; i++) {
            if(items[i].account === data.account) { //如果帳號相同，代表是在做編輯
                items.splice(i, 1, data);
                break;
            }
            if (i === len - 1) { //找到最後都沒有帳號相同的，代表在做創帳號
                items.push(data);
            } 
        }
    },
}


