<template>
<div id="input-filed">
    <b-row align-v="center" class="card-header m-0">
        <b-col md="2">
            <span class="font-weight-bold" style="font-size: 30px">玩家日報表</span>
        </b-col>
        <b-col md="2">
            <b-input-group prepend="幣別">
                <b-form-select v-model="currency" :options="currencyOptions"/>
            </b-input-group>
        </b-col>
        <b-col md="7">
            <date-time
                v-on:startDate="startDate=$event"
                v-on:startTime="startTime=$event"
                v-on:endDate="endDate=$event"
                v-on:endTime="endTime=$event"
            ></date-time>
        </b-col>
        <b-col md="1">
            <b-btn variant="primary" @click="getMemberReport()">查詢</b-btn>
        </b-col>
    </b-row>
    <div class="p-2">
        <b-row align-v="center" class="m-0">
            <b-col md="11">
                <b-form-group v-if="dayOptions.length > 0" label="選取指定的日期: ">
                    <b-form-checkbox v-model="isAllSelect" @change="toggleAll">{{ isAllSelect ? '清除' : '全選' }}</b-form-checkbox>
                    <b-form-checkbox-group 
                        v-model="chooseDayLocal"
                        :options="dayOptions">
                    </b-form-checkbox-group>
                </b-form-group>
            </b-col>
        </b-row>
        <div v-if="dayOptions.length > 0">
            <div>標示遊玩天數超過<input v-model="newTargetLocal" type="number" style="width: 72px" class="m-2 text-center" min="0" max="30"/>天之玩家(最多30天)</div>
            <div>篩選玩家 <b-btn @click="$modal.show('playerSelect')" style="font-size: 10px">已選取{{ playerSelectedNum }}位玩家</b-btn></div>
        </div>
    </div>
    <list 
        v-on:showRoundsModal="$emit('showRoundsModal', $event)"
        :targetPlayers="sendTargetPlayer"
    ></list>
    <modal name="playerSelect" width="50%" height="auto" scrollable>
        <div class="border h-100">
            <header class="modal-header">
                <h5 class="modal-title">篩選玩家</h5><button type="button" aria-label="Close" class="close" @click="$modal.hide('playerSelect')">×</button>
            </header>
        </div>
        <div class="modal-body">
            <b-form-group>
                <template slot="label">
                    <b-form-checkbox @change="toggleAllPlayer">選取欲篩選之玩家(已篩選{{ playerSelectedNum }}位):</b-form-checkbox>
                </template>
                <b-list-group>
                    <b-list-group-item 
                        button
                        v-for="(item, key) in playDays" :key="key" 
                        @click="targetPlayer[item.mid]=!targetPlayer[item.mid]"
                        class="d-flex justify-content-between"
                    >
                        <b-form-checkbox v-model="targetPlayer[item.mid]">{{players[item.mid]}}</b-form-checkbox>
                        遊玩天數: {{item.days}}天
                    </b-list-group-item>
                </b-list-group>
            </b-form-group>
        </div>
        <footer class="modal-footer">
            <button type="button" class="btn btn-primary" @click="$modal.hide('playerSelect')">Cancel</button>
            <button type="button" class="btn btn-primary" @click="filterPlayer">OK</button>
        </footer>
    </modal>
</div>
</template>

<script>
import { from } from 'rxjs'
import { filter, toArray, distinct, map, concatMap } from 'rxjs/operators'
import List from './list';
import DateTime from '../date_time'
import { createNamespacedHelpers } from 'vuex';
const { mapGetters, mapActions } = createNamespacedHelpers('member_report');

export default {
    name: "input-filed",
    components: {
        DateTime,
        List
    },
    data() {
        return {
            allSelected: false,
            targetPlayer: '',
            sendTargetPlayer: '',
            newTarget: 2,
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
            currency: 'TWD',
            currencyOptions: [],
            dayOptions: []
        };
    },
    created: function () {
        this.getCurrencyOption();
    },
    computed: {
        isAllSelect: {
            get () {
                this.allSelected = this.chooseDay.length === this.dayOptions.length ? true : false;
                return this.allSelected
            },
            set (value) {
                this.allSelected = value;
            }
        },
        playerSelectedNum() {
            let count = 0;
            this.$subscribeTo(
                from(Object.keys(this.targetPlayer)).pipe(
                    filter(e => this.targetPlayer[e] === true),
                ), e => count++);
            return count;
        },
        chooseDayLocal: {
            get () {
                return this.chooseDay
            },
            set (value) {
                this.setChooseDay(value)
            }
        },
        newTargetLocal: {
            get () {
                return this.newTarget;
            },
            set (value) {
                this.setNewTarget(value);
            }
        },
        ...mapGetters({
            chooseDay: "getChooseDay",
            players: "getPlayers",
            playDays: "getNewPlayer"
        })
    },
    methods: {
        getMemberReport() {
            this.setChooseDay([]);
            this.dayOptions = []; 
            this.sendTargetPlayer = '';
            this.newTargetLocal = 2;
            let totalStart = `${this.startDate} ${this.startTime.HH}:${this.startTime.mm}:${this.startTime.ss}`;
            let totalend   = `${this.endDate} ${this.endTime.HH}:${this.endTime.mm}:${this.endTime.ss}`;
            let body = {
                currency: this.currency,
                start_time: totalStart,
                end_time: totalend
            };
            let data = {
                lists: [
                    {
                        date: '2018-09-01',
                        details: [
                            {
                                mid: 0,
                                rounds: 50,
                                bet: 20,
                                win: 30
                            },
                            {
                                mid: 1,
                                rounds: 10,
                                bet: 500,
                                win: 30
                            },
                            {
                                mid: 2,
                                rounds: 20,
                                bet: 100,
                                win: 400
                            },
                        ]
                    },
                    {
                        date: '2018-09-02',
                        details: [
                            {
                                mid: 0,
                                rounds: 50,
                                bet: 20,
                                win: 30
                            }
                        ]
                    },
                    {
                        date: '2018-09-03',
                        details: [
                            {
                                mid: 0,
                                rounds: 50,
                                bet: 20,
                                win: 30
                            }
                        ]
                    },
                ],
                players: [
                    '人員1', '人員2', '人員3'
                ]
            };
            if (data.length === 0) {
                this.$api.showModalInfo('Success', `無資料`);
                return;
            }
            let lists = data.lists;
            for (let i = 0, len = lists.length; i < len; i++) {
                let details = lists[i].details;
                for (let j = 0, len2 = details.length; j < len2; j++) {
                    details[j].result = details[j].win - details[j].bet;
                }
            }
            this.setItems(lists);
            this.setPlayers(data.players);
            this.setTargetPlayer(data.players);
            this.setCurrency(this.currency);
            this.setStartTime(totalStart);
            this.setEndTime(totalend);
            this.getDayOption(lists);
            this.setChooseDay(this.dayOptions);
            this.getNewPlayer(lists);
            // this.$api.requestFetch(`/report/member/day`, "POST", body)   
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             let data = res.data;
            //             if (data.length === 0) {
            //                 this.$api.showModalInfo('Success', `無資料`);
            //                 return;
            //             }
            //             let lists = data.lists;
            //             for (let i = 0, len = lists.length; i < len; i++) {
            //                 let details = lists[i].details;
            //                 for (let j = 0, len2 = details.length; j < len2; j++) {
            //                     details[j].result = details[j].win - details[j].bet;
            //                 }
            //             }
            //             this.setItems(lists);
            //             this.setPlayers(data.players);
            //             this.setTargetPlayer(data.players);
            //             this.setCurrency(this.currency);
            //             this.setStartTime(totalStart);
            //             this.setEndTime(totalend);
            //             this.getDayOption(lists);
            //             this.setChooseDay(this.dayOptions);
            //             this.getNewPlayer(lists);
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        getNewPlayer(data) {
            let mids = [];
            this.$subscribeTo(
                from(data).pipe(
                    concatMap(e => e.details),
                    map(e => e.mid),
                    distinct(),
                    toArray()
                ), e => mids = e);
            
            let _data = [
                {
                    mid: 0,
                    days: 1
                },
                {
                    mid: 1,
                    days: 30
                },
                {
                    mid: 2,
                    days: 20
                },
            ];
            this.setNewPlayer(_data);
            // this.$api.requestFetch(`/report/member/player_days`, "POST", { mids: mids }, false)
            //     .catch(err => alert(err)) 
            //     .then(res => {
            //         if (res.status) {
            //             this.setNewPlayer(res.data);
            //         } else {
            //             this.$api.showModalInfo('Error', `查詢失敗`);
            //         }
            //     });
        },
        setTargetPlayer(players) {
            let keys = Object.keys(players);
            let data = {};
            for (let i = 0, len = keys.length; i < len; i++) {
                data[keys[i]] = false;
            }
            this.targetPlayer = data;
        },
        filterPlayer() {
            let keys = Object.keys(this.targetPlayer);
            let temp = [];
            for (let i = 0, len = keys.length; i < len; i++) {
                if (this.targetPlayer[keys[i]]) {
                    temp.push(keys[i])
                }
            }
            this.sendTargetPlayer = temp;
            this.$modal.hide('playerSelect');
        },
        toggleAll(checked) {
            this.chooseDayLocal = checked ? this.dayOptions.slice() : [];
        },
        toggleAllPlayer(checked) {
            this.$subscribeTo(
                from(Object.keys(this.targetPlayer)), e => this.targetPlayer[e] = checked ? true : false);
        },
        getDayOption(items) {
            this.$subscribeTo(
                from(items).pipe(
                    map(e => e.date),
                    distinct(),
                    toArray()
                ), e => this.dayOptions = e);
        },
        getCurrencyOption() {
            let currency = config.currency;
            let keys    = Object.keys(currency);
            for (let i = 0, len = keys.length; i < len; i++) {
                this.currencyOptions.push({ value: keys[i], text: currency[keys[i]] });
            }
        },
        ...mapActions(["setItems", "setCurrency", "setStartTime", "setEndTime", "setChooseDay", "setPlayers", "setNewPlayer", "setNewTarget"])
    }
}
</script>


