<template>
  <div id="login">
    <div class="login-title">
      <h1 style="font-size: 40px;">{{ title }}</h1>
      <p style="font-size: 20px;">帳號密碼皆為123</p>
      <!-- <b-btn variant="primary" @click="get">GET</b-btn> -->
    </div>
    <b-row class="justify-content-md-center">
      <b-col md="3" style="margin: 10px;">
        <b-input-group prepend="帳號">
          <b-form-input type="text" v-model="account" @keyup.native.enter="loginCheck"></b-form-input>
        </b-input-group>
      </b-col>
    </b-row>
    <b-row class="justify-content-md-center">
      <b-col md="3" style="margin: 10px;">
        <b-input-group prepend="密碼">
          <b-form-input type="password" v-model="password" @keyup.native.enter="loginCheck"></b-form-input>
          <b-input-group-append>
            <b-btn variant="outline-success" @click="loginCheck">登入</b-btn>
          </b-input-group-append>
        </b-input-group>
      </b-col>
    </b-row>
    <span class="err"> {{error}} </span>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { ws } from "../lib/ws"

export default {
  name: "Login",
  data() {
    return {
      account: "",
      password: "",
      error: "",
      title: `${config.title}`,
      date: ''
    };
  },
  created() {

  },
  computed: {
    loginData() {
      return {
        username: this.account,
        password: this.password
      };
    }
  },
  methods: {
    loginCheck() {
      // this.$api.requestFetch(`/admin/login`, 'POST', this.loginData)
      //   .catch(err => alert(err))
      //   .then(res => {
      //     if (res.status) {
      //       sessionStorage.setItem('sessionKey', res.data.session);
      //       sessionStorage.setItem('level', res.data.level);
      //       if (res.data.level >= 900) {
      //         this.$ws.login();
      //         this.$api.getLog();
      //       }
      //       this.$api.getGames();
      //       this.setLogin(true);
      //       this.setUsername(this.loginData.username);
      //       this.$router.push({ path: "./home" });
      //     } else {
      //       this.error = "帳號密碼錯誤!";
      //     }
      //   });
      const isCorrect = this.loginData.username === '123' && this.loginData.password === '123';
      if (isCorrect) {
        sessionStorage.setItem('sessionKey', '12345');
        sessionStorage.setItem('level', 999);
        this.$ws.login();
        this.$api.getLog();
        this.$api.getGames();
        this.setLogin(true);
        this.setUsername('人員A');
        this.$router.push({ path: "./home" });
      } else {
        this.error = "帳號密碼錯誤!";
      }
    },
    get() {
      // this.$api.requestFetch(`/admin`, 'GET')
      //   .then(res => this.$api.showModalInfo('success', res.data))
      //   .catch(err => alert(err));
    },
    ...mapActions([
      "setLogin",
      "setUsername",
      "setLogs",
      "pushLogs"
    ])
  }
};
</script>

<style lang="sass">
#login 
  font-family: "Avenir", Helvetica, Arial, sans-serif
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  text-align: center
  font-size: 30px
  color: #2c3e50;
  overflow-x: hidden;
  background-color: #d8dee3;
.err
  color: red
  font-size: 15px
.login-title
  margin-top: 50px;
</style>




