import Vue from 'vue'
import types from './types'

export const setItems = ({ commit }, data) => {
    commit(types.SETITEMS, data);
}
export const setCurrency = ({ commit }, data) => {
    commit(types.SETCURRENCY, data);
}
export const setStartTime = ({ commit }, data) => {
    commit(types.SETSTARTTIME, data);
}
export const setEndTime = ({ commit }, data) => {
    commit(types.SETENDTIME, data);
}
export const clear = ({ commit }, data) => {
    commit(types.CLEAR, data);
}


