<template>
    <div class="border h-100">
        <header class="modal-header">
            <h5 class="modal-title">更改匯率表</h5><button type="button" aria-label="Close" class="close" @click="$emit('close')">×</button>
        </header>
        <div class="modal-body">
            <b-list-group-item v-for="(item, key) in data" :key="key" class="d-flex justify-content-between">
                {{key}}<b-form-input type="number" min="0" class="text-right" style="height: 30px; width: 80px" v-model="data[key]"></b-form-input>
            </b-list-group-item>
        </div>
        <footer class="modal-footer">
            <b-btn @click="$emit('close')">Cancel</b-btn>
            <b-btn @click="edit">OK</b-btn>
        </footer>
    </div>
</template>

<script>

export default {
    props: [ 'data', 'refresh' ],
    methods: {
        edit() {
            let keys = Object.keys(this.data);
            for (let i = 0, len = keys.length; i < len; i++) {
                this.data[keys[i]] = Number(this.data[keys[i]]);
            }
            // this.$api.requestFetch(`/jackpot/exchange_rate`, "POST", this.data)   
            //     .catch(err => alert(err)) 
            //     .then(res => this.checkRes(res));
            this.$emit('close');
        },
        checkRes(res) {
            if (res.status) {
                this.$api.showModalInfo('SUCCESS', '更改匯率成功');
                this.refresh(res.data);
            } else {
                this.$api.showModalInfo('ERROR', '更改匯率失敗' + `(${res.msg})`);
            }
        }
    }
}
</script>

<style>

</style>



