import Vue from 'vue'
import mutations from './mutations'
import * as types from './mutations_type.js'

export const setLogin = ({ commit }, isLogin) => {
    commit(types.SETLOGIN, isLogin);
}
export const setUsername = ({ commit }, name) => {
    commit(types.SETUSERNAME, name);
}
export const setLogs = ({ commit }, logs) => {
    commit(types.SETLOGS, logs);
}
export const pushLogs = ({ commit }, log) => {
    commit(types.PUSHLOGS, log);
}
export const setIsLoading = ({ commit }, data) => {
    commit(types.SETISLOADING, data);
}
export const setApiLevel = ({ commit }, data) => {
    commit(types.SETAPILEVEL, data);
}
export const setWsStatus = ({ commit }, data) => {
    commit(types.SETWSSTATUS, data);
}


