<template>
    <v-chart :forceFit="true" :height="height" :data="data" :scale="scale" :padding="padding">
        <v-tooltip />
        <v-axis />
        <v-bar :position="`${x}*${y}`" :label="label" />
    </v-chart>
</template>

<script>
export default {
    props: ['data', 'x', 'y', 'height', 'padding'],
    computed: {
        label() {
            return [this.y, { offset: 16 }]
        },
        scale() {
            return [{ dataKey: this.y }];
        }
    },
    data() {
        return {
            
        }
    }
}
</script>

<style>

</style>



