<template>
    <div class="pie-chart p-4">
        <h4>{{ title }}</h4>
        <v-chart :forceFit="true" :height="height" :data="data" :scale="scale" :padding="[ '20%', '30%' ]">
            <v-tooltip :showTitle="false" dataKey="name*percent" />
            <v-axis />
            <v-legend dataKey="name" position="bottom-left" :useHtml="true" :itemTpl="itemTpl" :containerTpl="containerTpl"/>
            <v-pie position="percent" color="name" :v-style="pieStyle" :label="labelConfig" />
            <v-coord type="theta" />
        </v-chart>
    </div>
</template>

<script>

const scale = [{
    dataKey: 'percent',
    min: 0,
    formatter: '.0%',
}];

export default {
    props: ['title', 'data'],
    data() {
        return {
            scale,
            height: 400,
            pieStyle: {
                stroke: "#fff",
                lineWidth: 1
            },
            labelConfig: ['percent', {
                formatter: (val, item) => {
                    return item.point.name + ': ' + val;
                }
            }],
            containerTpl: '<div class="g2-legend legend-position">' +
                '<table class="g2-legend-list" style="list-style-type:none;margin:0;padding:0;"></table>' +
                '</div>',
            itemTpl: (value, color, checked, index) => {
                let obj = this.data[index] ? this.data[index] : { percent: 0 };
                checked = checked ? 'checked' : 'unChecked';
                return '<tr class="g2-legend-list-item item-' + index + ' ' + checked +
                    '" data-value="' + value + '" data-color=' + color +
                    ' style="cursor: pointer;font-size: 14px;">' +
                    '<td width=150 style="border: none;padding:0;"><i class="g2-legend-marker" style="width:10px;height:10px;display:inline-block;margin-right:10px;background-color:' + color + ';"></i>' +
                    '<span class="g2-legend-text">' + value + '</span></td>' +
                    '<td style="text-align: right;border: none;padding:0;">' + (obj.percent * 100).toFixed(2) + '%</td>' +
                    '</tr>';
            }
        }
    }
}
</script>

<style lang="scss">
.pie-chart {
    .legend-position {
        position: unset!important;
        margin-left: 20%; 
    }
}
</style>



